<template>
  <div class="goodsList">
    <v-header>商品列表</v-header>
    <v-goodsItem :goods="goods" v-for="(goods, index) in goodsList" :key="index"></v-goodsItem>
    <!--<div class="hotComment">-->
      <!--<ul class="hotCommentList">-->
        <!--<li class="hotCommentItem" v-for="(item, index) in goodsList" @click="goGoodsDetail(item.id)">-->
          <!--<img class="hotImg" :src="item.images[0]" alt="热评">-->
          <!--<span class="hotMess">-->
              <!--<p class="title">{{item.title}}</p>-->
              <!--<span class="price">￥{{item.purchase_info.price}}</span> <span class="mess">月销：{{item.sales_per_month}}</span>-->
              <!--<p class="mess nameMess">{{item.category}}</p>-->
            <!--</span>-->
        <!--</li>-->
      <!--</ul>-->
    <!--</div>-->
  </div>
</template>
<script>
  import header from 'components/common/header/header';
  import app from 'common/js/app';
  import goodsItem from 'components/common/goodsItem/goodsItem';
  export default {
    data() {
      return {
        goodsList: []
      };
    },
    activated() {
      this.storeId = this.$route.query.shopID;
      this.getStoreGoods();
    },
    components: {
      'v-header': header,
      'v-goodsItem': goodsItem
    },
    methods: {
      getStoreGoods() {
        this.$http.get(`${app.url.storeGoods}${this.storeId}/products/`).then((res) => {
          this.goodsList = res.results;
        });
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .goodsList
    position: absolute
    z-index: 9999
    width: 100%
    height: 100%
    background : #fff

</style>



