<template>
  <div class='tabBar'>
    <span class="iconfont icon-llmainpageback"
          @click="goBack"></span> {{title}}
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  activated() {
  },
  props: {
    'title': {
      type: String
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.tabBar
  text-align: center
  width: 100%
  height: .44rem
  line-height: .44rem
  box-sizing: border-box
  padding: 0 .2rem
  background-color: #9c4294
  color: #fff
  font-size: .18rem
  font-weight: bold
  &>span
    font-size: .18rem
    position: absolute
    left: .2rem
    top: 0
</style>



