<template>
  <div class="date-picker">
    <div class="picker-title border-1px">
      <span class="iconfont icon-wrong"></span>
      <h1>服务时间</h1>
      <span>确定</span>
    </div>
    <div class="day-wrapper">
      <ul class="day-list">
        <li class="day-item" :class="item.select?'active':''" v-for="(item, index) in dayList"
            @click="selectServiceDay(index)">{{item.day}}
        </li>
      </ul>
    </div>
    <div class="hour-wrapper">
      <ul class="hour-list">
        <li class="hour-item" :class="item.select?'active':''" v-for="(item, index) in hourList"
            @click="selectServiceTime(index)">{{item.time}}



        </li>
      </ul>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        dayList: [],
        hourList: []
      };
    },
    created() {
      this.getDate();
      this.resetTimeList();
    },
    methods: {
      getDate() {
        let date = new Date();
        let now = Date.parse(date);
        let i = 0;
        let numList = ['一', '二', '三', '四', '五', '六', '日'];
        while (i !== 6) {
          let day;
          let nextDay = now + 86400000 * i;
          let d = new Date(nextDay);
          if (i === 0) {
            day = '今天';
          } else if (i === 1) {
            day = '明天';
          } else if (i === 2) {
            day = '后天';
          } else {
            day = `星期${numList[d.getDay()]}`;
          }
          let item = {day: `${day} ${d.getMonth()}-${d.getDate()}`, select: false};
          if (i === 0) {
            item.select = true;
          }
          this.dayList.push(item);
          i++;
        }
      },
      resetTimeList() {
        let hour = 7;
        for (let i = 0; i < 32; i++) {
          let item = {};
          if (i % 2 === 0) {
            hour += 1;
            if (hour < 10) {
              item.time = `0${hour}:00`;
            } else {
              item.time = `${hour}:00`;
            }
          } else {
            if (hour < 10) {
              item.time = `0${hour}:30`;
            } else {
              item.time = `${hour}:30`;
            }
          }
          item.select = false;
          this.hourList.push(item);
        }
        console.log(this.times);
      },
      /**
       * 选择服务时间（几点）
       * @param positon
       */
      selectServiceTime(positon) {
        this.hourList.forEach((item, index) => {
          if (index === positon) {
            this.hourList[index].select = true;
          } else {
            this.hourList[index].select = false;
          }
        });
      },
      /**
       * 选择服务日期（那天）
       * @param positon
       */
      selectServiceDay(positon) {
        this.dayList.forEach((item, index) => {
          if (index === positon) {
            this.dayList[index].select = true;
          } else {
            this.dayList[index].select = false;
          }
        });
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../../common/stylus/mixin.styl"
  .date-picker
    position: fixed
    bottom: 0
    left: 0
    z-index: 300
    height: 3rem
    width: 100%
    background: #fff
    .picker-title
      display: flex
      line-height: 0.4rem
      border: 1px solid #f2f2f2
      box-sizing: border-box
      :nth-child(1)
        display: block
        flex: 0 0 0.4rem
        width: 0.4rem
        text-align: center
      :nth-child(2)
        display: block
        flex: 1
        text-align: center
      :nth-child(3)
        display: block
        flex: 0 0 0.4rem
        width: 0.4rem
        font-size: 0.12rem
        color: deepskyblue
    .day-wrapper
      .day-list
        height: 0.4rem
        overflow-x: scroll
        overflow-y: hidden
        white-space: nowrap
        &::-webkit-scrollbar
          display: none
        .day-item
          display: inline-block
          height: 0.4rem
          line-height: 0.4rem
          width: 0.8rem
          text-align: center
          font-size: 0.12rem
          &.active
            color: deepskyblue
            background: #f2f2f2
    .hour-wrapper
      .hour-list
        height: 2.1rem
        padding: 0.05rem 0
        text-align: left
        .hour-item
          display: inline-block
          height: 0.2rem
          width: 0.55rem
          line-height: 0.2rem
          font-size: 0.12rem
          margin: 0.05rem 0.1rem
          box-sizing: border-box
          text-align: center
          border: 1px solid #000
          &.active
            color: deepskyblue
            border: 1px solid deepskyblue
</style>



