<template>
  <div class="addAddress">
    <v-header class="det-tit">{{addTit}}地址<span class="save" @click="saveNewAddress">{{saveAdd}}</span></v-header>
    <div class="baseInfo">
      <p class="userName">联系人<input type="text" placeholder="确认联系人姓名" class="import name" v-model="userName"
                                    @change="checkName"></p>
      <p class="userTel">联系方式<input type="text" placeholder="请输入手机号码" class="import tel" v-model="userTel"
                                    @change="checkPhoneNum"></p>
    </div>
    <div class="addressInfo">
      <p class="userArea" @click="goAddArea">所在地区<input class="import area" v-model="userArea.name" ref="selectArea"
                                                        placeholder="目前仅限北京地区，其他城市陆续开放"><span
        class="iconfont icon-jiantouyou"></span></p>
      <textarea name="detAdd" id="detAdd" v-model="detAddress" placeholder="填写详细地址，街道、小区、楼牌号等"
                @change="checkAddress"></textarea>
    </div>
    <div class="setDefault">设为默认地址<span class="iconfont" :class="[isActive ? close : open]"
                                        @click="taggleClass($event)"></span></div>
    <v-addArea class="addArea" v-show="isShow" :showItem="isShow" @child-say="listenToChild"></v-addArea>
  </div>
</template>
<script>
  import app from 'common/js/app';
  import header from 'components/common/header/header';
  import {checkPhoneNum} from 'common/js/util';
  import {Toast} from 'mint-ui';
  import addAreaSelect from 'components/receiverAddress/addAreaSelect';
  export default {
    data() {
      return {
        isActive: true,
        close: 'icon-huadongguanbi',
        open: 'icon-huadongxuanzhong',
        userName: '',
        userTel: '',
        userArea: '',
        detAddress: '',
        userInfo: {
          receiver: '',
          phone_number: '',
          district: '',
          address: '',
          is_default_for_shipping: false
        },
        isShow: false,
        needEditAddId: '',
        saveAdd: '保存',
        addTit: '新增',
        needEditAdd: {}
      };
    },
    activated() {
      this.needEditAddId = this.$route.query.editAdd;
      this.getEditAddress();
    },
    methods: {
      // 默认地址设置
      taggleClass(ev) {
        this.userInfo.is_default_for_shipping = this.isActive;
        this.isActive = !this.isActive;
      },
      // 联系人输入验证
      checkUserName(userName) {
        let isUserName = false;
        if (userName) {
          var reg = /^[\u4e00-\u9fa5]{1,6}$/;
          isUserName = reg.test(userName);
        } else {
          window.alert('联系人输入有误');
        }
        return isUserName;
      },
      // 保存新增地址
      saveNewAddress() {
        this.checkDistrict(this.userArea);
        if (this.userInfo.receiver && this.userInfo.phone_number && this.userInfo.district && this.userInfo.address) {
          let fromData = new FormData();
          for (let key in this.userInfo) {
            fromData.append(key, this.userInfo[key]);
          }
          this.$http.post(app.url.address, fromData).then((res) => {
            Toast({
              message: '保存成功',
              position: 'bottom',
              duration: 1500
            });
          });
        } else {
          console.log(this.userInfo);
          Toast({
            message: '请将信息输入完整',
            position: 'bottom',
            duration: 1500
          });
        }
      },
      // 检验手机号是否合法
      checkPhoneNum() {
        if (!this.userTel) return;
        let isPhone = checkPhoneNum(this.userTel);
        if (!isPhone) {
          this.userTel = '';
          Toast({
            message: '手机号不合法',
            position: 'bottom',
            duration: 1500
          });
        } else {
          this.userInfo.phone_number = this.userTel;
        }
      },
      // 检验联系人是否合法
      checkName() {
        if (!this.userName) return;
        let isName = this.checkUserName(this.userName);
        if (!isName) {
          this.userName = '';
          Toast({
            message: '请输入1-6位汉字',
            position: 'bottom',
            duration: 1500
          });
        } else {
          this.userInfo.receiver = this.userName;
        }
      },
      // 检验所在地区是否选择
      checkDistrict(userArea) {
        if (!userArea) {
          return;
        } else {
          this.userInfo.district = userArea.id;
        }
      },
      // 检验详细地址是否填写
      checkAddress() {
        if (!this.detAddress) {
          this.detAddress = '';
          Toast({
            message: '请输入详细地址',
            position: 'bottom',
            duration: 1500
          });
        } else {
          this.userInfo.address = this.detAddress;
        }
        console.log(this.userInfo.address);
      },
      // 显示地址地区选择
      goAddArea() {
        this.isShow = true;
      },
      // 地区选择写入
      listenToChild(showItem, selectArea) {
        console.log(showItem, selectArea);
        this.isShow = showItem;
        this.userArea = selectArea;
      },
      /**
       * 获取需修改地址
       */
      getEditAddress() {
        if (this.needEditAddId) {
          this.saveAdd = '修改';
          this.addTit = this.saveAdd;
          this.$http.get(`${app.url.address}${this.needEditAddId}/`).then((res) => {
            this.needEditAdd = res;
            this.userName = res.receiver;
            this.userTel = res.phone_number;
            this.userArea = res.district;
            this.detAddress = res.address;
          });
        } else {
          this.saveAdd = '保存';
          this.addTit = '新增';
        }
      }
    },
    components: {
      'v-header': header,
      'v-addArea': addAreaSelect
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .addAddress
    height: 100%
    .det-tit
      background: #FAFAFA
      position: relative
      border-bottom: .05rem solid #F6F6F6
      .save
        font-size: .14rem
        color: #64C1FD
        position: absolute
        right: .1rem
    .addressInfo
      height: 1.1rem
    .baseInfo, .addressInfo, .setDefault
      text-align: left
      text-indent: .1rem
      line-height: .5rem
      border-bottom: .05rem solid #F6F6F6
      .import
        height: .35rem
        margin-left: .3rem
        width: 65%
        font-size: .14rem
        border: none
      .name
        margin-left: .48rem
      #detAdd
        border: none
        width: 100%
        box-sizing: border-box
        height: .6rem
        line-height: .3rem
        padding: 0 .1rem
        resize: none
        font-size: .18rem
        font-size: .16rem
        overflow: hidden
    .setDefault
      border: none
      line-height: .7rem
      position: relative
      .icon-huadongguanbi
        position: absolute
        right: .1rem
        color: #eee
        font-size: .2rem
      .icon-huadongxuanzhong
        position: absolute
        right: .1rem
        color: green
        font-size: .2rem
    .addArea
      position: absolute
      top: 0
      left: 0
      z-index: 999
</style>



