<template>
  <div class="discover">
    <v-header>发现</v-header>

    <mt-loadmore class="pull-refresh"
                 :top-method="loadTop"
                 :bottom-method="loadBottom"
                 :bottom-all-loaded="allLoaded"
                 topPullText="↓"
                 topDropText="↑"
                 bottomPullText="上拉加载"
                 bottomDropText="释放加载"
                 ref="loadmore">

      <v-boxlist :child-msg="bestSellerArr"><img class="imgIcon" src="./今日旺店@3x.png"/>今日旺店</v-boxlist>
      <div class="healthSelect">
        <p><img class="imgIcon" src="./优选健康@3x.png"/><span>优选健康</span></p>
        <div class="healthSelectBox">
          <div v-for="(item, index) in healthChoose"
               :key="index"
               @click="goGoodsDetail(item.id)">
            <img class="bgImg"
                 :src="item.images[0]"
                 alt="占位"/>
            <p>{{item.title}}</p>
          </div>
        </div>
      </div>
      <v-boxlist :child-msg="newStores"><img class="imgIcon" src="./新店@3x.png"/>新店入驻</v-boxlist>
      <div class="nearHotComment">
        <p><img class="imgIcon" src="./热搜@3x.png"/><span>附近热评榜</span></p>
        <ul class="hotCommentList">
          <li class="hotCommentItem"
              v-for="(item, index) in hotComments"
              @click="goGoodsDetail(item.id)">
            <img class="hotImg"
                 :src="item.images[0]"
                 alt="热评">
            <span class="hotMess">
              <p class="title">{{item.title}}</p>
              <p class="mess"><span>月销：{{item.sales_per_month}}笔</span><span class="miter">公里</span></p>
            </span>
          </li>
        </ul>
      </div>
    </mt-loadmore>


  </div>
</template>
<script>
  import app from 'common/js/app';
  import header from 'components/common/header/header';
  import boxlist from 'components/common/newStoreList/newStoreList';
  export default {
    data() {
      return {
        allLoaded: false,
        bestSellerArr: [], // 今日旺店 百度地图密钥： FzrqgZoqnBIRWQ8oSnnjURa7T3P3mAcH
        healthChoose: [],
        newStores: [],
        hotComments: []
      };
    },
    created() {
      this.init();
    },
    activated() {
    },
    components: {
      'v-header': header,
      'v-boxlist': boxlist
    },
    methods: {
      /**
       * 初始化
       */
      init() {
        this.getFeaturedList();   // 今日旺店
        this.getHealthChoose();   // 优选健康
        this.getNewStore();       // 新店入驻
        this.getHotCommentList(); // 热评榜
//        if (!window.AV.User.current()) {
//          this.login();
//        }
//        this.login();
      },
      /**
       * 获取数据GET
       */
      getFeaturedList() { // 今日旺店
        this.$http.get(app.url.bestSeller).then((res) => {
          this.bestSellerArr = res.slice(0, 2);
        });
      },
      getHealthChoose() { // 优选健康
        let params = {
          params: {
            filter: 'financial-top'
          }
        };
        this.$http.get(app.url.healthChoose, params).then((res) => {
          this.healthChoose = res.products.slice(0, 3);
        });
      },
      getNewStore() { // 新店入驻
        this.$http.get(app.url.newStore).then((res) => {
          this.newStores = res.results.slice(0, 4);
        });
      },
      getHotCommentList() { // 热评榜
        this.$http.get(app.url.hotComment).then((res) => {
          this.hotComments = res;
          this.$refs.loadmore.onTopLoaded();
        });
      },
      goStoreDetail(storeID) { // 店铺详情
        this.$router.push({name: 'storeDetail', query: {storeId: storeID}});
      },
      goGoodsDetail(goodsId) { // 商品详情
        this.$router.push({name: 'goodsDetail', query: {goodsId: goodsId}});
      },
      /**
       * 下拉刷新
       */
      loadTop() {
        this.bestSellerArr = [];
        this.healthChoose = [];
        this.newStores = [];
        this.hotComments = [];
        this.init();
      },
      /**
       * 获取AccessToken
       */
//      getAccessToken() {
//        let url = 'http://wx.lianzhongtongxing.cn/?code=' + getQueryString('code');
//        return new Promise((resolve, reject) => {
//          this.$http.get(url).then((res) => {
//            console.log('登陆登陆登陆登陆登陆登陆');
//            console.log(res);
//            res = res.body;
//            resolve(res);
//          });
//        });
//      },
      /**
       * 登录
       */
      login() {
        this.getAccessToken().then((data) => {
          alert(JSON.stringify(data));
//          'openid': data.openid,
//            'access_token': data.accessToken,
//            'expires_at': new Date().toString()
        });
      },
      loadBottom() {
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .discover
    position: relative
    width: 100%
    .healthSelect, .nearHotComment
      width: 100%
      height: auto
      & > p
        font-size: .15rem
        height: .5rem
        line-height: .5rem
        background-color: #ffffff
        .imgIcon
          position: relative
          width: .20rem
          height: .20rem
          padding-right: .1rem
          padding-bottom: .07rem
          vertical-align: middle
        & > span
          height: .5rem
    .healthSelect
      background-color: #edebeb
      padding-bottom: .01rem
      .healthSelectBox
        display: flex
        margin: .06rem
        box-sizing: border-box
        flex-wrap: wrap
        position: relative
        & > div
          margin: .04rem
          .bgImg
            width: 100%
            height: 100%
          & > p
            color: #fff
            background: rgba(0, 0, 0, .3)
            font-size: .14rem
            text-align: left
            padding-left: .1rem
            position: relative
            height: .25rem
            line-height: .25rem
            margin-top: -.28rem
            white-space: nowrap
            overflow: hidden
            text-overflow: ellipsis
        & > :nth-child(1)
          flex: 1
          height: 1.7rem
        & > :nth-child(2), & > :nth-child(3)
          height: .8rem
          width: 48%
        & > :nth-child(3)
          position: absolute
          top: 50%
          left: 50%
    .nearHotComment
      .hotCommentList
        margin-top: -.05rem
        .hotCommentItem
          border-bottom: .01rem solid #d0d0d0
          height: 1.25rem
          text-align: left
          padding-bottom: .1rem
          display: flex
          box-sizing: border-box
          .hotImg
            width: 1.15rem
            height: 1.15rem
            margin-left: .05rem
            margin-top: .05rem
          .hotMess
            margin-left: .1rem
            margin-top: .1rem
            flex: 1
            .title
              font-size: .14rem
              color: #222222
            .mess
              font-size: .10rem
              color: #d0d0d0
              margin-top: .75rem
              .miter
                right: .1rem
                position: absolute

</style>



