<template>
  <div class='appraise'>
    <v-header class="det-tit">发表评论</v-header>
    <div class='app-com app-con'>
      <div class='commodity'>
        <img src='./1@2x.png' class='compic'>
      </div>
      <ul class='app-choices'>
        <li class='apps' @click='appraise(1)' :class="{check:selectIndex === 1}"><span class='icon good'
                                                                                       :class="{select1:selectIndex === 1}"></span>好评


        </li>
        <li class='apps' @click='appraise(2)' :class="{check:selectIndex === 2}"><span class='icon normal'
                                                                                       :class="{select2:selectIndex === 2}"></span>中评





        </li>
        <li class='apps' @click='appraise(3)' :class="{check:selectIndex === 3}"><span class='icon bad'
                                                                                       :class="{select3:selectIndex === 3}"></span>差评





        </li>
      </ul>
      <textarea ref="textarea" class="textarea" placeholder="评论内容...">
      </textarea>
      <div class="addPicture">
        <div class="photo">
          <span v-for="(item,index) in commentImgs">
            <img class="photoImg" ref='photoImg' src="dd"/>
          </span>
        </div>
        <div class='add-photo' ref='photo' @click="sendPho">
          <img src='./camera@2x.png' class='camera'>添加照片


        </div>
      </div>
    </div>
    <div class='app-shop app-con'>
      <p class='shopgrade'><span class='shoplogo'></span>店铺评分</p>
      <ul class='grades'>
        <li class='det-grade' v-for='(item,index) in gradeType'>
          <span>{{item}}</span>
          <ul class='stars'>
            <!-- 添加点击事件:item代表元素，index代表下标，choose事件中传入两个参数：1：事件，2：下标 -->
            <li v-for='(item,index) in 5' class="star" @click='choose($event,index)'></li>
          </ul>
        </li>
      </ul>
    </div>
    <div class='send-app app-con'>
      <span class='anonymity' @click="taggleCheck($event)"></span>
      匿名评价


      <div class='sureapp' @click="submitComment()">发表评价</div>
    </div>
  </div>
</template>
<script>
  import { uploadImg } from 'common/js/util';
  import { Indicator, Toast } from 'mint-ui';
  import header from 'components/common/header/header';
  import app from 'common/js/app';
  export default {
    data() {
      return {
        gradeType: ['描述相符', '物流服务', '服务态度'],
        //  1代表匿名
        //  0代表实名
        isAnonymous: '1',
        maxnumImg: false,
        produceId: 0,
        scoreIndex: 5,
        selectIndex: 0,
        commentImgs: [],
        img: ''
      };
    },
    components: {
      'v-header': header
    },
    activated() {
      this.produceId = this.$route.query.produceId;
    },
    methods: {
      //  传入两个参数：
      //  1：ev：代表当前事件，可以通过事件拿到当前点击的元素
      //  2：index:代表当前元素的下标，【注意】这里面下标从0开始
      choose(ev, index) {
        this.scoreIndex = index + 1;
        let strats = ev.currentTarget.parentNode.children;
        for (var i = 0; i < strats.length; i++) {
          strats[i].className = 'star';
          for (var j = 0; j < index + 1; j++) {
            strats[j].className = 'chooseStar';
          }
        }
      },
      sendPho() {
        uploadImg(this.$refs.photo, (result, el) => {
            if (this.commentImgs.length >= 3) { // 评论照片最多3个
              this.commentImgs.shift();
              this.maxnumImg = true;
            }
            this.commentImgs.push(result);
            Indicator.open({
              text: '',
              spinnerType: 'snake'
            });
          }
        );
      },
      sellectType(indexs) {
        this.commentScale.forEach(function (collectItem, index) {
          if (indexs === index) {
            collectItem.isSellect = true;
          } else {
            collectItem.isSellect = false;
          }
        });
      },
      appraise(index) {
        this.selectIndex = index;
      },
      taggleCheck(ev) {
        ev.currentTarget.className === 'anonymity' ? (ev.currentTarget.className = 'unanonymity', this.isAnonymous = '0') : (ev.currentTarget.className = 'anonymity', this.isAnonymous = '1');
      },
      commitFn() {
        alert(this.isAnonymous);
      },
      /*
       *发表评论
       */
      submitComment() {
        let title = this.$refs.textarea.value;
        let params = new FormData();
        params.append('product_id', this.produceId);
        params.append('score', this.scoreIndex);
        params.append('title', title);
        params.append('images', 'img'); // this.commentImgs[0]
        this.$http.post(`${app.url.products}${this.produceId}/reviews/`, params)
          .then((res) => {
            Toast({
              message: '评价成功!',
              position: 'bottom',
              duration: 1500
            });
          }, err => {
            Toast({
              message: '评价失败!',
              position: 'bottom',
              duration: 1500
            });
            console.log(err);
          });
      }
    },
    /*
     *刷新照片
     */
    updated() {
      let imgArr = this.$refs.photoImg;
      if (!this.maxnumImg) {
        imgArr[imgArr.length - 1].src = this.commentImgs[this.commentImgs.length - 1];
      } else {
        this.commentImgs.forEach(function (imgItem, index) {
          imgArr[index].src = imgItem;
        });
      }
    },
    created() {
    }
  };
</script>
<style lang='stylus' rel='stylesheet/stylus'>
  .appraise
    height: 100%
    background: #eee
    position: relative
    z-index: 1000
    .app-con
      background: #fff
    .app-com
      height: 2.80rem
      padding: .15rem 0 0 .15rem
      position: relative
      .commodity
        width: .6rem
        height: .6rem
        float: left
        .compic
          width: .6rem
          height: .6rem
      .app-choices
        float: left
        .apps
          float: left
          margin: .2rem .1rem 0 .2rem
          color: #ccc
          .icon
            display: inline-block
            vertical-align: middle
            width: .25rem
            height: .25rem
            margin-right: .1rem
          .good
            background: url('./good.png') no-repeat 0 0 / .25rem .25rem
          .select1
            background: url('./goodS.png') no-repeat 0 0 / .25rem .25rem
          .normal
            background: url('./normal.png') no-repeat 0 0 / .25rem .25rem
          .select2
            background: url('./normalS.png') no-repeat 0 0 / .25rem .25rem
          .bad
            background: url('./bad.png') no-repeat 0 0 / .25rem .25rem
          .select3
            background: url('./badS.png') no-repeat 0 0 / .25rem .25rem
        .check
          color: red
      .textarea
        text-align: left
        position: absolute
        left: .1rem
        margin-top: .8rem
        width: 3.49rem;
        font-size: .14rem
        height: .8rem
        border: .01rem solid #ccc
      .addPicture
        position: absolute
        left: .1rem
        bottom: .1rem
        width: 100%
        .photo
          float: left
          .photoImg
            width: .8rem
            height: .8rem
            padding-right: .07rem
        .add-photo
          float: left
          width: .8rem
          height: .8rem
          border: 2px dashed #ccc
          color: #ccc
          .camera
            width: .3rem
            height: .3rem
            margin: .2rem .2rem 0 .2rem
    .app-shop
      height: 2rem
      margin-top: .1rem
      text-align: left
      padding-top: .1px
      padding-left: .1rem
      color: #999
      .shopgrade
        margin-top: .2rem
        height: .2rem
        line-height: .2rem
        .shoplogo
          display: inline-block
          vertical-align: middle
          margin-right: .05rem
          width: .25rem
          height: .25rem
          background: url('./shop@2x.png') no-repeat 0 0 / .25rem .25rem
      .grades
        .det-grade
          margin-top: .2rem
          height: .25rem
          line-height: .25rem
          position: relative
          .stars
            position: absolute
            width: 2rem
            height: 100%
            left: 1.3rem
            top: 0
            .star
              float: left
              width: .25rem
              height: .25rem
              margin: 0 .05rem
              background: url('./stars@2x.png') no-repeat 0 0 / .25rem .25rem
    .send-app
      height: .5rem
      line-height: .5rem
      position: absolute
      width: 100%
      left: 0
      bottom: .0rem
      text-align: left
      .anonymity
        display: inline-block
        vertical-align: middle
        width: .25rem
        height: .25rem
        margin-left: .1rem
        background: url('./checked@2x.png') no-repeat 0 0 / .25rem .25rem
      .sureapp
        position: absolute
        right: 0
        top: 0
        height: 100%
        width: 1.2rem
        background: red
        color: #fff
        text-align: center

  .chooseStar
    float: left
    width: .25rem
    height: .25rem
    margin: 0 .05rem
    background: url('./star@2x.png') no-repeat 0 0 / .25rem .25rem

  .unanonymity
    display: inline-block
    vertical-align: middle
    width: .25rem
    height: .25rem
    margin-left: .1rem
    background: url('./nocheck.png') no-repeat 0 0 / .27rem .27rem
</style>



