<template>
  <div class="orderDetail">
    <v-header class="det-tit">订单详情</v-header>
    <div class="order-state resize">
      <span class="orderState">{{orderInfo.orderStatus}}</span>
      <span class="orderNum">订单号:{{orderInfo.number}}</span>
    </div>
    <div class="aboutUser resize">
      <div class="iconfont icon-weizhi1"></div>
      <p class="userInf">收货人:{{orderInfo.shipping_address.receiver}}<span class="tel">{{orderInfo.shipping_address.phone_number}}</span></p>
      <p class="userLoc">收货地址:{{orderInfo.shipping_address.address}}</p>
    </div>
    <ul class="order-list">
      <li class="order-det" @click="goGoodsDetail(item.product.id)" v-for="(item, index) in orderDetInfo">
        <p class="com-name">{{item.product.partner_info.name}}</p>
        <div class="order-com">
          <div class="comPic">
            <img :src=" item.product.images[0] " alt="" class="comPicture"/>
          </div>
          <div class="orderName">{{item.product.title}}</div>
          <div class="orderInf">
            <div class="price resize">￥{{item.product.purchase_info.price}}</div>
            <div class="old-price resize">￥{{item.price_incl_tax}}</div>
            <div class="num resize">x{{item.quantity}}</div>
          </div>
        </div>
      </li>
      <li>
        <div class="orderSum">
          <span class="sumNum resize">共{{orderDetInfo.length}}类商品</span>
          <span class="sumPri resize">实付款:￥<span class="spe">{{orderInfo.total_incl_tax}}</span></span>
        </div>
      </li>
    </ul>
  </div>  
</template>
<script>
// import app from 'common/js/app';
import header from 'components/common/header/header';
    export default {
      data() {
        return {
          orderState: ['全部', '未付款', '待发货', '待确认', '已完成'],
          orderDet: '',
          orderInfo: {},
          orderDetInfo: []
        };
      },
      activated() {
        this.orderDet = this.$route.query.orderLink;
        this.getOrderDetails();
      },
      components: {
        'v-header': header
      },
      methods: {
        /**
        *获取订单详情
        */
        getOrderDetails() {
          this.$http.get(this.orderDet).then((res) => {
            this.orderInfo = res;
            switch (this.orderInfo.status) {
              case 'Cancelled':
                this.orderInfo.orderStatus = '交易取消';
                break;
              case 'Waiting for payment':
                this.orderInfo.orderStatus = '未付款';
                break;
              case 'Waiting for delivery':
                this.orderInfo.orderStatus = '待发货';
                break;
              case 'Waiting for confirm':
                this.orderInfo.orderStatus = '待确认';
                break;
              case 'Completed':
                this.orderInfo.orderStatus = '已完成';
                break;
              case 'Waiting for agree refund':
                this.orderInfo.orderStatus = '退款受理中';
                break;
              default:
                console.log('orderState undefined');
                break;
            };
            this.$http.get(this.orderInfo.lines).then((res) => {
              this.orderDetInfo = res;
            });
          });
        },
        goGoodsDetail(goodsId) {
          this.$router.push({name: 'goodsDetail', query: {goodsId: goodsId}});
        }
      }
    };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .orderDetail
    height:100%
    .order-state,.aboutUser,.order-list
      padding:0 .15rem
      text-align:left
    .det-tit
      background:#FAFAFA
      border-bottom:1px solid #F2EFEC
    .order-state
      line-height:.6rem
      background:#FFEEDA
      border-bottom:.05rem solid #F6F6F6
      color:#FEB540
      .orderNum
        float:right
        color:#A7A4A2
    .aboutUser
      border-bottom:.05rem solid #F6F6F6
      padding:.1rem .15rem
      position:relative
      .icon-weizhi1
        position:absolute
        top:.3rem
        font-size:.15rem
        color:#eee
      .userInf,.userLoc
        line-height:.3rem
        text-indent:.3rem
        .tel
          margin-left:.3rem
    .order-list
      text-align:left
      .order-det
        .com-name
          line-height:.35rem
          border-bottom:1px solid #f2f2f2
          margin:0 .1rem
        .order-com
          overflow:hidden
          padding:.1rem .1rem
          border-bottom:1px solid #f2f2f2
          & div
            float:left
          .comPic
            margin:0 .1rem
            .comPicture
              width:1rem
              height:.6rem
          .orderName
            width:45%
          .orderInf
            float:right
            width:15%
            & div
              width:100%
              text-align:right
            .old-price
              text-decoration:line-through
              color:#eee
              line-height:.14rem
            .num
              color:#eee
              line-height:.35rem
      .orderSum
        border-bottom:1px solid #f2f2f2
        line-height:.35rem
        padding:0 .1rem
        line-height:.35rem
        .sumPri
          float:right
          color:#FF8000
          .spe
            font-size:.16rem
            font-weight:600
    .resize
      font-size:.12rem
</style>



