<template>
  <div class="modifyInformation">
    <div class="modifyInfo-header">
      <img class="headerImg"
           :src="imgUrl"
           alt=""
           ref="headerImg"> 上传头像
      <input id="modifySave"
             type="file"
             class="file"
             @change="fileHandle" />
    </div>
    <div class="modifyInfo-info">
      <div class="name">
        <span>姓名</span>
        <input @blur="setName" type="text"
               :value="username"></input>
      </div>
      <div class="gender">
        <span>性别</span>
        <div class="genderDiv">
          <label for=""
                 class="male"
                 @click="selectMale"><span class="iconfont"
                  :class="selectRaio[isClassMale]"></span>男</label>
          <label for=""
                 class="female"
                 @click="selectFemale"><span class="iconfont"
                  :class="selectRaio[isClassFemale]"></span>女</label>
        </div>
      </div>
      <div class="birthday">
        <span>生日</span>
        <span class="birthdaySpan"
              @click="birthdayHandle">{{birthday}}</span>
      </div>
    </div>
    <div class="modifySave"
         @click="modifySave">
      保存修改
    </div>
    <div class="modifyPicker"
         v-show="isShow">
      <div class="pickerSelecter">
        <span @click="modifyCancel">取消</span> 选择生日
        <span @click="modifyComplete">完成</span>
      </div>
      <mt-picker :slots="slots"
                 @change="onValuesChange"></mt-picker>
    </div>
    <backBar></backBar>
  </div>
</template>

<script>
import baseURL from '../../common/js/app.js';
import plupload from 'plupload';
import { Toast } from 'mint-ui';
import backBar from '../common/backBar/backBar';
export default {
  data() {
    return {
      isShow: false,
      isClassMale: 1,
      isClassFemale: 1,
      username: '',
      imgUrl: '',
      tempImg: '',
      hashKeyValue: '',
      selectRaio: [
        'icon-yuanhuan',
        'icon-yuanhuan1'
      ],
      domainURL: 'http://p1.lzhealth.com.cn/',
      yearsIndex: 1900,
      years: ['2017年'],
      dayIndex: 1,
      day: ['1日'],
      birthday: '请选择年/月/日',
      pickerValue: [],
      newPickerValue: [],
      slots: [
        {
          flex: 1,
          values: [],
          className: 'slot1',
          textAlign: 'center',
          defaultIndex: 100
        },
        {
          flex: 1,
          values: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
          className: 'slot2',
          textAlign: 'center',
          defaultIndex: 5
        },
        {
          flex: 1,
          values: [],
          className: 'slot3',
          textAlign: 'center',
          defaultIndex: 14
        }
      ]
    };
  },
  components: {
    'backBar': backBar
  },
  activated() {
    this.userDetails();
    this.calculatedYear();
    this.calculatedDay();
    this.qiniuUploader();
  },
  methods: {
    setName(e) {
      // console.log(e.target.value);
      this.username = e.target.value;
    },
    userDetails() {
      this.$http.get(`${baseURL.url.profile}`)
        .then(result => {
          this.tempImg = result.avatar_url;
          this.imgUrl = result.avatar_url;
          this.username = result.name;
          if (result.gender === 'M') {
            this.isClassMale = 0;
            this.isClassFemale = 1;
          } else {
            this.isClassFemale = 0;
            this.isClassMale = 1;
          }
          this.birthday = result.birthday.split('-').join('/');
        });
    },
    selectMale() {
      this.isClassMale = 0;
      this.isClassFemale = 1;
    },
    selectFemale() {
      this.isClassMale = 1;
      this.isClassFemale = 0;
    },
    birthdayHandle() {
      this.isShow = !this.isShow;
      let tempBir = this.birthday.split('/');
      this.slots[0].defaultIndex = tempBir[0] - 1900;
      this.slots[1].defaultIndex = tempBir[1] - 1;
      this.slots[2].defaultIndex = tempBir[2] - 1;
    },
    modifyCancel() {
      this.isShow = !this.isShow;
    },
    modifyComplete() {
      this.newPickerValue = [];
      this.pickerValue.forEach((item) => {
        this.newPickerValue.push(parseInt(item));
      });
      this.newPickerValue[1] = this.newPickerValue[1] < 10 ? `0${this.newPickerValue[1]}` : this.newPickerValue[1];
      this.newPickerValue[2] = this.newPickerValue[2] < 10 ? `0${this.newPickerValue[2]}` : this.newPickerValue[2];
      this.birthday = this.newPickerValue.join('/');
      this.isShow = !this.isShow;
    },
    calculatedYear() {
      this.yearsIndex = 1900;
      this.years = [];
      while (true) {
        if (this.yearsIndex > 2017) {
          break;
        }
        this.years.push(`${this.yearsIndex}年`);
        this.yearsIndex++;
      }
      this.slots[0].values = this.years;
    },
    calculatedDay() {
      this.dayIndex = 1;
      this.day = [];
      while (true) {
        if (this.dayIndex > 31) {
          break;
        }
        this.day.push(`${this.dayIndex}日`);
        this.dayIndex++;
      }
      this.slots[2].values = this.day;
    },
    onValuesChange(picker, values) {
      this.pickerValue = values;
    },
    fileHandle(e) {
      const reader = new FileReader();
      const self = this;
      reader.readAsDataURL(e.target.files[0]);
      reader.onload = function (e) {
        self.imgUrl = this.result;
      };
    },
    modifySave() {
      if (this.tempImg === this.imgUrl) {
        Toast({
          message: '请确定头像是否有修改!',
          position: 'center',
          duration: 1500
        });
      } else {
        const parmas = {
          name: this.username,
          avatar_url: this.hashKeyValue,
          birthday: this.birthday.split('/').join('-')
        };
        this.$http.put(`${baseURL.url.profile}`, parmas)
          .then(result => {
            // console.log(result);
            this.$router.push({ name: 'mine' });
          });
      }
    },
    qiniuUploader() {
      const self = this;
      this.$http.get(`${baseURL.url.qiniu}`)
        .then(result => {
          const uploader = window.Qiniu.uploader({
            runtimes: 'html5,flash,html4',
            browse_button: 'modifySave',
            uptoken: `${result.public.token}`,
            get_new_uptoken: false,
            domain: this.domainURL,
            max_file_size: '100mb',
            flash_swf_url: 'plupload/js/Moxie.swf',
            max_retries: 3,
            dragdrop: true,
            drop_element: 'container',
            chunk_size: '4mb',
            auto_start: true,
            init: {
              'FilesAdded': function (up, files) {
                plupload.each(files, function (file) {
                });
              },
              'BeforeUpload': function (up, file) {
                up.settings.url = 'http://up-z1.qiniu.com';
              },
              'FileUploaded': function (up, file, info) {
                self.hashKeyValue = JSON.parse(info).key;
                self.imgUrl = `${self.domainURL}${self.hashKeyValue}`;
              },
              'Error': function (up, err, errTip) {
                // 上传出错时，处理相关的事情
                console.log(err);
                console.log(errTip);
              }
            }
          });
          return uploader;
        });
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.modifyInformation
  position: relative
  z-index: 300
  height: 100%
  background-color: #fff
  .modifyInfo-header
    height: 1.13rem
    box-sizing: border-box
    padding: .235rem .1rem
    color: #a1a1a1
    text-align: left
    line-height: .61rem
    border-bottom: .05rem solid #f8f8f8
    position: relative
    .headerImg
      float: left
      width: .66rem
      height: .66rem
      margin: 0 .1rem
      box-sizing: border-box
      border: 1px solid #e3e3e3
      border-radius: .33rem
      overflow: hidden
    .file
      position: absolute
      top: .235rem
      left: .2rem
      width: .66rem
      height: .66rem
      opacity: 0
  .modifyInfo-info
    border-bottom: .05rem solid #f8f8f8
    text-align: left
    &>div
      height: .5rem
      line-height: .14rem
      box-sizing: border-box
      padding: .18rem .3rem
      font-size: .14rem
      display: flex
      &>span
        float: left
        width: .62rem
        box-sizing: border-box
        padding: 0 .05rem
      &>:nth-child(2)
        flex: 1
        font-size: .12rem
        line-height: .14rem
    .gender
      .genderDiv
        &>label
          float: left
          width: .5rem
          .icon-yuanhuan1
            font-size: .12rem
            line-height: .13rem
            color: skyBlue
          .icon-yuanhuan
            font-size: .13rem
            line-height: .13rem
            color: skyBlue
      .birthdaySpan
        color: #a1a1a1
    &>:nth-child(n+2)
      border-top: 1px solid #f8f8f8
  .modifySave
    box-sizing: border-box
    width: 1.66rem
    height: .43rem
    border: 1px solid #8f8f8f
    border-radius: .1rem
    font-size: .14rem
    color: #8f8f8f
    margin: .2rem auto
    line-height: .43rem
  .modifyPicker
    position: fixed
    bottom: 0
    left: 0
    width: 100%
    height: 2.2rem
    .pickerSelecter
      height: .4rem
      background-color: #e3e3e3
      font-size: .14rem
      font-weight: bold
      line-height: .4rem
      box-sizing: border-box
      padding: 0 .2rem
      &>span
        font-size: .12rem
        color: blue
        line-height: .4rem
      &>:nth-child(1)
        float: left
      &>:nth-child(2)
        float: right
</style>



