<template>
  <div class="address-picker">
    <div class="picker-title">
      <span class="confirm" @click="cancle($event)">取消</span>
      <span class="cancle" @click="confirm($event)">确定</span>
    </div>
    <mt-picker v-show="true" :slots="addressSlots" @change="onAddressChange"
               :visible-item-count="5"></mt-picker>
  </div>
</template>
<script>
  import {getAddress} from 'common/js/util';
  export default {
    data() {
      return {
        addressSlots: [
          {
            flex: 1,
            values: Object.keys(getAddress()),
            className: 'slot1',
            textAlign: 'center'
          }, {
            divider: true,
            content: '-',
            className: 'slot2'
          }, {
            flex: 1,
            values: ['北京'],
            className: 'slot3',
            textAlign: 'center'
          }
        ],
        addressProvince: '北京',
        addressCity: '北京'
      };
    },
    props: {
      getAddress: {
        type: Function,
        default: () => {
          return {
            addressProvince: this.addressProvince,
            addressCity: this.addressCity
          };
        }
      },
      cancle: {
        type: Function,
        default: () => {
        }
      },
      confirm: {
        type: Function,
        default: () => {
        }
      }
    },
    methods: {
      onAddressChange(picker, values) {
        picker.setSlotValues(1, getAddress()[values[0]]);
        this.addressProvince = values[0];
        this.addressCity = values[1];
        // 收货地址信息
        let data = {
          addressProvince: this.addressProvince,
          addressCity: this.addressCity
        };
        this.getAddress(data);
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .address-picker
    position: absolute
    width: 100%
    left: 0
    bottom: 0
    .picker-title
      width: 100%
      height: 0.35rem
      padding: 0 0.06rem
      background: #eee
      box-sizing: border-box
      .confirm
        display: block
        width: 0.46rem
        height: 0.29rem
        line-height: 0.29rem
        float: left
        margin: 0.03rem 0
        color: #000
        font-size: 0.12rem
        text-align: center
        border-radius: 0.04rem
        background: #fff
      .cancle
        display: block
        width: 0.46rem
        height: 0.29rem
        line-height: 0.29rem
        float: right
        margin: 0.03rem 0
        color: #fff
        font-size: 0.12rem
        text-align: center
        border-radius: 0.04rem
        background: #007af0

</style>



