<template>
    <div class="order">
      <v-header class="orderTit">我的订单</v-header>
      <ul class="orderState">
        <li class="states" v-for="(item,index) in orderState" @click="orderType($event,index)">{{item}}</li>
      </ul>
      <mt-loadmore class="pull-refresh"
                   :top-method="loadTop"
                   :bottom-method="loadBottom"
                   :bottom-all-loaded="allLoaded"
                   topPullText="↓"
                   topDropText="↑"
                   bottomPullText="上拉加载"
                   bottomDropText="释放加载"
                   ref="loadmore"
                   v-show="orderList.length > 0">
        <div class="order-lists">
          <ul class="order-list" v-for="(item1, index1) in orderList">
            <li class="order-det" v-for="(item2, index2) in detOrder[index1]">
              <p class="com-name">{{item2.product.partner_info.name}}</p>
              <div class="order-com" @click="goOrderDetail(item1.url)">
                <div class="comPictures">
                  <img :src=" item2.product.images[0] " alt="" class="comPicture"/>
                </div>
                <div class="orderName">{{item2.product.title}}</div>
                <div class="orderInf">
                  <div class="price resize">￥{{ item2.product.purchase_info.price}}</div>
                  <div class="old-price resize">￥{{ item2.product.purchase_info.price_retail }}</div>
                  <div class="num resize">x{{ item2.quantity }}</div>
                </div>
              </div>
            </li>
            <li class="order-sum">
              <div class="orderSum">
                <span class="sumNum resize">共{{item1.totalNum}}个商品</span>
                <span class="sumPri resize">合计：￥{{item1.total_incl_tax}}</span>
              </div>
              <div class="order-state">
                <span class="order-tail resize">{{item1.orderStatus}}</span>
                <ul class="manageOrders">
                  <li class="manageOrder noPay" v-show="item1.status==='Waiting for payment'">
                    <span class="cancelOrder" @click="cancelOrder(index1)">取消订单</span>
                    <span class="pay" @click="payOrder(index1)">付款</span>
                    <!--<span class="pay" @click="goComment(detOrder[index1][0].product.id)">付款</span>-->
                  </li>
                  <li class="manageOrder unshipped" v-show="item1.status === 'Waiting for delivery'">
                    <span class="drawback" @click="refundMoney(index1)">退款</span>
                  </li>
                  <li class="manageOrder toBeConfirm" v-show="item1.status==='Waiting for receive'">
                    <span class="drawback" @click="refundMoney(index1)">退款</span>
                    <span class="confirmGet" @click="confirmGet(index1)">确认收货</span>
                  </li>
                  <li class="manageOrder done" v-show="item1.statsu==='Complete'">
                    <span class="appraise" @click="goComment(detOrder[index1][0].product.id)">评价</span>
                    <span class="cusService">联系客服</span>
                  </li>
                </ul>
              </div>
            </li>
          </ul>
        </div>
      </mt-loadmore>
      <v-payOrder :close="hidePayDialog" v-show="showPayDialog" :payInfo="payInfos"></v-payOrder>
    </div>
</template>
<script>
  // import {Indicator} from 'mint-ui';
  import app from 'common/js/app';
  import axios from 'axios';
  import header from 'components/common/header/header';
  import dialogPay from 'components/common/dialogPay/dialogPay';
    export default {
      data() {
        return {
          // 订单状态
          orderState: {
            '0': '全部',
            '1': '未付款',
            '2': '待发货',
            '3': '待确认',
            '4': '已完成'
          },
          orderList: [], // 订单列表
          detOrder: [], // 订单详情商品列表
          pageSize: 10, // pagesize
          pageNum: 1, // 默认页码
          allLoaded: false,
          loading: true, // 是否正在加载数据
          showPayDialog: false, // 是否显示支付界面
          // 支付信息
          payInfos: {
            total_incl_tax: '',
            payment_url: ''
          },
          // 当前订单状态
          statusNum: '0'
        };
      },
      components: {
        'v-header': header,
        'v-payOrder': dialogPay
      },
      activated() {
        this.init();
        this.orderList = [];
        this.detOrder = [];
      },
      methods: {
        /**
        *初始化
        */
        init() {
          this.getOrderList();
        },
        /**
        *获取订单列表
        *@param orderstatus 订单状态
        */
        getOrderList(orderstatus) {
          if (orderstatus) {
            this.pageNum = 1;
            this.orderList = [];
          };
          // 创建参数对象 包含请求订单状态 页码
          let params = {
            params: {
              status: orderstatus,
              page: this.pageNum
            }
          };
          this.$http.get(app.url.order, params).then((res) => {
            res.results.forEach((item) => {
              this.orderList.push(item);
            });
            return this.orderList;
          }).then((data) => {
            this.detOrder = [];
            let requestList = [];
            // 循环请求订单商品数据
            for (let i = 0; i < data.length; i++) {
              // 订单状态页面显示
              switch (data[i].status) {
                case 'Cancelled':
                  this.orderList[i].orderStatus = '交易取消';
                  break;
                case 'Waiting for payment':
                  this.orderList[i].orderStatus = '未付款';
                  break;
                case 'Waiting for delivery':
                  this.orderList[i].orderStatus = '待发货';
                  break;
                case 'Waiting for confirm':
                  this.orderList[i].orderStatus = '待确认';
                  break;
                case 'Completed':
                  this.orderList[i].orderStatus = '已完成';
                  break;
                case 'Waiting for agree refund':
                  this.orderList[i].orderStatus = '退款受理中';
                  break;
                default:
                  console.log('orderState undefined');
                  break;
              }
              requestList.push(this.request(data, i));
            }
            axios.all(requestList).then(axios.spread((...res) => {
              this.detOrder = res;
              this.$refs.loadmore.onBottomLoaded();
              this.$refs.loadmore.onTopLoaded();
              this.totalize();
            }));
            this.loading = false;
          }, (e) => {
            if (this.pageNum >= 2) this.pageNum --;
          });
        },
        request (data, i) {
          return this.$http.get(data[i].lines);
        },
        /**
        *商品总数计算
        */
        totalize() {
          let totalCount = [];
          this.detOrder.forEach((item) => {
            let count = 0;
            for (var i = 0; i < item.length; i++) {
              count += item[i].quantity;
            };
            totalCount.push(count);
          });
          for (let j = 0; j < this.orderList.length; j++) {
            this.orderList[j].totalNum = totalCount[j];
          }
        },
        /**
        *点击查看不同转台订单
        *@param index 点击订单状态
        */
        orderType(ev, index) {
          this.statusNum = index;
          let orderTits = ev.currentTarget.parentNode.childNodes;
          for (var i = 0; i < orderTits.length; i++) {
            orderTits[i].style.color = '#000';
          }
          ev.currentTarget.style.color = 'skyblue';
          this.getDifOrder(this.statusNum);
        },
        /**
        *分类数据请求
        */
        getDifOrder(index) {
          switch (index) {
            case '0':
              this.getOrderList();
              break;
            case '1':
              this.getOrderList('Waiting for payment');
              break;
            case '2':
              this.getOrderList('Waiting for delivery');
              break;
            case '3':
              this.getOrderList('Waiting for receive');
              break;
            case '4':
              this.getOrderList('Complete');
              break;
          };
        },
        /**
        *点击跳转订单详情
        *@param thisOrderUrl 当前订单的url链接
        */
        goOrderDetail(thisOrderUrl) {
          this.$router.push({name: 'orderDetail', query: {orderLink: thisOrderUrl}});
        },
        /**
         * 下拉刷新
         */
        loadTop() {
          if (!this.loading) {
            this.pageNum = 1;
            this.orderList = [];
            this.getDifOrder(this.statusNum);
          }
        },
        /**
        * 上拉加载
        */
        loadBottom() {
          if (!this.loading) {
            this.pageNum++;
            this.getDifOrder(this.statusNum);
            this.allLoaded = true;  // 若数据已全部获取完毕
          };
        },
        /**
        *取消订单
        */
        cancelOrder(index) {
          let orderUrl = this.orderList[index].url;
          this.orderList[index].status = 'Cancelled';
          this.orderList[index].orderStatus = '交易取消';
          this.$http.delete(orderUrl);
          // this.goOrderDetail(orderUrl);
        },
        /**
        *付款
        */
        payOrder(index) {
          this.showPayDialog = true;
          this.payInfos.payment_url = this.orderList[index].payment_url;
          this.payInfos.total_incl_tax = this.orderList[index].total_incl_tax;
        },
        hidePayDialog() {
          this.showPayDialog = false;
        },
        /**
        *退款
        */
        refundMoney(index) {
          this.orderList[index].status = 'Waiting for agree refund';
          this.orderList[index].orderStatus = '退款受理中';
          let orderUrl = this.orderList[index].url;
          let orderInfo = new FormData();
          orderInfo.append('status', 'Waiting for agree refund');
          this.$http.patch(orderUrl, orderInfo);
        },
        /**
        *确认收货
        */
        confirmGet(index) {
          this.orderList[index].status = 'Completed';
          this.orderList[index].orderStatus = '已完成';
          let orderUrl = this.orderList[index].url;
          let orderInfo = new FormData();
          orderInfo.append('status', 'Completed');
          this.$http.patch(orderUrl, orderInfo);
        },
        /**
        *评价
        */
        goComment(goodsId) {
          this.$router.push({name: 'comment', query: {produceId: goodsId}});
        }
        /**
        *联系客服
        */
      }
    };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped="true">
  .order
    display: flex
    position: relative
    height: 100%
    width: 100%
    padding: 0
    flex-direction: column
    background-color: #fff
    .orderTit
      background:#FAFAFA
      position:fixed
      z-index:1
      left:0
      top:0
    .orderState
      width:100%
      background:#F9F9F9
      overflow:hidden
      border-top:1px solid #E2E2E2
      font-size:.14rem
      top:.4rem
      position:fixed
      z-index:1
      display:flex
      .states
        height:.35rem
        line-height:.35rem
        flex:1
        &:nth-child(1)
          color:skyblue
    .pull-refresh
      position: absolute
      width:100%
      top: 0.8rem
      left: 0
      background-color: #fff
      flex: 1
      padding-bottom:.5rem
      .order-lists
        text-align:left
        min-height:5.5rem
        .order-list
          width:100%
          border-bottom:.05rem solid #F6F6F6
          .order-det
            .com-name
              line-height:.35rem
              border-bottom:1px solid #f1f1f1
              margin:0 .1rem
            .order-com
              overflow:hidden
              padding:.1rem .1rem
              & div
                float:left
              .comPictures
                margin:0 .1rem
                .comPicture
                  width:1rem
                  height:.6rem
              .orderInf
                float:right
                width:15%
                & div
                  width:100%
                  text-align:right
                .old-price
                  text-decoration:line-through
                  color:#eee
                  line-height:.14rem
                .num
                  color:#eee
                  line-height:.35rem
          .order-sum
            .orderSum
              border:1px solid #f2f2f2
              line-height:.35rem
              .sumPri
                float:right
            .order-state,.orderSum
              padding:0 .1rem
              line-height:.35rem
              .order-tail
                color:#F4BA64
                opacity:.6
                background:#E7FEF2
              .manageOrders
                float:right
                font-size:.14rem
                .manageOrder
                  float:left
                  span
                    display:inline-block
                    width:.7rem
                    line-height:.2rem
                    text-align:center
                    border:1px solid gray
                    border-radius:.03rem
                    &:nth-child(2)
                      color:#F4BA64
                      border-color:#F4BA64
        .resize
          font-size:.12rem
</style>



