import Vue from 'vue';
import Router from 'vue-router';
import { initWechatSDK, getStorage } from 'common/js/util';
import home from 'components/home/home';
import lzHomeHome from 'components/lzHome/home';
import lzShopHome from 'components/lzShop/home';
import discover from 'components/discover/discover';
import order from 'components/order/order';
import mine from 'components/mine/mine';
import login from 'components/login/login';
import register from 'components/register/register';
import registerNow from 'components/register/registerNow';
import receiverAddress from 'components/receiverAddress/receiverAddress';
import addReceiverAddress from 'components/receiverAddress/addReceiverAddress';
import addManage from 'components/receiverAddress/addManage';
import addAddress from 'components/receiverAddress/addAddress';
import storeDetail from 'components/storeDetail/storeDetail';
import setUp from 'components/setUp/setUp';
import modifyInformation from 'components/modifyInformation/modifyInformation';
import position from 'components/position/position';
import merchantCenter from 'components/merchantCenter/merchantCenter';
import orderDetail from 'components/order/orderDetail';
import goodsSearch from 'components/goodsSearch/goodsSearch';
import collect from 'components/collect/collect';
import memberCard from 'components/memberCard/memberCard';
import recharge from 'components/recharge/recharge';
import goodsDetail from 'components/goodsDetail/goodsDetail';
import shopcart from 'components/shopcart/shopcart';
import serviceOrderSubmit from 'components/orderSubmit/serviceOrderSubmit';
import orderPay from 'components/orderPay/orderPay';
import aboutUs from 'components/aboutUs/aboutUs';
import goodsList from 'components/goodsList/goodsList';
import collection from 'components/collection/collection';
import merchantInformation from 'components/merchantInformation/merchantInformation';
import goodsOrderSubmit from 'components/orderSubmit/goodsOrderSubmit';
import classify from 'components/classify/classify';
import comment from 'components/comment/comment';
import commentList from 'components/commentList/commentList';
import contactCustomerService from 'components/contactCustomerService/contactCustomerService';
import activity from 'components/activity/activity';

Vue.use(Router);

let router = new Router({
  routes: [
    // 登录
    {
      path: '/login',
      name: 'login',
      component: login
    },
    // 注册-输入手机号
    {
      path: '/register',
      name: 'register',
      component: register
    },
    // 注册-立即注册
    {
      path: '/registerNow',
      name: 'registerNow',
      component: registerNow
    },
    // 首页
    {
      path: '/',
      name: 'home',
      component: home
    },
    // 商城首页
    {
      path: '/lzHomeHome',
      name: 'lzHomeHome',
      component: lzHomeHome
    },
    // 到家首页
    {
      path: '/lzShopHome',
      name: 'lzShopHome',
      component: lzShopHome
    },
    // 分类
    {
      path: '/classify',
      name: 'classify',
      component: classify
    },
    // 评论
    {
      path: '/comment',
      name: 'comment',
      component: comment
    },
    // 评论列表
    {
      path: '/commentList',
      name: 'commentList',
      component: commentList
    },
    // 发现
    {
      path: '/discover',
      name: 'discover',
      component: discover
    },
    // 发现 详情
    {
      path: '/storeDetail',
      name: 'storeDetail',
      component: storeDetail
    },
    // 商品列表
    {
      path: '/goodsList',
      name: 'goodsList',
      component: goodsList
    },
    // 商户中心
    {
      path: '/merchantCenter',
      name: 'merchantCenter',
      component: merchantCenter
    },
    // 订单
    {
      path: '/order',
      name: 'order',
      component: order
    },
    // 订单详情
    {
      path: '/orderDetail',
      name: 'orderDetail',
      component: orderDetail
    },
    // 我的
    {
      path: '/mine',
      name: 'mine',
      component: mine
    },
    // 我的页面-去设置
    {
      path: '/setUp',
      name: 'setUp',
      component: setUp
    },
    // 修改个人信息
    {
      path: '/modifyInformation',
      name: 'modifyInformation',
      component: modifyInformation
    },
    // 收货地址
    {
      path: '/receiverAddress',
      name: 'receiverAddress',
      component: receiverAddress
    },
    // 新增收货地址
    {
      path: '/addReceiverAddress',
      name: 'addReceiverAddress',
      component: addReceiverAddress
    },
    // 收货地址管理
    {
      path: '/addManage',
      name: 'addManage',
      component: addManage
    },
    // 新增地址
    {
      path: '/addAddress',
      name: 'addAddress',
      component: addAddress
    },
    // 地图位置
    {
      path: '/position',
      name: 'position',
      component: position
    },
    // 商品搜索
    {
      path: '/goodsSearch',
      name: 'goodsSearch',
      component: goodsSearch
    },
    // 收藏
    {
      path: '/collect',
      name: 'collect',
      component: collect
    },
    // 会员卡
    {
      path: '/memberCard',
      name: 'memberCard',
      component: memberCard
    },
    // 充值
    {
      path: '/recharge',
      name: 'recharge',
      component: recharge
    },
    // 商品详情
    {
      path: '/goodsDetail',
      name: 'goodsDetail',
      component: goodsDetail
    },
    // 购物车
    {
      path: '/shopcart',
      name: 'shopcart',
      component: shopcart
    },
    // 提交订单
    {
      path: '/serviceOrderSubmit',
      name: 'serviceOrderSubmit',
      component: serviceOrderSubmit
    },
    // 支付订单
    {
      path: '/orderPay',
      name: 'orderPay',
      component: orderPay
    },
    // 我的收藏
    {
      path: '/collection',
      name: 'collection',
      component: collection
    },
    // 购物车
    {
      path: '/shopcart',
      name: 'shopcart',
      component: shopcart
    },
    // 关于我们
    {
      path: '/aboutUs',
      name: 'aboutUs',
      component: aboutUs
    },
    // 商品详情
    {
      path: '/merchantInformation',
      name: 'merchantInformation',
      component: merchantInformation
    },
    // 提交订单（商品）
    {
      path: '/goodsOrderSubmit',
      name: 'goodsOrderSubmit',
      component: goodsOrderSubmit
    },
    // 联系客服
    {
      path: '/contactCustomerService',
      name: 'contactCustomerService',
      component: contactCustomerService
    },
    // 活动页
    {
      path: '/activity',
      name: 'activity',
      component: activity
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else {
      return { x: 0, y: 0 };
    }
  }
});
router.beforeEach((to, from, next) => {
  // 路由改变时重新初始化微信SDK 否则会导致签名失败
  if (window.buildType === 'wx') {
    initWechatSDK();
  }
  // 校验登录状态 未跳转到登录页面
  if (to.name !== 'home' && to.name !== 'classify' && to.name !== 'login' && to.name !== 'registerNow' && !getStorage('user')) {
    next({name: 'login'});
  } else {
    next();
  }
});
export default router;



