<template>
  <div class="dialog-pay" @click.self="close($event)">
    <div class="dialog-pay-wrapper">
      <div class="pay-title">
        <h1>付款详情</h1>
        <span class="iconfont icon-wrong" @click="close($event)"></span>
      </div>
      <div class="pay-info">
        <div>
          <span class="text">会员账号</span>
          <span class="username">{{vipInfo.username}}</span>
        </div>
        <div>
          <span class="text">支付</span>
          <span class="total-price">¥ {{payInfo.total_incl_tax}}</span>
        </div>
      </div>
      <div class="pay-type-wrapper">
        <ul class="pay-types">
          <li class="pay-type-item" v-show="false">
            <span class="iconfont icon-huiyuanqia"></span>
            <span>会员卡余额</span>
            <span class="vip-blance">¥0</span>
            <v-radio :click="VIPSelect" :checked="vipChecked"></v-radio>
          </li>
          <li class="pay-type-item">
            <span class="iconfont icon-weixin"></span>
            <span>微信支付</span>
            <v-radio :click="wechatSelect" :checked="wechatChecked"></v-radio>
          </li>
          <li class="pay-type-item" v-show="false">
            <span class="iconfont icon-zhifubao"></span>
            <span>支付宝支付</span>
            <v-radio :click="alipaySelect" :checked="alipayChecked"></v-radio>
          </li>
        </ul>
      </div>
      <div class="pay-submit" @click="payConfirm">确认支付</div>
    </div>
  </div>
</template>
<script>
// import pingpp from 'pingpp-js';
import radio from 'components/common/radio/radio';
import app from 'common/js/app';
import { getStorage } from 'common/js/util';
export default {
  data() {
    return {
      show: true,
      vipInfo: {},
      vipChecked: true,
      wechatChecked: false,
      alipayChecked: false,
      payChanel: ''
    };
  },
  activated() {
    this.getVipInfo();
  },
  components: {
    'v-radio': radio
  },
  props: {
    payInfo: {
      type: Object,
      default() {
        return {};
      }
    },
    close: {
      type: Function,
      defalut: () => {
      }
    }
  },
  methods: {
    /**
     *
     * @param select 选中状态
     * @constructor
     */
    VIPSelect(select) {
      this.vipChecked = select;
      this.wechatChecked = false;
      this.alipayChecked = false;
    },
    /**
     *
     * @param select 选中状态
     * @constructor
     */
    wechatSelect(select) {
      this.wechatChecked = select;
      this.vipChecked = false;
      this.alipayChecked = false;
      if (window.buildType === 'wx') {
        // 公众号支付
        this.payChanel = 'wx_pub';
      } else if (window.buildType === 'app') {
        this.payChanel = 'wx';
      }
    },
    /**
     *
     * @param select 选中状态
     * @constructor
     */
    alipaySelect(select) {
      this.alipayChecked = select;
      this.vipChecked = false;
      this.wechatChecked = false;
      this.payChanel = 'alipay_wap';
    },
    /**
     * 立即支付
     */
    payConfirm() {
      let params = {
        params: {
          channel: this.payChanel
        }
      };
      this.$http.get(this.payInfo['payment_url'], params).then((res) => {
        if (window.buildType === 'wx') {
          window.wx.chooseWXPay({
            timestamp: res.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
            nonceStr: res.nonceStr, // 支付签名随机串，不长于 32 位
            package: res.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
            signType: res.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
            paySign: res.paySign, // 支付签名
            success: (res) => {  // 支付成功后的回调函数
              this.$router.push({ name: 'order' });
            }
          });
        } else if (window.buildType === 'app') {
          let params = {
            partnerid: res.credential.wx.partnerId, // merchant id
            prepayid: res.credential.wx.prepayId, // prepay id
            noncestr: res.credential.wx.nonceStr, // nonce
            timestamp: res.credential.wx.timeStamp, // timestamp
            sign: res.credential.wx.sign // signed string
          };
          window.Wechat.sendPaymentRequest(params, function() {
            this.$router.push({ name: 'order' });
          }, function(reason) {
            alert(reason);
          });
        }
      });
    },
    /**
     * 获取会员信息
     */
    getVipInfo() {
      this.$http.get(app.url.account).then((res) => {
        res.username = getStorage('user').username;
        this.vipInfo = res;
      });
    }
  }
};
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  .dialog-pay
    position: fixed
    top: 0
    left: 0
    width: 100%
    height: 100%
    background: rgba(0, 0, 0, 0.5)
    z-index: 300
    .dialog-pay-wrapper
      position: absolute
      width: 100%
      min-height: 3rem
      left: 0
      bottom: 0
      background: #fff
      padding: 0 0.1rem
      box-sizing: border-box
      .pay-title
        width: 100%
        height: 0.4rem
        line-height: 0.4rem
        font-size: 0.16rem
        color: #000
        & > h1
          text-align: center
        .icon-wrong
          position: absolute
          top: 0
          left: 0.15rem
      .pay-info
        & > div
          width: 100%
          height: 0.4rem
          line-height: 0.4rem
          .text
            display: block
            float: left
            color: #000
            font-size: 0.12rem
          .username
            display: block
            float: right
            color: #000
          .total-price
            display: block
            float: right
            color: red
      .pay-type-wrapper
        .pay-types
          .pay-type-item
            display: flex
            font-size: 0.12rem
            height: 0.4rem
            line-height: 0.4rem
            .vip-blance
              padding: 0 0.05rem
            .icon-huiyuanqia
              color: red
            .icon-weixin
              color: rgb(23, 211, 0)
            .icon-zhifubao
              color: rgb(17, 258, 235)
            :nth-child(1)
              flex: 0 0 0.3rem
              height: 100%
            :nth-child(2)
              flex: 1
              text-align: left
            :last-child
              flex: 0 0 0.2rem
      .pay-submit
        height: 0.4rem
        line-height: 0.4rem
        text-align: center
        color: #fff
        margin: 0.4rem 0.2rem
        background: deepskyblue
</style>



