<template>
  <div class="position">
    <div id="positionMap"></div>
  </div>
</template>

<script>
import baseURL from '../../common/js/app.js';
export default {
  data() {
    return {
      longitude: 116.430291,
      latitude: 39.996738,
      district: ''
    };
  },
  activated() {
    this.longitude = 0;
    this.latitude = 0;
    this.district = '';
    this.positionURL(this.loadMapSdk);
  },
  methods: {
    loadMapSdk() {
      var map = new window.BMap.Map('positionMap');
      map.centerAndZoom(new window.BMap.Point(this.longitude, this.latitude), 15);
      map.addControl(new window.BMap.NavigationControl());
      var myGeo = new window.BMap.Geocoder();
      // 将地址解析结果显示在地图上,并调整地图视野
      myGeo.getPoint(this.district, function (point) {
        if (point) {
          map.centerAndZoom(point, 15);
          map.addOverlay(new window.BMap.Marker(point));
        } else {
          alert('您选择地址没有解析到结果!');
        }
      }, this.district);
    },
    positionURL(callback) {
      const shopId = this.$route.query.shopID;
      console.log(shopId);
      // const shopId = 861;
      this.$http.get(`${baseURL.url.partners}${shopId}`)
        .then(result => {
          console.log(result);
          this.longitude = Number(result.longitude);
          this.latitude = Number(result.latitude);
          this.district = result.district;
          callback();
        });
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.position
  height: 100%
  width: 100%
  position: relative
  z-index: 300
  background-color: #fff
  padding: 0
  #positionMap
    height: 100%
    width: 100%
</style>



