<template>
  <div class="goods-item" @click="goGoodsDetail">
    <div class="left">
      <img :src="goods.images[0]" alt="商品图片">
    </div>
    <div class="right">
      <h1 class="goods-name">{{goods.title}}</h1>
      <div class="goods-info">
        <span>月销{{goods.sales_per_month}}笔</span>
        <span>旗舰店</span>
      </div>
      <h2 class="goods-price">¥ {{goods.purchase_info.price}}</h2>
    </div>
  </div>
</template>
<script>
  export default {
    props: {
      goods: {
        type: Object,
        default: {}
      }
    },
    methods: {
      goGoodsDetail() {
        this.$router.push({name: 'goodsDetail', query: {goodsId: this.goods.id}});
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  .goods-item
    display: flex
    height: 1.25rem
    padding: 0.05rem
    background-color: #fff
    .left
      flex: 0 0 1.15rem
      height: 1.15rem
      overflow: hidden
      img
        width: 100%
        height: 100%
    .right
      flex: 1
      text-align: left
      padding: 0.05rem 0.1rem
      .goods-name
        font-size: 0.14rem
        color: #222
        line-height : .15rem
      .goods-info
        padding: 0.1rem 0
        color: #d0d0d0
        font-size: 0.11rem
      .goods-price
        padding-top: 0.3rem
        color: #ff4242
        font-size: 0.1rem
</style>



