<template>
  <div class="register-now">
    <div class="register-now-header">
      <span class="register-Id">已有账号<em>?</em><strong><router-link to="/login">登录</router-link></strong></span>
    </div>
    <div class="logo-wrapper">
      <img src="./logo.png" alt="logo">
    </div>
    <div class="verify-code-wrapper">
      <label class="verify-code-number">+86&nbsp;&nbsp;&nbsp;</label>
      <input type="number" class="telephone-number" placeholder="请输入手机号码" maxlength="11" v-model="username"
             @change.lazy="checkPhone"/><br/>
    </div>
    <div class="verify-code-auth">
      <label class="verify-code-numbers">验证码</label>
      <input type="number" class="telephone-auth-code" placeholder="请输入验证码" v-model="verifyCode"/>
      <button class="btn-auth-code btn" @click="getVerifyCode">{{ text }}


      </button>
      <br/>
    </div>
    <div class="verify-code-password">
      <label class="register-password">密码&nbsp;&nbsp;&nbsp;</label>
      <input type="password" v-model="password" class="input-password" placeholder="请输入密码(6-32位字符)"/>
    </div>
    <h1 class="user-agreement">
      <input type="checkbox" class="register-check" :checked="checked" @click = 'checked = !checked'/>
      注册即表示您阅读并同意联众同行
      <a href="http://www.baidu.com">《用户使用协议》</a>
    </h1>
    <div class="btn-register-wrapper">
      <button class="btn-register" @click="register">立即注册


      </button>
    </div>
    <c-back></c-back>
  </div>
</template>
<script>
  import {Toast} from 'mint-ui';
  import {setStorage, checkPhoneNum} from 'common/js/util';
  import app from '../../common/js/app.js';
  import backBar from 'components/common/backBar/backBar';
  export default {
    data() {
      return {
        username: '',
        password: '',
        verifyCode: '',
        time: 0,
        checked: false
      };
    },
    components: {
      'c-back': backBar
    },
    activated() {
    },
    computed: {
      text() {
        return this.time === 0 ? '获取验证码' : this.time + 's 后重新获取';
      }
    },
    methods: {
      /**
       * 注册
       */
      register() {
        if (!this.username) {
          Toast({
            message: '请输入手机号',
            position: 'bottom',
            duration: 1500
          });
          return;
        }

        if (!this.verifyCode) {
          Toast({
            message: '请输入验证码',
            position: 'bottom',
            duration: 1500
          });
          return;
        }

        if (!this.password) {
          Toast({
            message: '请输入密码',
            position: 'bottom',
            duration: 1500
          });
          return;
        }

        if (!this.checked) {
          Toast({
            message: '请阅读并同意用户协议',
            position: 'bottom',
            duration: 1500
          });
          return;
        }
        let params = new FormData();
        params.append('username', this.username);
        params.append('verifycode', this.verifyCode);
        params.append('password', this.password);

        this.$http.post(app.url.register, params).then((res) => {
          this.$store.commit('setUser', {username: this.username, password: this.password});
          // 缓存用户信息
          setStorage('user', {username: this.username, password: this.password});
        }, (err) => {
          Toast({
            message: err,
            position: 'bottom',
            duration: 1500
          });
        });
      },
      /**
       * 校验手机号
       */
      checkPhone() {
        if (!this.username) {
          return;
        }
        if (!checkPhoneNum(this.username)) {
          Toast({
            message: '手机号不合法',
            position: 'bottom',
            duration: 1500
          });
          return;
        }
      },
      /**
       * 获取验证码
       */
      getVerifyCode() {
        if (!this.username) {
          Toast({
            message: '请先填写手机号',
            position: 'bottom',
            duration: 1500
          });
          return;
        }

        let params = {
          params: {
            mobie: this.username
          }
        };
        this.time = 59;
        let timmer = setInterval(() => {
          this.time--;
          if (this.time === 0) {
            clearInterval(timmer);
          }
        }, 1000);

        this.$http.get(app.url.verifyCode, params).then((success) => {
          Toast({
            message: '验证码已发送至您的手机',
            position: 'bottom',
            duration: 1500
          });
        }, (e) => {
          Toast({
            message: '验证码发送失败，请稍后再试',
            position: 'bottom',
            duration: 1500
          });
          clearInterval(timmer);
          this.time = 0;
        });
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .register-now
    position: relative
    width: 100%
    height: 100%
    z-index: 300
    font-size: 0.13rem
    background: #fff
    .register-now-header
      float: right
      padding: 0.15rem 0.1rem
      .icon-wrong
        displayy: inline-block
        width: 0.25rem
        height: 0.2rem
        font-size: 0.25rem
      .register-Id
        color: #dddddd
        em
          color: #4bb8fe
        strong
          a
            color: #000000
    .logo-wrapper
      text-align: center
      padding: 0.3rem 0
      clear: both
      img
        width: 1.5rem
    .verify-code-wrapper
      border-top: 0.08rem solid #f6f6f6
      border-bottom: 0.01rem solid #f6f6f6
      padding: 0.04rem 0.8rem 0.04rem 0.3rem
      .verify-code-number
        float: left
        line-height: 0.34rem
      .telephone-number
        outline: none
        height: 0.3rem
      .verify-code-number
        float: left
        font-size: 0.16rem
    .verify-code-auth
      border-bottom: 1px solid #f6f6f6
      padding: 0.04rem 0.8rem 0.04rem 0.3rem
      position: relative
      .verify-code-numbers
        float: left
        line-height: 0.34rem
      .telephone-auth-code
        height: 0.3rem
        outline: none
      .btn-auth-code
        position: absolute
        right: 0.7rem
        top: 0.1rem
        outline: none
        font-size: 0.12rem
        border: 1px solid #4bb8fe
        background: white
        border-radius: 0.04rem
        color: #4bb8fe
    .verify-code-password
      border-bottom: 0.08rem solid #f6f6f6
      padding: 0.04rem 0.8rem 0.04rem 0.3rem
      .register-password
        float: left
        line-height: 0.34rem
      .input-password
        clear: both
        height: 0.3rem
        outline: none

  .user-agreement
    text-align: left
    font-size: 0.12rem
    color: #d0d0d0
    padding: 0.3rem 0.3rem
    a
      color: #4bb8fe

  .btn-register
    padding: 0.1rem 0.4rem
    border: 1px solid #4bb8fe
    color: #4bb8fe
    background: white
</style>



