<template>
  <div class="memberCard">
    <img class="vipLogo"
         src="./vip.png"
         alt="" />
    <div class="balance">
      <p class="title">余额</p>
      <p class="money">¥&nbsp;<span>{{balance}}</span></p>
      <p class="tagging">账户保障,资金安全</p>
    </div>
    <div class="detailed">
      <div class="title">交易明细</div>
      <div class="detailedList">
        <div class="detailedItem">
          <p class="time"></p>
          <div class="nameType"></div>
          <div class="price"></div>
        </div>
      </div>
    </div>
    <div class="rechargeBtn"
         @click="goRecharge">
      充值
    </div>
    <backBar></backBar>
  </div>
</template>

<script>
import backBar from '../common/backBar/backBar';
export default {
  data() {
    return {
      balance: 0,
      cardNum: 0
    };
  },
  activated() {
    this.balance = this.$route.query.balance;
    this.cardNum = this.$route.query.cardNum;
  },
  components: {
    'backBar': backBar
  },
  methods: {
    goRecharge() {
      this.$router.push({ name: 'recharge', query: { balance: this.balance, cardNum: this.cardNum } });
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.memberCard
  height: 100%
  position: relative
  z-index: 300
  background-color: #fff
  box-sizing: border-box
  overflow-y: auto
  .vipLogo
    width 3.7rem
    height: 2.45rem
    box-sizing: border-box
    padding: .1rem
    border-radius: .3rem
    overflow: hidden
  .balance
    box-sizing: border-box
    padding: .1rem
    color: #666565
    border-bottom .01rem solid #d0d0d0
    ont-size: .16rem
    &>.title
      font-weight: bold
    &>.money
      font-weight: bold
      padding: .2rem 0
      &>span
        font-size: .18rem
        font-weight bold
        color: #9c4294
    &>.tagging
      font-size: .14rem
      font-weight: bold
      padding: .2rem
  .detailed
    height: 1.3rem
    text-align: left
    &>.title
      box-sizing: border-box
      height: .25rem
      padding: .08rem .1rem
      font-size: .09rem
      color: #222
    &>.detailedList
      height: 1.05rem
      box-sizing: border-box
      border-top: .05rem solid #edebeb
      overflow-y: auto
      .detailedItem
        height: .5rem
        box-sizing: border-box
        padding: 0 .1rem
        border-bottom 1px solid #d0d0d0
        .time
          font-size: .09rem
          padding: .05rem 0
        .nameType
          float: left
          font-size: .15rem
          font-weight: bold
        .price
          float: right
          font-size: .18rem
          color: #9c4294
          font-weight: bold
  .rechargeBtn
    margin: 0 auto
    margin-top: .225rem
    background-color: #9c4294
    width: 1.9rem
    height: .3rem
    line-height: .3rem
    color: #fff
    font-weight: bold
    border-radius: .06rem
</style>



