<template>
  <div class="home">
    <v-header>联众到家</v-header>
    <mt-loadmore class="pull-refresh"
                 :top-method="loadTop"
                 :bottom-method="loadBottom"
                 :bottom-all-loaded="allLoaded"
                 topPullText="↓"
                 topDropText="↑"
                 bottomPullText="上拉加载"
                 bottomDropText="释放加载"
                 ref="loadmore">
      <div class="swipe-wrapper">
        <mt-swipe :auto="3000"
                  :continuous="true">
          <mt-swipe-item v-for="banner in bannerList"
                         :key="banner.id">
            <a :href="banner.url">
              <img :src="banner.image"
                   alt="广告图">
            </a>
          </mt-swipe-item>
        </mt-swipe>
      </div>
      <div class="ad-wrapper">
        <img src="./ad1.jpg" alt="广告占位图">
      </div>
      <div class="submodule-wrapper">
        <ul class="submodule-list">
          <li class="submodule-item" v-for="submodule in submoduleList" @click="goSearch(submodule.id)">
            <a>
              <img :src="submodule.image"></img>
              <span class="module-name">{{submodule.name}}</span>
            </a>
          </li>
        </ul>
      </div>
      <div class="hot-recommend" v-show="false">
        <div class="recommend-item" v-for="recommend in recommendList">
          <div class="recommend-title border-1px">
            <img class="icon" :src="recommend.iconPath" alt=""><span>{{recommend.title}}</span>
          </div>
          <ul class="goods-list">
            <li class="goods-item border-1px">
              <div class="left">
                <h1>电影之旅</h1>
                <h2>新片首应</h2>
              </div>
              <div class="right">
                <img src="./movie.jpeg" alt="商品图片">
              </div>
            </li>
            <li class="goods-item border-1px">
              <div class="left">
                <h1>电影之旅</h1>
                <h2>新片首应</h2>
              </div>
              <div class="right">
                <img src="./movie.jpeg" alt="商品图片">
              </div>
            </li>
            <li class="goods-item border-1px">
              <div class="left">
                <h1>电影之旅</h1>
                <h2>新片首应</h2>
              </div>
              <div class="right">
                <img src="./movie.jpeg" alt="商品图片">
              </div>
            </li>
            <li class="goods-item border-1px">
              <div class="left">
                <h1>电影之旅</h1>
                <h2>新片首映</h2>
              </div>
              <div class="right">
                <img src="./movie.jpeg" alt="商品图片">
              </div>
            </li>
          </ul>
        </div>
      </div>
      <v-recommend-list></v-recommend-list>
    </mt-loadmore>
    <v-search class="search" :searchClick="goSearch"></v-search>
  </div>
</template>
<script>
  //  import {Indicator} from 'mint-ui';
  import header from 'components/common/header/header';
  import search from 'components/common/search/search';
  import recommendList from 'components/common/recommendList/recommendList';
  import app from 'common/js/app';
  export default {
    data() {
      return {
        bannerList: [],
        allLoaded: false,
        moduleList: [], // 一级模块列表
        submoduleList: [], // 子模块列表
        recommendList: [
          {
            title: '健康商城',
            iconPath: require('./更多@2x.png'),
            recommendGoodsList: []
          },
          {
            title: '健康到家',
            iconPath: require('./更多@2x.png'),
            recommendGoodsList: []
          },
          {
            title: '同行之约',
            iconPath: require('./更多@2x.png'),
            recommendGoodsList: []
          },
          {
            title: '理财推荐',
            iconPath: require('./更多@2x.png'),
            recommendGoodsList: []
          }
        ], // 推荐列表
        goodsList: [],
        pageSize: 10, // pagesize
        pageNum: 0,
        loading: true // 是否正在加载数据
      };
    },
    created() {
      this.init();
    },
    activated() {
    },
    components: {
      'v-header': header,
      'v-search': search,
      'v-recommend-list': recommendList
    },
    methods: {
      /**
       * 初始化
       */
      init() {
        this.getAdList();
        this.getModuleList();
      },
      /**
       * 获取广告图
       */
      getAdList() {
        let params = {
          params: {
            positions: 'home-top'
          }
        };
        this.$http.get(app.url.ads, params).then((res) => {
          this.bannerList = res['home-top'];
        });
      },
      /**
       * 获取子模块列表
       */
      getModuleList() {
        let params = {
          params: {
            filter: '到家'
          }
        };
        this.$http.get(app.url.modules, params).then((res) => {
          this.submoduleList = res;
        });
      },
      /**
       * 跳转搜索页面
       * @param moduleId 模块ID
       */
      goSearch(moduleId) {
        let params = {
          name: 'goodsSearch'
        };
        if (moduleId) {
          params.query = {moduleId: moduleId};
        }
        this.$router.push(params);
      },
      /**
       * 下拉刷新
       */
      loadTop() {
        this.pageNum = 0;
        this.goodsList = [];
        this.init();
      },
      loadBottom() {
        if (!this.loading) {
          this.getGoodsList();
        }
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/mixin.styl"
  .home
    width: 100%
    height: 100%
    background: #f5f5f5
    .search
      position: absolute
      top: 0.44rem
      left: 0
      width: 100%
    .pull-refresh
      padding-bottom: 0.5rem
      .swipe-wrapper
        width: 100%
        height: 1.55rem
        overflow: hidden
        img
          width: 100%
          height: 1.55rem
      .submodule-wrapper
        .submodule-list
          display: flex
          height: 1.75rem
          flex-wrap: wrap
          align-content: space-between
          padding-top: 0.15rem
          background: #fff
          .submodule-item
            width: 25%
            a
              display: flex
              height: 0.875rem
              flex-direction: column
              justify-content: space-between
              align-items: center
              box-sizing: border-box
              font-size: 0.12rem
              img
                display: block
                width: 0.45rem
                height: 0.45rem
              .module-name
                display: block
                padding-bottom: 0.15rem
      .hot-recommend
        background: #f5f5f5
        .recommend-item
          margin-top: 0.07rem
          background: #fff
          .recommend-title
            height: 0.32rem
            width: 100%
            line-height: 0.32rem
            font-size: 0.15rem
            color: #000
            border-1px(#f5f5f5)
            .icon
              display: inline-block
              width: 0.32rem
              height: 0.32rem
              padding: 0.06rem
              box-sizing: border-box
            span
              display: inline-block
              height: 0.32rem
              line-height: 0.32rem
              vertical-align: top
        .goods-list
          width: 100%
          height: 2.2rem
          overflow: hidden
          .goods-item
            width: 50%
            height: 1.1rem
            float: left
            border-1px(#f5f5f5)
            box-sizing: border-box
            &:nth-of-type(odd)
              border-right: 0.5px solid #f5f5f5
            .left
              float: left
              width: 50%
              height: 100%
              text-align: center
              padding: 0.12rem 0.14rem 0 0.1rem
              box-sizing: border-box
              h1
                font-size: 0.15rem
                color: #4c4c4c
              h2
                font-size: 0.09rem
                color: #1fc956
            .right
              float: left
              width: 50%
              height: 100%
              text-align: center
              box-sizing: border-box
              img
                width: 100%
                height: 100%
                padding: 0.15rem 0.2rem 0.15rem 0
                box-sizing: border-box
</style>



