<template>
  <div class="shopcart-item">
    <div class="shop-title">
      <span :class="item.partner_info.select?'iconfont icon-duihao1':'off'"
            @click="toggleStore"></span>
      <span class="shop-name">{{item.partner_info.name}}</span>
    </div>
    <div class="goods-list-wrapper">
      <ul class="goods-list">
        <li class="goods-item"
            v-for="(goods,index) in item.product_list" @click="goShopcartDetail(goods.id)">
          <div class="left"
               @click.stop="toggleGoods(index)">
            <span :class="goods.select?'iconfont icon-duihao1':'off'"></span>
          </div>
          <div class="center">
            <img :src="goods.images[0]"
                 alt="">
          </div>
          <div class="right">
            <h1 class="goods-name">{{goods.title}}</h1>
            <h2 class="goods-spc">颜色分类：灰色</h2>
            <div class="goods-price">
              <span class="goods-now-price">¥{{goods.purchase_info.price}}</span>
              <span class="goods-old-price" v-show="goods.price_retail">¥{{goods.purchase_info.price_retail}</span>
              <span class="goods-count">x{{goods.quantity}}</span>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    item: {
      type: Object,
      default: {}
    }
  },
  methods: {
    toggleStore() {
      this.$store.commit('updateStoreSelectState', this.item.partner_info.id);
    },
    toggleGoods(index) {
      this.$store.commit('updateGoodsSelectState', { storeId: this.item.partner_info.id, index: index });
    },
    goShopcartDetail(goodsId) {
        this.$router.push({name: 'goodsDetail', query: {goodsId: goodsId}});
    }
  }
};
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import '../../../common/stylus/mixin.styl'
  .shopcart-item
    .shop-title
      display: flex
      width: 100%
      height: 0.4rem
      color: #000
      line-height: 0.4rem
      border-1px(#f2f2f2)
      .shop-name
        flex: 1
        text-align: left
        font-size: 0.14rem
      .icon-duihao1
        display: block
        flex: 0 0 0.4rem
        height: 0.4rem
        width: 0.4rem
        align-self: center
        color: #ff4242
        border-radius: 50%
      .off
        display: block
        flex: 0 0 0.17rem
        height: 0.17rem
        width: 0.17rem
        align-self: center
        color: transparent
        border: 1px solid #f2f2f2
        box-sizing: border-box
        margin: 0.115rem
        border-radius: 50%
    .goods-list-wrapper
      .goods-list
        .goods-item
          display: flex
          height: 1.14rem
          .left
            flex: 0 0 0.4rem
            text-align: center
            .icon-duihao1
              display: block
              height: 0.15rem
              width: 0.15rem
              color: #ff4242
              border-radius: 50%
              margin: 0.115rem
            .off
              display: block
              height: 0.15rem
              width: 0.15rem
              border-radius: 50%
              border: 1px solid #f2f2f2
              margin: 0.115rem
          .center
            flex: 0 0 0.93rem
            height: 100%
            border-1px(#f2f2f2)
            img
              width: 0.93rem
              height: 0.93rem
              padding: 0.1rem 0
          .right
            position: relative
            flex: 1
            height: 100%
            padding: 0.15rem 0.05rem 0.15rem 0.2rem
            box-sizing: border-box
            border-1px(#f2f2f2)
            .goods-name
              font-size; 0.14rem
              color: #000
              text-align: left
            .goods-spc
              font-size: 0.1rem
              height: 0.2rem
              line-height: 0.2rem
              color: #9a9a9a
              text-align: left
            .goods-price
              padding-top: 0.3rem
              text-align: left
              .goods-now-price
                color: #fc2930
                font-size: 0.14rem
              .goods-old-price
                font-size: 0.1rem
                text-decoration: line-through
                color: #9a9a9a
              .goods-count
                display: block
                float: right
</style>



