<template>
  <div class="goods-order-submit">
    <v-header>订单提交</v-header>
    <div class="receive-address-wrapper">
      <div class="add-adderss" v-show="!receiverAddress">新增地址</div>
      <div class="receive-address" v-show="receiverAddress">
        <div class="receiver-name">收货人: {{receiverAddress.receiver}} {{receiverAddress.phone_number}}</div>
        <div class="receiver-address">收货地址: {{receiverAddress.district}} {{receiverAddress.address}} </div>
      </div>
    </div>
    <v-goods-item :goods="item" v-for="item in this.$route.query.goodsList"></v-goods-item>
    <div class="footer">
      <span class="order-price">待支付¥{{getOrderTotalPrice}}</span>
      <span class="order-submit" @click="placeSubmit">提交订单</span>
    </div>
    <v-fade-in>
      <v-dialog-pay :close="hidePayDialog" :payInfo="payInfo" v-show="showPayDialog"></v-dialog-pay>
    </v-fade-in>
  </div>
</template>
<script>
  import {mapState} from 'vuex';
  import app from 'common/js/app';
  import header from 'components/common/header/header';
  import goodsItem from 'components/common/goodsItem/goodsItem';
  import dialogPay from 'components/common/dialogPay/dialogPay';
  import fadeIn from 'components/common/fadeIn/fadeIn';

  export default {
    data() {
      return {
        goodsList: [],
        receiverAddress: {},
        showPayDialog: false,
        payInfo: {}
      };
    },
//    beforeRouteEnter (to, from, next) {
//    },
    activated() {
      this.getAddressInfo();
    },
    computed: {
      ...mapState([
        'selectedAddressId'
      ]),
      getOrderTotalPrice() {
        let totalPrice = 0;
        this.$route.query.goodsList.forEach((item) => {
          totalPrice += item.purchase_info.price * (item.quantity ? item.quantity : 1);
        });
        return totalPrice;
      },
      getGoodsIdList() {
        let goodsIdList = [];
        this.$route.query.goodsList.forEach((item) => {
            goodsIdList.push(item.id);
        });
        return goodsIdList;
      }
    },
    components: {
      'v-header': header,
      'v-goods-item': goodsItem,
      'v-dialog-pay': dialogPay,
      'v-fade-in': fadeIn
    },
    methods: {
      /**
       * 获取地址信息
       */
      getAddressInfo() {
        this.$http.get(app.url.address).then((res) => {
          // 有默认地址取默认地址  无默认地址则取第一条
          res.forEach((item, index) => {
            if (item.is_default_for_shipping && index === res.length) {
              this.receiverAddress = res[index];
            } else {
              this.receiverAddress = res[0];
            }
          });
        });
      },
      /**
       * 提交订单
       */
      placeSubmit() {
        let params = {
          total: this.$store.getters.getShopcartTotalPrice,
          shipping_method_code: 'no-shipping-required',
          shipping_charge: '6',
          user_address_id: this.receiverAddress.id.toString(),
          product_ids: this.getGoodsIdList
        };
        let formData = new FormData();
        for (let key in params) {
          formData.append(key, params[key]);
        }
        this.$http.post(app.url.placeOrder, formData).then((res) => {
          this.payInfo = res;
          this.showPayDialog = true;
        });
      },
      hidePayDialog() {
        this.showPayDialog = false;
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  .goods-order-submit
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    background: #fff
    z-index: 400
    .receive-address-wrapper
      padding: 0.1rem
      text-align: left
      .receive-address
        .receiver-name
          border-bottom: 1px solid #f2f2f2
          height: 0.3rem
          line-height: 0.3rem
        .receiver-address
          height: 0.3rem
          line-height: 0.3rem
    .footer
      position: fixed
      display: flex
      width: 100%
      height: 0.4rem
      line-height: 0.4rem
      left: 0
      bottom: 0
      border-top: 1px solid #f2f2f2
      .order-price
        flex: 1
        text-align: right
        padding: 0 0.1rem
      .order-submit
        flex: 0 0 0.9rem
        color: #fff
        text-align: center
        background: red
</style>



