<template>
  <div class="goods-search">
    <v-search-header class="search-header" :search="productSearch"></v-search-header>
    <div class="tab-wrapper">
      <div class="tab-item" :class="{active:currentTab===0}" @click="orderBy('-rating', 0)">
        综合

      </div>
      <div class="tab-item" :class="{active:currentTab===1}" @click="orderBy('-sales_count', 1)">
        销量

      </div>
      <div class="tab-item" :class="{active:currentTab===2}" @click="orderBy(orderByPrice?'price':'-price', 2)">
        <span>价格</span>
        <span class="iconfont icon-paixu"></span>
      </div>
    </div>
    <mt-loadmore class="pull-refresh"
                 :top-method="loadTop"
                 :bottom-method="loadBottom"
                 :bottom-all-loaded="allLoaded"
                 topPullText="↓"
                 topDropText="↑"
                 bottomPullText="上拉加载"
                 bottomDropText="释放加载"
                 ref="loadmore">
      <ul class="goods-list">
        <v-goods-item :goods="goods" v-for="(goods, index) in goodsList" :key="index"
                      v-if="goodsList.length>0"></v-goods-item>
      </ul>
    </mt-loadmore>
  </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  import app from 'common/js/app';
  import goodsitem from 'components/common/goodsItem/goodsItem';
  import goodsSearchHeader from 'components/common/goodsSearchHeader/goodsSearchHeader';
  export default {
    data() {
      return {
        currentTab: 0,
        goodsList: [],
        pageNum: 1,
        loading: true, // 是否正在加载数据
        allLoaded: false,
        orderByPrice: true, // 价格正序||倒序
        keyWord: '',
        categories: null,
        sortType: '-rating' // 排序类型
      };
    },
    activated() {
      this.goodsList = [];
      if (this.$route.query.moduleId) {
        this.categories = this.$route.query.moduleId;
        this.productSearch(this.keyword, this.categories, '-rating');
      } else {
        this.productSearch(this.keyword, '', '-rating');
      }
    },
    components: {
      'v-search-header': goodsSearchHeader,
      'v-goods-item': goodsitem
    },
    methods: {
      /**
       * 搜索排序
       * @param sortType
       */
      orderBy(sortType, currentTab) {
        this.currentTab = currentTab;
        this.sortType = sortType;
        if (this.sortType === 'price' || this.sortType === '-price') {
          this.orderByPrice = !this.orderByPrice;
        }
        this.goodsList = [];
        this.productSearch(this.keyword, this.categories, this.sortType);
      },
      /**
       * 商品搜索
       * @param keyword 搜索关键字
       * @param categories 模块ID
       * @param sortType 排序类型
       */
      productSearch(keyword, categories, sortType) {
        this.keyword = keyword;
        this.categories = categories;
        this.sortType = sortType;
        if (!this.keyword && !this.categories && !this.sortType) {
          Toast({
            message: '请输入搜索内容',
            position: 'bottom',
            duration: '500'
          });
          return;
        }
        let params = {
          params: {
            order_by: this.sortType,
            title: this.keyword,
            categories: this.categories,
            page: this.pageNum
          }
        };
        this.$http.get(app.url.productSearch, params).then((res) => {
          res.results.forEach((item) => {
            this.goodsList.push(item);
            this.$refs.loadmore.onBottomLoaded();
            this.$refs.loadmore.onTopLoaded();
          });
          this.loading = false;
        }, (e) => {
          if (this.pageNum >= 2) this.pageNum--;
        });
        console.log(this.goodsList);
      },
      cancle() {
        this.$router.go(-1);
      },
      /**
       * 下拉刷新
       */
      loadTop() {
        if (!this.loading) {
          this.pageNum = 1;
          this.goodsList = [];
          this.productSearch(this.keyword, this.categories, this.sortType);
        }
      },
      /**
       * 上拉加载
       */
      loadBottom() {
        if (!this.loading) {
          this.pageNum++;
          this.productSearch(this.keyword, this.categories, this.sortType);
        }
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped="true">
  .goods-search
    display: flex
    position: relative
    top: 0
    left: 0
    flex-direction: column
    height: 100%
    width: 100%
    z-index: 500
    background: #fff
    padding-bottom: 0
    .search-header
      position: fixed
      top: 0
      left: 0
      z-index: 300
    .tab-wrapper
      position: fixed
      top: 0.44rem
      left: 0
      display: flex
      justify-content: space-between
      align-items: center
      height: 0.4rem
      width: 100%
      z-index: 200
      background: #fff
      .tab-item
        height: 100%
        line-height: 0.4rem
        flex: 1
        text-align: center
        border-bottom: 0.02rem solid transparent
        &.active
          border-bottom: 0.02rem solid #ff4242
    .pull-refresh
      position: absolute
      top: 0.88rem
      left: 0
      background: #fff
      overflow: hidden
</style>



