<template>
  <div class="addAreaSelect" v-show="showItem" @click="hideList">
    <div class="areaList">
      <p class="selectArea">选择地区</p>
      <div class="areas">
        <ul class="areasIn">
          <li class="area" v-for="item in districtsList" @click="selectThis(item)">{{item.name}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
  import app from 'common/js/app';
  export default {
    data() {
      return {
        districtsList: [],
        selectArea: ''
      };
    },
    props: {
      showItem: {
        type: Boolean,
        default: true
      }
    },
    activated() {
      this.getDistrictsList();
    },
    methods: {
      hideList() {
        this.showItem = false;
        this.$emit('child-say', this.showItem, this.selectArea);
      },
      selectThis(item) {
        this.selectArea = item;
        console.log(item);
        console.log(this.selectArea);
      },
      /**
       * 获取地址列表
       */
      getDistrictsList() {
        let districtsList = [];
        this.$http.get(app.url.districts).then((res) => {
          for (let key in res) {
            districtsList.push({id: key, name: res[key]});
          }
          this.districtsList = districtsList;
        });
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .addAreaSelect
    width: 100%
    height: 100%
    background: rgba(0, 0, 0, .2)
    position: relative
    .areaList
      width: 70%
      height: 80%
      position: absolute
      left: 15%
      top: 5%
      padding: 0 .05rem
      background: #fff
      .selectArea
        height: 10%
        line-height: .6rem
      .areas
        text-align: left
        height: 90%
        overflow: auto
        .area
          line-height: .5rem
          border-bottom: 1px solid #f1f1f1
          text-indent: .1rem
</style>



