<template>
  <div class="receiver-address">
    <ul class="receiver-address-list">
      <li class="receiver-address-item border-1px" v-show="receiveAddressList.length > 0" v-for="item in receiveAddressList" @click='returnOrderSubmit'>
        <div class="address-left">
          <span class="icon-checkmark"></span>
        </div>
        <div class="address-right">
          <div class="header">
            <span class="receiver-name">{{item.receiverName}}</span>
            <span class="receiver-phone-num">{{item.receiverTel}}</span>
          </div>
          <div class="footer">{{item.address.addressProvince}}{{item.address.addressCity}}{{item.addressDeatil}}</div>
        </div>
        <div class="edit">编辑</div>
      </li>
    </ul>
    <div class="setDefalutAddress-wrapper">
      <h1>设为默认地址</h1>
      <h2>注：每次下单时将默认使用改地址</h2>
      <mt-switch class="switch" :value.sync="true"></mt-switch>
    </div>
    <footer>
      <v-border-button :click="createAddress">新建地址</v-border-button>
    </footer>
  </div>
</template>
<script>
import borderButton from 'components/common/borderButton/borderButton';
export default {
  data() {
    return {
      receiveAddressList: []
    };
  },
  activated() {
      this.getReceiveAddress();
  },
  components: {
    'v-border-button': borderButton
  },
  methods: {
    /**
     * 新建收货地址
     */
    createAddress() {
      // 跳转到新增地址页面
      this.$router.push({name: 'addReceiverAddress'});
      console.log('1111');
    },
    // 获取收货地址
    getReceiveAddress() {
        const query = new window.AV.Query('ReceiveAddress');
        query.find().then((res) => {
            this.receiveAddressList = JSON.parse(JSON.stringify(res));
        });
    },
    returnOrderSubmit() {
      this.$router.push({ name: 'orderSubmit' });
    }
  }
};
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .receiver-address
    position: relative
    width: 100%
    height: 100%
    background: #fff
    z-index: 300
    .receiver-address-list
      min-height: 0.6rem
      .receiver-address-item
        display: flex
        position: relative
        height: 0.6rem
        width: 100%
        border-1px(#f2f2f2)
        .address-left
          flex: 0 0 0.2rem
          width: 0.4rem
          height: 0.6rem
          line-height: 0.6rem
          .icon-checkmark
            display: block
            width: 0.23rem
            height: 0.23rem
            margin: 0.2rem 0.1rem
            font-size: 0.1rem
            padding-top: 0.04rem
            color: #fff
            background: #ff4442
            border-radius: 50%
            border: 1px solid #fff
            box-sizing: border-box
        .address-right
          flex: 1
          padding: 0.15rem 0
          box-sizing: border-box
          .header
            text-align: left
            font-size: 0.14rem
            color: #000
            .receiver-phone-num
              padding-left: 0.1rem
          .footer
            text-align: left
            margin-top: 0.07rem
            font-size: 0.12rem
            color: #636363
        .edit
          position: absolute
          height: 0.6rem
          line-height: 0.6rem
          top: 0
          right: 0.12rem
          font-size: 0.12rem
          color: #9a9a9a
    .setDefalutAddress-wrapper
      position: relative
      height: 0.4rem
      padding: 0.08rem 0.1rem
      box-sizing: border-box
      text-align: left
      border-1px(#f2f2f2)
      h1
        font-size: 0.1rem
        color: #000
      h2
        font-size: 0.1rem
        color: #9a9a9a
      .switch
        position: absolute
        top: 0.04rem
        right: 0.1rem
    footer
      position: absolute
      left: 0
      bottom: 0.4rem
      width: 100%
      height: 0.3rem
</style>



