<template>
  <div class="collect">
    <div class="header">
      <v-header class="det-tit">我的收藏</v-header>
      <div class="collectType">
        <div v-for="(item, index) in collectTypes"
             :key="index"
             @click="sellectType(index)"
             :class="{checked:item.isSellect}">
          {{item.name}}
        </div>
      </div>
    </div>
    <div class="page-tab-container">
      <mt-tab-container class="page-tabbar-tab-container" v-model="active" swipeable>
        <mt-tab-container-item id="tab-container1">
          <v-collectItem :goods="item"
                         v-for="(item, index) in collectGoods"
                         :key="index">
          </v-collectItem>
        </mt-tab-container-item>
        <mt-tab-container-item id="tab-container3" class="containerItem3">
          <div class="collectStore">
            <ul class="hotCommentList">
              <li class="hotCommentItem"
                  v-for="(item, index) in collectStore" @click="goStoreDetail(item.partner.id)">
                <img class="hotImg"
                     :src=" item.partner.store_images[0] "
                     alt="热评">
                <span class="hotMess">
              <p class="title">{{item.partner.name}}</p>
              <p class="mess"><span>月销：{{item.partner.sales_per_month}}笔</span><span class="miter">{{item.partner.distance}}</span></p>
            </span>
              </li>
            </ul>
          </div>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
  </div>
</template>


<script>
  import app from 'common/js/app';
  import header from 'components/common/header/header';
  import collectItem from 'components/common/collectItem/collectItem';
  export default {
    name: 'page-tab-container',
    data() {
      return {
        active: 'tab-container1',
        collectTypes: [
          {name: '商品', isSellect: true}, {name: '店铺', isSellect: false}
        ],
        tabContainer: ['tab-container1', 'tab-container3'],
        // 测试数据
        collectStore: [],
        collectGoods: []
      };
    },
    created() {
      this.init();
    },
    components: {
      'v-header': header,
      'v-collectItem': collectItem
    },
    methods: {
      /**
      *初始化
      */
      init() {
        this.getCollectGoods();
        this.getCollectStore();
      },
      /**
      *商铺详情跳转
      */
      goStoreDetail(storeId) {
        this.$router.push({name: 'storeDetail', query: {storeId: storeId}});
      },
      sellectType(indexs) {
        this.collectTypes.forEach(function (collectItem, index) {
          if (indexs === index) {
            collectItem.isSellect = true;
          } else {
            collectItem.isSellect = false;
          }
        });
        this.active = this.tabContainer[indexs];
      },
      /**
      *请求收藏数据
      */
      getCollectGoods() {
        this.$http.get(app.url.collectGoods).then((res) => {
          this.collectGoods = res.results;
          console.log(this.collectGoods);
        });
      },
      getCollectStore() {
        this.$http.get(app.url.collectStore).then((res) => {
          this.collectStore = res.results;
          console.log(this.collectStore);
        });
      }
    },
    updated() {
      let indexs;
      switch (this.active) {
        case 'tab-container1':
          indexs = 0;
          break;
        case 'tab-container3':
          indexs = 1;
          break;
        default:
          indexs = 0;
      }
      this.collectTypes.forEach(function (collectItem, index) {
        if (indexs === index) {
          collectItem.isSellect = true;
        } else {
          collectItem.isSellect = false;
        }
      });
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .collect
    width: 100%
    height: 100%
    position: relative
    background-color: #edebeb
    z-index: 900
    .header
      .det-tit
        background-color: #ffffff
      .collectType
        display: flex
        flex-wrap: nowrap
        box-sizing: border-box
        height: .5rem
        width: 100%
        justify-content: space-around
        border-bottom: .1rem solid #edebeb
        border-top: .01rem solid #edebeb
        padding-bottom: .1rem
        background-color: #ffffff
        & > div
          color: #313131
          font-size: .15rem
          height: .38rem
          line-height: .4rem
          padding: .0rem .16rem
          text-align: center
        .checked
          border-bottom: .018rem solid #b14ea8
          color: #b14ea8
    .page-tab-container
      .containerItem3
        .collectStore
          .hotCommentList
            background-color: #ffffff
            .hotCommentItem
              border-bottom: .01rem solid #d0d0d0
              height: .8rem
              text-align: left
              .hotImg
                width: .6rem
                height: .6rem
                margin-left: .1rem
                margin-top: .1rem
              .hotMess
                position: absolute
                margin-left: .15rem
                margin-top: .15rem
                width: 2.9rem
                .title
                  font-size: .14rem
                  color: #222222
                .mess
                  font-size: .10rem
                  color: #666565
                  margin-top: .3rem
                  .miter
                    right: .1rem
                    position: absolute
</style>



