<template>
  <div class="storeDetail">
    <div class="header border-1px">
      <span class="iconfont icon-fanhui"
            @click="goBack"></span>
      <h1 class="title">
        <slot>{{storeDetailData.name}}</slot>
      </h1>
      <span v-if="this.isCollect"
            class="iconfont icon-shoucang"
            @click="goCollect"></span>
      <span class="iconfont icon-shoucang2"
            @click="goCollect"
            v-else></span>
      <span class="iconfont icon-iconfontfenxiang"
            @click="goShare"></span>
    </div>
    <div class="store-pic-wrapper">
      <mt-swipe :auto="4000"
                class="swipe">
        <mt-swipe-item v-for="(item, index) in bannerList"
                       :key="index"
                       class="swipe-item">
          <img :src="item"
               width="100%">
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="storeMess">
      <div class="store-name">{{storeDetailData.name}}</div>
      <div class="store-mess">
        <span class="store-category">{{storeDetailData.category}}</span>
        <span class="sell-count">月销量：{{storeDetailData.sales_per_month}}</span>
      </div>
    </div>
    <div class="addressAndPhone">
      <p @click="goAddressMap()"><i class="iconfont icon-weizhi1"></i>地址: {{storeDetailData.address}}
        <i class="iconfont icon-jiantouyou"></i>
      </p>
      <p><i class="iconfont icon-lianxiwomen"></i>电话：{{storeDetailData.phone_number}}</p>
    </div>
    <div class="storeProduce"
         v-if="goodsList.length!=0">
      <div class="goodsTitle">
        <span>店内商品</span><span @click="goAllGoods()">全部商品</span>
      </div>
      <div class="hotComment">
        <ul class="hotCommentList">
          <li class="hotCommentItem"
              v-for="(item, index) in goodsList"
              v-if="index<=2"
              @click="goGoodsDetail(item.id)">
            <img class="hotImg"
                 :src="item.images[0]"
                 alt="热评">
            <span class="hotMess">
                                                                                                                                                                                                                                                                  <p
                                                                                                                                                                                                                                                                    class="title">{{item.title}}</p>
                                                                                                                                                                                                                                                                  <span
                                                                                                                                                                                                                                                                    class="price">￥{{item.purchase_info.price}}</span>
            <span class="mess">月销：{{item.sales_per_month}}</span>
            <p class="mess nameMess">{{item.category}}</p>
            </span>
          </li>
        </ul>
      </div>
    </div>
    <div class="storeDescription">
      <ul class="store-desc">
        <li class="store-desc-item">
          <div class="storeTitle">商家信息</div>
          <div class="rating-title"><span>营业时间</span><span>{{storeDetailData.opening_hours}}</span></div>
          <div class="rating-title"><span>预约信息</span><span>{{storeDetailData.appointment}}</span></div>
          <div class="rating-content">{{storeDetailData.description}}</div>
        </li>
      </ul>
    </div>
    <div class="goods-classify">
      <h1>我是商家，立即入住，提升门店收入 <span>免费入驻</span></h1>
      <i class="iconfont icon-jiantouyou"></i>
    </div>
    <div class="recommentStore"
         v-if="recommendList.length!=0">
      <p>推荐店铺</p>
      <div class="recommentStoreBox">
        <a href="javascript:scrollTo(0,0);"
           v-for="(item, index) in recommendList"
           :key="index"
           @click="goStoreDetail(item.id)">
          <img class="bgImg"
               :src="item.store_images[0]"
               alt="占位"/>
          <p>{{item.name}}</p>
        </a>
      </div>
    </div>
  </div>
</template>
<script>
  import app from 'common/js/app';
  import { Toast } from 'mint-ui';
  // import baseURL from '../../common/js/app.js';
  export default {
    data() {
      return {
        storeId: '',
        goodsList: [],
        bannerList: [],
        isCollect: false,
        recommendList: [],
        storeDetailData: {},
        deleteCollection: ''
      };
    },
    activated() {
      this.test();
      this.deleteCollection = '';
      this.isCollect = false;
      this.storeId = this.$route.query.storeId;
      this.getAlreadyCollection();
      this.getStoreDetails();
      this.getRcommendStore();
      this.getStoreGoods();
    },
    methods: {
      // 获取已经收藏
      getAlreadyCollection() {
        const self = this;
        this.$http.get(`${app.url.collectStore}`)
          .then(result => {
            result.results.map(item => {
              if (item.partner.id === parseInt(self.storeId)) {
                this.deleteCollection = item.url;
                self.isCollect = true;
                return;
              }
            });
          });
      },
      // －－－获取数据
      getStoreDetails() { // 店铺详情
        this.$http.get(`${app.url.storeDetail}${this.storeId}/`).then((res) => {
          this.storeDetailData = res;
          this.bannerList = res.store_images;
        });
      },
      getRcommendStore() { // 推荐店铺
        this.$http.get(`${app.url.recommendStore}${this.storeId}/recommended-partners/`).then((res) => {
          this.recommendList = res;
        });
      },
      getStoreGoods() {    // 店内商品
        this.$http.get(`${app.url.storeGoods}${this.storeId}/products/`).then((res) => {
          this.goodsList = res.results;
        });
      },
      // －－－跳转事件
      goAddressMap() {
        this.$router.push({name: 'position', query: {shopID: this.storeId}});
      },
      goAllGoods() {
        this.$router.push({name: 'goodsList', query: {shopID: this.storeId}});
      },
      goStoreDetail(storeID) { // 店铺详情
        this.storeId = storeID;
        this.$http.get(`${app.url.storeDetail}${this.storeId}/`).then((res) => {
          this.storeDetailData = res;
          this.bannerList = res.store_images;
        });
      },
      goGoodsDetail(goodsId) { // 商品详情
        this.$router.push({name: 'goodsDetail', query: {goodsId: goodsId}});
      },
      goBack() {
        this.$router.go(-1);
      },
      goCollect() {
        if (!this.isCollect) {
          let params = new FormData();
          params.append('partner', `${app.url.partners}${this.storeId}/`);
          this.$http.post(`${app.url.collectStore}`, params)
            .then((res) => {
              this.deleteCollection = res.url;
              this.isCollect = !this.isCollect;
            }, err => {
              Toast({
                message: '收藏失败!',
                position: 'bottom',
                duration: 1500
              });
              console.log(err);
            });
        } else {
          this.$http.delete(this.deleteCollection)
            .then(result => {
              this.isCollect = !this.isCollect;
            });
        }
      },
      goShare() {
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .storeDetail
    position: relative
    z-index: 300
    width: 100%
    height: 100%
    padding-bottom: 0.5rem
    background: #fff
    .header
      position: fixed
      text-align: center
      width: 100%
      height: 0.44rem
      line-height: 0.44rem
      background: #fff
      top: 0.0rem
      left: 0.0rem
      z-index: 6666
      .title
        width: 100%
        height: 100%
        text-align: center
        font-weight: 700
        font-size: .16rem
      .icon-fanhui, .icon-shoucang2, .icon-iconfontfenxiang, .icon-shoucang
        position: absolute
        padding: 0 0.1rem
        font-size: 0.22rem
        top: 0
      .icon-fanhui
        left: 0
      .icon-shoucang2, .icon-shoucang
        right: .4rem
      .icon-iconfontfenxiang
        right: .05rem
    .store-pic-wrapper
      width: 100%
      height: 2.35rem
      margin-top: .44rem
    .storeMess
      background: #fff
      border-bottom: .05rem solid #edebeb
      .store-name
        padding: 0.1rem 0 0.1rem 0
        text-align: left
        font-size: 0.16rem
        color: #222222
        margin-left: .1rem
      .store-mess
        height: 0.1rem
        text-align: left
        padding-bottom: .1rem
        margin-left: .1rem
        color: #969696
        font-size: .12rem
        .store-category
          margin-right: .25rem
    .storeProduce
      background: #fff
      .goodsTitle
        display: flex
        box-sizing: border-box
        margin-top: .1rem
        & > span
          width: 50%
          font-size: .12rem
          color: #969696
        & > :nth-child(1)
          text-align: left
          padding-left: .1rem
        & > :nth-child(2)
          text-align: right
          padding-right: .1rem
      .hotComment
        width: 100%
        height: auto
        border-bottom: .05rem solid #edebeb
        .hotCommentList
          .hotCommentItem
            border-bottom: .005rem solid #edebeb
            height: .75rem
            text-align: left
            .hotImg
              width: .8rem
              height: .55rem
              margin-left: .1rem
              margin-top: .1rem
            .hotMess
              position: absolute
              margin-left: .1rem
              margin-top: .11rem
              .title
                font-size: .14rem
                color: #222222
                margin-bottom: .05rem
                width: 2.7rem
                white-space: nowrap
                overflow: hidden
                text-overflow: ellipsis
              .price, .mess
                font-size: .12rem
                font-weight: 500
              .price
                color: #ff4242
                padding-top: .08rem
                padding-right: .1rem
              .mess
                color: #969696
              .nameMess
                margin-top: .05rem
    .addressAndPhone
      height: 1.0rem
      text-align: left
      font-size: 0.14rem
      color: #222222
      padding-left: 0.1rem
      background: #fff
      border-bottom: .05rem solid #edebeb
      & > p
        line-height: .5rem
        border-bottom: .01rem solid #F1F1F1
        white-space: nowrap
        overflow: hidden
        text-overflow: ellipsis
        padding-right: .5rem
        & > :nth-child(1)
          padding-right: .2rem
        & > :nth-child(2)
          position: absolute
          right: .1rem
    .storeDescription
      background-color: #fff
      .store-desc
        background: #fff
        .store-desc-item
          padding: 0.1rem
          text-align: left
          .storeTitle
            font-size: .12rem
            padding-bottom: .1rem
            color: #969696
          .rating-title, .rating-content
            font-size: 0.14rem
            color: #222222
          .rating-title
            line-height: .25rem
            height: .25rem
            & > :nth-child(1)
              color: #969696
              margin-right: .1rem
          .rating-content
            padding-top: 0.1rem
            line-height: .18rem
    .goods-classify
      position: relative
      height: 0.5rem
      line-height: 0.5rem
      font-size: 0.14rem
      text-align: left
      color: #222222
      padding-left: .2rem
      background: #fff
      border-top: .05rem solid #edebeb
      border-bottom: .05rem solid #edebeb
      & > h1
        & > span
          margin-left: .1rem
          background: #3FA6FC
          color: #fff
          padding: .0rem .1rem
      .icon-jiantouyou
        position: absolute
        right: 0.1rem
        bottom: 0.01rem
        font-size: 0.15rem
    .recommentStore
      width: 100%
      height: auto
      background: #fff
      & > p
        font-size: .12rem
        padding-top: .2rem
        text-align: left
        margin-left: .1rem
      .recommentStoreBox
        display: flex
        margin-top: .1rem
        margin-left: .08rem
        margin-right: .08rem
        box-sizing: border-box
        flex-wrap: wrap
        & > a
          box-sizing: border-box
          margin: .04rem
          width: 1.715rem
          height: 1.1rem
          position: relative
          .bgImg
            width: 100%
            height: 100%
          & > p
            color: #fff
            font-size: .12rem
            padding-left: .05rem
            text-align: left
            height: auto
            background: rgba(0, 0, 0, .3)
            position: absolute
            line-height: .18rem
            width: 1.667rem
            bottom: 0.0rem;
</style>



