<template>
  <div class='login'>
    <div class="login-header">
      <router-link to="registerNow">立即注册</router-link>
    </div>
    <div class='logo-wrapper'>
      <img src='./logo.png' alt='logo'>
    </div>
    <div class='username-wrapper'>
      <label class="username-lab">+86&nbsp;</label>
      <input class='username' type='number' placeholder='请输入手机号' v-model='username' @change.lazy='checkPhoneNum' maxlength='11'>
    </div>
    <div class='password-wrapper'>
      <label class="password-lab">密码</label>
      <input class='password' type='password' placeholder='请输入密码(6-32位字符)' v-model='password' @keyup.enter='login'>
    </div>
    <div class="search-password">
      <span>忘记密码?</span>
    </div>
    <div class='btn-login-wrapper'>
      <button class='btn-login' @click='login'>登录
      </button>
    </div>
    <div class='wechat-wrapper'>
      <img src='./wechat.png' alt='微信logo'>
      <span class='text'>微信登录</span>
    </div>
    <div class="back" @click="goBack">
      <i class="iconfont icon-fanhui1"></i>
    </div>
  </div>
</template>
<script>
import { checkPhoneNum, setStorage, getStorage } from 'common/js/util';
import baseURL from '../../common/js/app.js';
import { Toast } from 'mint-ui';

export default {
  data() {
    return {
      username: '',
      password: ''
    };
  },
  created() {
  },
  activated() {
    this.username = '';
    this.password = '';
  },
  methods: {
    /**
     * 登录
     */
    login() {
      if (!this.username) {
        Toast({
          message: '请输入用户名!',
          position: 'bottom',
          duration: 1500
        });
        return;
      } else {
        this.checkPhoneNum();
      }
    },
    checkPhoneNum() {
      let isPhone = checkPhoneNum(this.username);
      if (!isPhone) {
        //          this.username = '';
        Toast({
          message: '手机号不合法',
          position: 'bottom',
          duration: 1500
        });
      } else if (!this.password) {
        Toast({
          message: '请输入密码!',
          position: 'bottom',
          duration: 1500
        });
        return;
      } else {
        let params = new FormData();
        params.append('username', this.username);
        params.append('password', this.password);
        if (getStorage('wechatUserInfo')) {
          params.append('openid', getStorage('wechatUserInfo').openid);
        }
        this.$http.post(baseURL.url.login, params).then((res) => {
          this.$store.commit('setUser', { username: this.username, password: this.password });
          // 缓存用户信息
          setStorage('user', { username: this.username, password: this.password });
          const csrf = res[0]['csrf-tocken']['csrf_token'];
          window.document.cookie = `csrftoken=${csrf}`;
          this.$router.push({ name: 'mine', query: { username: this.username } });
        }, (err) => {
          Toast({
            message: err,
            position: 'bottom',
            duration: 1500
          });
        });
      }
    },
    /**
     * 微信登录
     */
    wechatLogin() {
      window.AV.User.signUpOrlogInWithAuthData({
        // 微博（weibo）用 uid
        // 微信（weixin）和 QQ（qq）用 openid
        'openid': 'oPrJ7uM5Y5oeypd0fyqQcKCaRv3o',
        'access_token': 'OezXcEiiBSKSxW0eoylIeNFI3H7HsmxM7dUj1dGRl2dXJOeIIwD4RTW7Iy2IfJePh6jj7OIs1GwzG1zPn7XY_xYdFYvISeusn4zfU06NiA1_yhzhjc408edspwRpuFSqtYk0rrfJAcZgGBWGRp7wmA',
        'expires_at': '2016-01-06T11:43:11.904Z'
      }, 'weixin').then((s) => {
        console.log('wechat login success');
      }, (e) => {
        console.log(JSON.stringify(e));
      });
    },
    /**
     * 返回按钮
     */
    goBack() {
      this.$router.push({ name: 'home' });
    }
  }
};
</script>
<style lang='stylus' rel='stylesheet/stylus'>
  @import '../../common/stylus/mixin.styl'
  .login
    overflow-y: hidden
    position: relative
    height: 100%
    font-size: 0
    z-index: 300
    background: #fff
    .login-header
      position: fixed
      right: 0.14rem
      top: 0.15rem
      font-size: 0.14rem
      a
        color: #53b3f1
    .logo-wrapper
      text-align: center
      padding: 0.3rem 0
      clear: both
      img
        width: 1.5rem
    .username-wrapper
      padding: 0.04rem 0.8rem 0.04rem 0.3rem
      border-top: 0.08rem solid #f6f6f6
      border-bottom: 0.01rem solid #f9f9f9
      font-size: 0
      .username-lab
        font-size: 0.14rem
        color: #000
        float: left
        line-height: 0.34rem
      .username
        height: 0.3rem
        outline: none
        font-size: 0.14rem
        background: #fff
        color: #000
    .password-wrapper
      border-bottom: 0.08rem solid #f6f6f6
      padding: 0.04rem 0.8rem 0.04rem 0.3rem
      .password-lab
        font-size: 0.14rem
        color: #000
        float: left
        line-height: 0.34rem
      .password
        outline: none
        font-size: 0.14rem
        background: #fff
        height: 0.3rem
        color: #000
    .search-password
      font-size: 0.1rem
      margin: 0.15rem .2rem
      color: #4bb8fe
      float: left
      .get-verify-code
        display: inline-block
        position: absolute
        top: 0
        right: 0
        height: 0.38rem
        width: 0.6rem
        line-height: 0.38rem
        font-size: 0.1rem
        color: #fff
        background: #a32397
        text-align: center
    .btn-login-wrapper
      position: relative
      height: 0.3rem
      text-align: center
      padding-top: 0.3rem
      .btn-login
        position: absolute
        width: 40%
        top: 0.4rem
        right: 33%
        height: 0.4rem
        line-height: 0.3rem
        margin: 0
        padding: 0
        background: #fff
        color: #4bb8fe
        border: 1px solid #4bb8fe
        border-radius: 0.02rem
    .wechat-wrapper
      position: absolute
      bottom: 0.6rem
      left: 1.625rem
      width: 0.5rem
      height: 0.8rem
      text-align: center
      img
        display: block
        width: 0.3rem
        height: 0.3rem
        padding: 0 0.1rem
        font-size: 0.3rem
      .text
        display: block
        font-size: 0.1rem
        padding-top: 0.1rem
        color: #000
    .back
      position: fixed
      top: 0.12rem
      left: 0.1rem
      width: 0.25rem
      height: 0.25rem
      border-radius: 50%
      text-align: center
      .icon-fanhui1
        display: inline-block
        width: 0.25rem
        height: 0.2rem
        font-size: 0.25rem
        color: rgba(0, 0, 0, 0.5)
</style>



