<template>
  <div class="home">
    <mt-loadmore class="pull-refresh"
                 :top-method="loadTop"
                 :bottom-method="loadBottom"
                 :bottom-all-loaded="allLoaded"
                 topPullText="↓"
                 topDropText="↑"
                 bottomPullText="上拉加载"
                 bottomDropText="释放加载"
                 ref="loadmore">
      <div class="swipe-wrapper">
        <mt-swipe :auto="3000"
                  :continuous="true">
          <mt-swipe-item v-for="banner in bannerList"
                         :key="banner.id">
            <a :href="banner.url">
              <img :src="banner.image"
                   alt="广告图">
            </a>
          </mt-swipe-item>
        </mt-swipe>
      </div>
      <div class="module-wrapper">
        <ul class="module-list ">
          <li class="module-item">
            <a href="#/lzShopHome">
              <i class="icon-shop"></i>
              <span class="module-name">联众商城</span>
            </a>
          </li>
          <li class="module-item">
            <a href="#/lzHomeHome">
              <i class="icon-at-home"></i>
              <span class="module-name">联众到家</span>
            </a>
          </li>
          <li class="module-item">
            <a href="">
              <i class="icon-happy-together"></i>
              <span class="module-name">快乐同行</span>
            </a>
          </li>
          <li class="module-item">
            <a href="">
              <i class="icon-rich-together"></i>
              <span class="module-name">财富同行</span>
            </a>
          </li>
        </ul>
      </div>
      <div class="ad-wrapper">
        <img src="./ad1.jpg" alt="广告占位图">
      </div>
      <div class="submodule-wrapper">
        <ul class="submodule-list">
          <li class="submodule-item" v-for="submodule in submoduleList" @click="goSearch(submodule.id)">
            <a>
              <img :src="submodule.modulePath"></img>
              <span class="module-name">{{submodule.moduleName}}</span>
            </a>
          </li>
        </ul>
      </div>
      <v-lz-message></v-lz-message>
      <div class="hot-recommend">
        <div class="recommend-item" v-for="recommend in recommendList">
          <div class="recommend-title border-1px">
            <img class="icon" :src="recommend.iconPath" alt=""><span>{{recommend.title}}</span>
          </div>
          <ul class="goods-list">
            <li class="goods-item border-1px" v-for="item in recommend.recommendGoodsList" @click="goGoodsDetail">
              <div class="left">
                <h1>{{item.name}}</h1>
                <h2 v-show="false">新片首应</h2>
              </div>
              <div class="right">
                <img :src="item.img" alt="商品图片">
              </div>
            </li>
          </ul>
        </div>
      </div>
      <v-recommend-list></v-recommend-list>
    </mt-loadmore>
    <v-search :searchClick="goSearch"></v-search>
  </div>
</template>
<script>
  //  import {Indicator} from 'mint-ui';
  import {getQueryString, setStorage} from 'common/js/util';
  import search from 'components/common/search/search';
  import recommendList from 'components/common/recommendList/recommendList';
  import lzMessage from 'components/common/lzMessage/lzMessage';
  import app from 'common/js/app';
  export default {
    data() {
      return {
        bannerList: [],
        allLoaded: false,
        moduleList: [], // 一级模块列表
        submoduleList: [
          {
            id: 19,
            moduleUrl: '#',
            moduleName: '上门技师',
            modulePath: require('./上门技师@2x.png')
          },
          {
            id: 18,
            moduleUrl: '#',
            moduleName: '上门家教',
            modulePath: require('./上门家教@2x.png')
          },
          {
            id: 20,
            moduleUrl: '#',
            moduleName: '上门医护',
            modulePath: require('./上门医护@2x.png')

          },
          {
            id: null,
            moduleUrl: '#',
            moduleName: '充值中心',
            modulePath: require('./充值中心@2x.png')
          },
          {
            id: null,
            moduleUrl: '#',
            moduleName: '健康生活',
            modulePath: require('./健康生活@2x.png')

          },
          {
            id: null,
            moduleUrl: '#',
            moduleName: '健康食品',
            modulePath: require('./健康食品@2x.png')

          },
          {
            id: null,
            moduleUrl: '#',
            moduleName: '酒水茶礼',
            modulePath: require('./酒水茶礼@2x.png')

          },
          {
            id: null,
            moduleUrl: '#',
            moduleName: '生活家电',
            modulePath: require('./生活家电@2x.png')

          }
        ], // 子模块列表
        recommendList: [
          {
            title: '健康商城',
            iconPath: require('./健康商城@2x.png'),
            recommendGoodsList: [
              {
                name: '精品好茶',
                img: require('./sc1@2x.png')
              },
              {
                name: '健康食品',
                img: require('./sc2@2x.png')
              },
              {
                name: '新鲜果蔬',
                img: require('./sc3@2x.png')
              },
              {
                name: '超值精选',
                img: require('./sc4@2x.png')
              }
            ]
          },
          {
            title: '健康到家',
            iconPath: require('./健康到家@2x.png'),
            recommendGoodsList: [
              {
                name: '一周精选',
                img: require('./dj1@2x.png')
              },
              {
                name: '帝王专区',
                img: require('./dj2@2x.png')
              },
              {
                name: '超值体验',
                img: require('./dj3@2x.png')
              },
              {
                name: '精品服务',
                img: require('./dj4@2x.png')
              }
            ]
          },
          {
            title: '同行之约',
            iconPath: require('./同行之约@2x.png'),
            recommendGoodsList: [
              {
                name: '和谐家庭',
                img: require('./tx1@2x.png')
              },
              {
                name: '户外活动',
                img: require('./tx2@2x.png')
              },
              {
                name: '一生好友',
                img: require('./tx3@2x.png')
              },
              {
                name: '精品影片',
                img: require('./tx4@2x.png')
              }
            ]
          },
          {
            title: '理财推荐',
            iconPath: require('./理财推荐@2x.png'),
            recommendGoodsList: [
              {
                name: '商务专享',
                img: require('./lc1@2x.png')
              },
              {
                name: '精心理财',
                img: require('./lc2@2x.png')
              },
              {
                name: '金融投资',
                img: require('./lc3@2x.png')
              },
              {
                name: '股市金融',
                img: require('./lc4@2x.png')
              }
            ]
          }
        ], // 推荐列表
        goodsList: [],
        pageSize: 10, // pagesize
        pageNum: 0,
        loading: true // 是否正在加载数据
      };
    },
    created() {
      this.init();
    },
    activated() {
      this.$root.eventHub.$emit('navTab', 0);
    },
    components: {
      'v-search': search,
      'v-recommend-list': recommendList,
      'v-lz-message': lzMessage
    },
    methods: {
      /**
       * 初始化
       */
      init() {
        this.getAdList();
        this.getWechatUserInfo();
      },
      /**
       * 获取广告图
       */
      getAdList() {
        this.$http.get(app.url.ads).then((res) => {
          this.bannerList = res.top;
        });
      },
      /**
       * 跳转搜索页面
       */
      goSearch(moduleId) {
        let params = {
          name: 'goodsSearch',
          query: {}
        };

        if (moduleId) {
          params.query.moduleId = moduleId;
        }

        this.$router.push(params);
      },
      /**
       * 跳转到商品详情
       */
      goGoodsDetail() {
        this.$router.push({name: 'goodsDetail'});
      },
      /**
       * 获取微信openId AccessToken及用户信息
       */
      getWechatUserInfo() {
        let params = {
          params: {
            code: getQueryString('code')
          }
        };
        this.$http.get(app.url.getWechatUserInfo, params).then((res) => {
          setStorage('wechatUserInfo', res);
        });
      },
      /**
       * 下拉刷新
       */
      loadTop() {
        this.pageNum = 0;
        this.goodsList = [];
        this.init();
      },
      loadBottom() {
        if (!this.loading) {
          this.getGoodsList();
        }
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .home
    width: 100%
