        overflow: hidden
        img
          width: 100%
          height: 1.55rem
      .module-wrapper
        .module-list
          width: 100%
          height: 1.07rem
          padding: 0.2rem 0
          line-height: 1.07rem
          overflow: hidden
          white-space: nowrap
          box-sizing: border-box
          background: #fff
          .module-item
            width: 25%
            height: 100%
            float: left
            text-align: center
            a
              display: flex
              flex-direction: column
              justify-content: space-between
              align-items: center
              height: 100%
              text-align: center
              .icon-shop
                display: block
                flex: 0 0 0.48rem
                vertical-align: top
                width: 0.48rem
                height: 0.48rem
                background-position: center
                background-size: 0.48rem 0.48rem
                bg-image('联众商城')
              .icon-at-home
                display: block
                flex: 0 0 0.48rem
                vertical-align: top
                width: 0.48rem
                height: 0.48rem
                background-position: center
                background-size: 0.48rem 0.48rem
                bg-image('联众到家')
              .icon-happy-together
                display: block
                flex: 0 0 0.48rem
                vertical-align: top
                width: 0.48rem
                height: 0.48rem
                background-position: center
                background-size: 0.48rem 0.48rem
                bg-image('快乐同行')
              .icon-rich-together
                display: block
                flex: 0 0 0.48rem
                vertical-align: top
                width: 0.48rem
                height: 0.48rem
                background-position: center
                background-size: 0.48rem 0.48rem
                bg-image('财富同行')
              .module-name
                display: block
                flex: 0 0 0.12rem
                height: 0.12rem
                padding-top: 0.06rem
                color: #3a3a3a
                font-size: 0.12rem
                line-height: 0.12rem
      .ad-wrapper
        height: 0.45rem
        margin: 0.07rem 0
        background: #fff
        padding: 0.1rem 0
        overflow: hidden
        img
          width: 100%
          height: 100%
      .submodule-wrapper
        .submodule-list
          display: flex
          height: 1.75rem
          flex-wrap: wrap
          align-content: space-between
          padding-top: 0.15rem
          background: #fff
          .submodule-item
            width: 25%
            a
              display: flex
              display: -webkit-flex
              height: 0.875rem
              flex-direction: column
              justify-content: space-between
              align-items: center
              box-sizing: border-box
              font-size: 0.12rem
              img
                display: block
                width: 0.45rem
                height: 0.45rem
              .module-name
                display: block
                padding-bottom: 0.15rem
      .hot-recommend
        background: #f5f5f5
        .recommend-item
          margin-top: 0.07rem
          background: #fff
          .recommend-title
            height: 0.32rem
            width: 100%
            line-height: 0.32rem
            font-size: 0.15rem
            color: #000
            border-1px(#f5f5f5)
            .icon
              display: inline-block
              width: 0.32rem
              height: 0.32rem
              padding: 0.06rem
              box-sizing: border-box
            span
              display: inline-block
              height: 0.32rem
              line-height: 0.32rem
              vertical-align: top
        .goods-list
          width: 100%
          height: 2.2rem
          overflow: hidden
          .goods-item
            width: 50%
            height: 1.1rem
            float: left
            border-1px(#f5f5f5)
            box-sizing: border-box
            &:nth-of-type(odd)
              border-right: 0.5px solid #f5f5f5
            .left
              float: left
              width: 50%
              height: 100%
              text-align: center
              padding: 0.12rem 0.14rem 0 0.1rem
              box-sizing: border-box
              h1
                font-size: 0.15rem
                color: #4c4c4c
              h2
                font-size: 0.09rem
                color: #1fc956
            .right
              float: left
              width: 50%
              height: 100%
              text-align: center
              box-sizing: border-box
              img
                width: 100%
                height: 100%
                padding: 0.15rem 0.2rem 0.15rem 0
                box-sizing: border-box
</style>



