<template>
  <!--<iframe src="http://192.168.31.177:3000/api/activity" frameborder="0">-->
  <!--</iframe>-->
  <div class='activityWhole' v-show="whole" ref='activityWhole'>
    <div class="activityHead" v-show="head"></div>
    <div class="activityBanner" v-show="banner"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      whole: false,
      head: false,
      banner: false,
      activityData: {}
    };
  },
  activated() {
    this.whole = false;
    this.$http.get('http://192.168.31.177:3000/api/activity')
      .then(result => {
        this.whole = true;
        this.activityData = result.success;
      });
  },
  updated() {
    console.log(this.$refs.activityWhole);
    let whole = document.createElement('div');
    whole.innerHTML = this.activityData;
    this.$refs.activityWhole.appendChild(whole);
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.activityWhole
  position: relative
  z-index: 300
  width: 100%
  height: 100%
  padding-bottom: 0
  background-color: #fff
  overflow-y: auto
</style>



