<template>
  <div class="mask-shopcart" @click="hideMask">
    <div class="mask-content" @click.stop>
      <div class="goods-price">
        <div class="img-wrap">
          <img :src="goodsDetail.goodsImgList?goodsDetail.goodsImgList[0]:''" alt="">
        </div>
        <p class="current-price">¥{{goodsDetail.goodsNowPrice}}</p>
        <p class="origin-price">价格:<span>¥{{goodsDetail.goodsOldPrice}}</span></p>
      </div>
      <div class="goods-model-wraper">
        <div class="goods-model" v-for="(spc,index) in resetSpecification" :key="spc.id">
          <h3>{{spc.spcName}}</h3>
          <div v-for="(item,index) in spc.spcOpitions" class="model" :class="{'active':item.selected===1}"
               @click="select(spc.spcOpitions, index)">
            {{item.spcOption}}


          </div>
        </div>
        <div class="goods-count">
          <h3>购买数量</h3>
          <div class="btn-group">
            <span class="iconfont icon-jianhao reduce" @click="reduce"></span>
            <input type="number" v-model="count">
            <span class="iconfont icon-jiahao plus" @click="plus"></span>
          </div>
        </div>
      </div>
      <div class="submit" @click="confirm(goodsDetail.GoodsSpc.objectId, count)">
        <p>确认</p>
      </div>
    </div>
  </div>
</template>
<script>
  import Vue from 'vue';
  export default {
    data() {
      return {
        count: 1
      };
    },
    props: {
      goodsDetail: {
        type: Object,
        default() {
          return {};
        }
      },
      confirm: {
        type: Function,
        default: () => {
        }
      },
      /**
       * 获取规格回调
       */
      getGoodsSpc: {
        type: Function,
        default: () => {
        }
      },
      hideMask: {
        type: Function,
        default: () => {
        }
      }
    },
    computed: {
      resetSpecification: function () {
        if (!this.goodsDetail.goodsSpecification) {
          return;
        }
        // 在商品规格中插入key = selected, value = 0 用于识别选中的规格
        return this.goodsDetail.goodsSpecification.map((item) => {
          let type = item.spcOpitions.map((spc, index) => {
            if (index === 0) {
              Vue.set(spc, 'selected', 1);
            } else {
              Vue.set(spc, 'selected', 0);
            }
            return spc;
          });
          item.type = type;
          return item;
        });
      }
    },
    methods: {
      plus() {
        this.count++;
      },
      reduce() {
        if (this.count === 1) {
          return;
        }
        this.count--;
      },
      select(spcOpitions, index) {
        for (let key in spcOpitions) {
          spcOpitions[key].selected = 0;
        }
        spcOpitions[index].selected = 1;
      }
//      confirm() {
//        let objId = this.goodsDetail.GoodsSpc.objectId;
//        let data = {
//          count: this.count,
//          objectId: objId
//        };
//        this.$router.push({name: 'orderSubmit', query: {goodsId:}})
//        this.show = false;
//      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../../common/stylus/mixin.styl"
  .mask-shopcart
    position fixed
    left 0
    bottom 0
    width 100%
    height 100%
    background rgba(0, 0, 0, 0.7)
    z-index 300
    .mask-content
      height 100%
      width 100%
      margin-top 40%
      background #fff
      .goods-price
        position relative
        width 100%
        height 1.07rem
        border-1px(#d0d0d0)
        .img-wrap
          position absolute
          bottom 0.1rem
          left 0.2rem
          width 1.17rem
          height 1.17rem
          border-radius 0.04rem
          overflow hidden
          img
            width 1.17rem
            height 1.17rem
        .current-price
          text-align left
          font-size 0.14rem
          color #ff4242
          padding-top 0.4rem
          margin-left 1.57rem
        .origin-price
          text-align left
          font-size 0.1rem
          color #d0d0d0
          margin 0.1rem 0 0 1.57rem
          span
            text-decoration line-through
      .goods-model-wraper
        max-height 3rem
        overflow-y auto
        .goods-model
          padding 0 0.1rem
          overflow hidden
          border-1px(#d0d0d0)
        h3
          text-align left
          font-size 0.13rem
          font-weight 700
          color #666565
          margin 0.15rem 0
        .model
          float left
          min-width 0.59rem
          max-width 1rem
          min-height 0.25rem
          text-align left
          line-height 0.25rem
          border-radius 0.03rem
          margin 0 0.4rem 0.1rem 0
          padding 0 0.15rem
          text-align center
          background-color #ebebed
          color #666565
          font-size 0.1rem
          word-wrap break-word
          &:nth-of-type(3n)
            margin-right 0
      .active
        background-color #ff4242 !important
        color #ffffff !important
      .goods-count
        padding 0 0.1rem
        overflow hidden
        h3
          float left
          text-align left
          font-size 0.13rem
          font-weight 700
          color #666565
          margin 0.15rem 0
        .btn-group
          float right
          margin 0.1rem 0.2rem 0 0
        .reduce, .plus
          display inline-block
          width 0.28rem
          height 0.23rem
          line-height 0.23rem
          text-align center
          background-color #eeeeee
          font-size 0.12rem
        input
          display inline-block
          width 0.28rem
          height 0.23rem
          text-align center
          background-color #eeeeee
          vertical-align top
          margin 0 0.01rem
          font-size 0.16rem
          color #000
      .submit
        width 100%
        height 0.45rem
        background-color #ff4242
        color #ffffff
        line-height 0.45rem
        position fixed
        bottom 0
        font-size 0.15rem

</style>



