<template>
  <div class="goods-search-header border-1px">
    <div class="search">
      <div @click="popupVisible = !popupVisible" class="select-wrapper">
        <span>{{currentOption}}</span>
        <span class="iconfont icon-sanjiao"></span>
      </div>
      <input type="text" v-model="keyword" @keyup.enter="search(keyword)" placeholder="搜店铺、商品和服务">
    </div>
    <div class="cancle" @click="cancle">取消</div>
    <mt-popup v-model="popupVisible" popup-transition="popup-fade" class="mint-popup-1">
      <ul>
        <li v-for="(option,index) in options" key="optaion.id" class="border-1px">
          <span v-if="index === 0" class="iconfont icon-gouwudai"></span>
          <span v-if="index === 1" class="iconfont icon-shouye"></span>
          <span>{{option}}</span>
        </li>
      </ul>
    </mt-popup>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        popupVisible: false,
        keyword: '',
        currentOption: '商品',
        options: ['商品', '店铺']
      };
    },
    props: {
      'search': {
        type: Function,
        default: () => {}
      }
    },
    methods: {
      /**
       * 取消搜索
       */
      cancle() {
        this.$router.go(-1);
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "../../../common/stylus/mixin.styl"
  .goods-search-header
    display: flex
    width: 100%
    height: 0.44rem
    line-height: 0.44rem
    background: #fff
    border-1px(gray)
    .search
      display: flex
      flex: 1
      margin: 0.06rem 0.1rem 0.06rem 0.2rem
      border-radius: 0.2rem
      background: #f5f5f5
      .select-wrapper
        display: flex
        height: 0.32rem
        line-height: 0.32rem
        justify-content: center
        font-size: 0.12rem
        padding: 0 0.1rem
        .icon-sanjiao
          font-size: 0.1rem
      input
        display: block
        flex: 1
        font-size: 0.12rem
        border-radius: 0.2rem
        background: #f5f5f5
    .cancle
      flex: 0 0 0.6rem
      font-size: 0.12rem
      align-items: center
      color: rgb(52, 114, 255)
    .mint-popup-1
      position: absolute
      top: 0.4rem
      left: 0.5rem
      width: 0.8rem
      border-radius: 8px
      padding: 10px
      transform: translate(-50%, 0)
      background: #000 !important
      color: #fff
      padding: 0
      &::before
        border-left: 0.1rem solid transparent
        border-right: 0.1rem solid transparent
        border-bottom: 0.1rem solid #000
        content: ''
        position: absolute
        top: -0.1rem
        left: 0.35rem
      li
        height: 0.4rem
        border-1px(#fff)
        box-sizing: border-box
    .v-modal
      background: #fff !important
</style>



