<template>
  <div class="add-receiver-address">
    <ul>
      <li border-1px>
        <span class="receiver-name">收货人</span>
        <input type="text" v-model="receiverName">
      </li>
      <li border-1px>
        <span class="receiver-phone">联系方式</span>
        <input type="text" v-model="receiverTel">
      </li>
      <li border-1px @click="toggleAddressPicker">
        <span class="receiver-location-area">所在地区</span>
        <span class="select">
          <span v-show="showSelect">请选择</span>
          <span>{{address.addressCity}} {{address.addressProvince}}</span>
          <i class="icon-ctrl"></i>
        </span>
      </li>
      <li border-1px>
        <span class="receiver-detail-address">详细地址</span>
        <input type="text" v-model="addressDeatil">
      </li>
    </ul>
    <footer>
      <v-border-button :click="saveAddress">保存地址</v-border-button>
    </footer>
    <v-address-picker v-show="pickerShow" :getAddress="getAddressInfo" :cancle="cancle"
                      :confirm="confirm"></v-address-picker>
  </div>
</template>
<script>
  import {Toast} from 'mint-ui';
  import borderButton from 'components/common/borderButton/borderButton';
  import addressPicker from 'components/common/addressPicker/addressPicker';

  export default {
    data() {
      return {
        receiverName: '',
        receiverTel: '',
        addressDeatil: '',
        address: {
          addressProvince: '',
          addressCity: ''
        },
        pickerShow: false,
        showSelect: true
      };
    },
    components: {
      'v-border-button': borderButton,
      'v-address-picker': addressPicker
    },
    methods: {
      saveAddress() {
        if (!this.receiverName || !this.receiverTel || !this.addressDeatil || !this.address) {
          Toast({
            message: '请将收货地址填写完整',
            position: 'bottom',
            duration: 1500
          });
          return;
        }

        let receiveAddress = new window.AV.Object('ReceiveAddress');
        for (let key in this.$data) {
          if (key === 'pickerShow' || key === 'showSelect') {
            continue;
          }
          receiveAddress.set(key, this.$data[key]);
        }

        const acl = new window.AV.ACL();
        acl.setPublicReadAccess(false);
        acl.setPublicWriteAccess(false);
        acl.setReadAccess(window.AV.User.current(), true);
        acl.setWriteAccess(window.AV.User.current(), true);

        receiveAddress.setACL(acl);

        receiveAddress.save().then((res) => {
          Toast({
            message: '保存成功',
            position: 'bottom',
            duration: 1500
          });
        }, (e) => {
          console.log(JSON.stringify(e));
        });
      },
      getAddressInfo(data) {
        this.address = data;
        this.showSelect = false;
      },
      toggleAddressPicker() {
        this.pickerShow = !this.pickerShow;
        console.log(this.pickerShow);
      },
      cancle() {
        this.address.addressProvince = '';
        this.address.addressCity = '';
        this.pickerShow = false;
      },
      confirm() {
        this.pickerShow = false;
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .add-receiver-address
    position: relative
    top: 0
    left: 0
    height: 100%
    z-index: 300
    background: #fff
    li
      height: 0.4rem
      line-height: 0.4rem
      border-1px(#f2f2f2)
      input
        float: left
        width: 80%
        height: 0.4rem
      .receiver-name
        display: block
        float: left
        width: 20%
        height: 0.4rem
      .receiver-phone
        display: block
        float: left
        width: 20%
        height: 0.4rem
      .receiver-location-area
        display: block
        float: left
        width: 20%
        height: 0.4rem
      .select
        display: block
        float: right
        text-align: right
        padding-right: 0.1rem
        box-sizing: border-box
        i
          display: inline-block
          transform: rotate(90deg)
      .receiver-detail-address
        display: block
        float: left
        width: 20%
        height: 0.4rem
    footer
      position: absolute
      width: 100%
      left: 0
      bottom: 60%
</style>



