<template>
  <div class="mine">
    <div class="mine-personal">
      <div class="minPersonalDetails">
        <div class="details" @click="goModifyInformation">
          <img :src="userdDetails.avatar_url" alt="">
          <div>{{userdDetails.name}}</div>
        </div>
        <div class="mineProduct">
          <div class="balance">
            <p>通行卡余额</p>
            <p>¥&nbsp;{{cardDetails.balance}}</p>
          </div>
          <div class="recharge" @click="goMember">
            <div>充值</div>
          </div>
        </div>
        <span class="iconfont icon-iconfontshezhi" @click="goSetUp"></span>
      </div>
    </div>
    <div class="mine-list">
      <div class="me">
        <div @click="goNews">
          <span class="iconfont icon-xiaoxi me-msg"></span> 我的消息
          <span class="iconfont icon-youjiantou"></span>
        </div>
        <div @click="goOrder">
          <span class="iconfont icon-tubiao4 me-shopcart"></span> 我的订单
          <span class="iconfont icon-youjiantou"></span>
        </div>
        <div @click="goCollection">
          <span class="iconfont icon-shoucang2 me-collection"></span> 我的收藏
          <span class="iconfont icon-youjiantou"></span>
        </div>
        <div @click="goRemark">
          <span class="iconfont icon-dianping me-remark"></span> 我的点评
          <span class="iconfont icon-youjiantou"></span>
        </div>
      </div>
      <div class="address" @click="goAddress">
        <span class="iconfont icon-weizhi me-address"></span> 地址管理
        <span class="iconfont icon-youjiantou"></span>
      </div>
      <div class="shops" @click="goShops">
        <span class="iconfont icon-cf-c14 me-shops"></span> 我是商家
        <span class="iconfont icon-youjiantou"></span>
      </div>
      <div class="contact">
        <div @click="goContact">
          <span class="iconfont icon-lianxiwomen me-contact"></span> 联系客服
          <span class="iconfont icon-youjiantou"></span>
        </div>
        <div @click="goAbout">
          <span class="iconfont icon-guanyuwomen me-about"></span> 关于我们
          <span class="iconfont icon-youjiantou"></span>
        </div>
      </div>
    </div>
  </div>
  </div>
</template>
<script>
import baseURL from '../../common/js/app.js';
import { Toast } from 'mint-ui';
export default {
  data() {
    return {
      csrf: '',
      userName: '未登录',
      userImg: '',
      csrfValue: '',
      userdDetails: {},
      cardDetails: {}
    };
  },
  activated() {
    this.getCookies(this.isLogIn);
    this.isLogIn();
  },
  methods: {
    isLogIn() {
      if (this.csrf) {
        this.getUsers();
        this.getAccount();
      } else {
        this.$router.push({ name: 'login' });
      }
    },
    getCookies(callback) {
      const cookieList = window.document.cookie.split(';');
      cookieList.map((item, index) => {
        item = item.trim();
        if (item.split('=').includes('csrftoken')) {
          this.csrf = item;
          this.csrfValue = item.split('=')[1];
        }
      });
      callback();
    },
    getUsers() {
      const url = `${baseURL.url.profile}`;
      this.$http.get(url)
        .then(res => {
          this.userdDetails = res;
          this.userName = res.name;
        });
    },
    getAccount() {
      this.$http.get(baseURL.url.account)
        .then(res => {
          this.cardDetails = res;
        });
    },
    // 去登录
    goLogin() {
      this.$router.push({ name: 'login' });
    },
    // 点击设置
    goSetUp() {
      this.$router.push({ name: 'setUp' });
    },
    // 修改头像
    goModifyInformation() {
      this.$router.push({ name: 'modifyInformation' });
    },
    // 点击充值
    goMember() {
      this.$router.push({ name: 'memberCard', query: { balance: this.cardDetails.balance, cardNum: this.cardDetails.card_number, username: this.userdDetails.name } });
    },
    // 点击保险
    goInsurance() {
      Toast({
        message: '暂无保险!',
        position: 'center',
        duration: 1500
      });
    },
    // 点击收藏
    goCollection() {
      this.$router.push({ name: 'collect' });
    },
    // 我的消息
    goNews() {
      alert('消息');
    },
    // 我的购物车
    goOrder() {
      this.$router.push({ name: 'order' });
    },
    // 我的点评
    goRemark() {
      Toast({
        message: '暂无点评!',
        position: 'bottom',
        duration: 1500
      });
    },
    // 地址管理
    goAddress() {
      this.$router.push({ name: 'addManage' });
    },
    // 我是商家
    goShops() {
      this.$router.push({ name: 'merchantCenter' });
      Toast({
        message: '请联系客服!',
        position: 'bottom',
        duration: 1500
      });
    },
    // 联系客服
    goContact() {
      this.$router.push({ name: 'contactCustomerService' });
    },
    // 关于我们
    goAbout() {
      this.$router.push({ name: 'aboutUs' });
    }
  }
};
</script>
<style lang="stylus" rel="stylesheet/stylus">
@import '../../common/stylus/mixin.styl'
  .mine
    height: 100%
    background-color: #f6f6f6
    overflow-y: auto
    .minPersonalDetails
      height: 2.16rem
      &>.details
        background-color: #de3745
        height: 1.25rem
        box-sizing: border-box
        padding: .4rem .4rem .2rem
        display: flex
        &>:nth-child(1)
          width: .6rem
          height: .6rem
          box-sizing: border-box
          border-radius: 50%
          overflow: hidden
        &>:nth-child(2)
          flex: 1
          font-size: .18rem
          color: #fff
          font-weight: bold
          text-align: left
          padding-left: .2rem
          line-height: .6rem
      &>.mineProduct
        height: .91rem
        box-sizing: border-box
        padding: .05rem 0
        background-color: #edebeb
        display: flex
        &>div
          flex: 1
        &>.balance
          box-sizing: border-box
          border-right: .025rem solid #edebeb
          background-color: #fff
          &>p
            height: .405rem
            color: #666565
            font-size: .14rem
          &>:nth-child(1)
            line-height: .51rem
          &>:nth-child(2)
            line-height: .3rem
        &>.recharge
          box-sizing: border-box
          border-left: .025rem solid #edebeb
          &>div
            background-color: #ff4242
            width: 1rem
            height: .4rem
            border-radius: .03rem
            font-size: .16rem
            font-weight: bold
            color: #fff
            line-height: .4rem
            margin: auto auto
      &>span
        position: absolute
        top: .1rem
        right: .1rem
        color: #fff
        font-size: .2rem
    .mine-list
      border-top: .05rem solid #f6f6f6
      &>div
        background-color: #fff
        margin-bottom: .05rem
      .me
        box-sizing: border-box
        padding: 0 .2rem
        &>div
          box-sizing: border-box
          height: .46rem
          font-size: .12rem
          color: #000
          line-height: .12rem
          padding: .17rem 0 .17rem .38rem
          text-align: left
          position: relative
          &>span
            font-size: .12rem
          &>:nth-child(1)
            position: absolute
            top: .17rem
            left: .1rem
          &>:nth-child(2)
            color: #c4c4c7
            float: right
          .me-msg
            color: #e44f81
          .me-shopcart
            color: #394458
          .me-collection
            color: #ff4242
          .me-remark
            color: #fde34b
        &>:nth-child(n+2)
          border-top: 1px solid #eaeaea
      .address
          box-sizing: border-box
          height: .46rem
          font-size: .12rem
          color: #000
          line-height: .12rem
          padding: .17rem .2rem .17rem .58rem
          text-align: left
          position: relative
          &>span
            font-size: .12rem
          .me-address
            color: #bf91ba
          &>:nth-child(1)
            position: absolute
            top: .17rem
            left: .3rem
          &>:nth-child(2)
            color: #c4c4c7
            float: right
      .shops
          box-sizing: border-box
          height: .46rem
          font-size: .12rem
          color: #000
          line-height: .12rem
          padding: .17rem .2rem .17rem .58rem
          text-align: left
          position: relative
          &>span
            font-size: .12rem
          .me-shops
            color: #9bdec8
          &>:nth-child(1)
            position: absolute
            top: .17rem
            left: .3rem
          &>:nth-child(2)
            color: #c4c4c7
            float: right
      .contact
        box-sizing: border-box
        padding: 0 .2rem
        position: relative
        &>div
          box-sizing: border-box
          height: .46rem
          font-size: .12rem
          color: #000
          line-height: .12rem
          padding: .17rem 0 .17rem .38rem
          text-align: left
          position: relative
          .me-contact
            color: #55caff
          .me-about
            color: #a1a1a1
          &>span
            font-size: .12rem
          &>:nth-child(1)
            position: absolute
            top: .17rem
            left: .1rem
          &>:nth-child(2)
            color: #c4c4c7
            float: right
        &>:nth-child(n+2)
          border-top: 1px solid #eaeaea
</style>



