<template>
  <div class="recharge">
    <backBar></backBar>
    <div class="header">
      <div class="info">
        <p class="name">{{userDetails.name}}</p>
        <p class="price">余额:&nbsp;<span>{{balance}}</span>元</p>
      </div>
      <img class="photo" :src="userDetails.avatar_url" alt="" />
    </div>
    <div class="rechargeableCard">
      <p class="title">储值卡</p>
      <div class="rechargeList">
        <div class="rechargeItem" :class="isSelectReItem[isSelectMap[index]]" v-for="(item, index) in rechargeList" @click="selectReItem(item, index)">
          ¥&nbsp;{{item.real}}
        </div>
        <div class="rechargeItem" @click="otherAmountHandle">其他金额</div>
      </div>
      <div class="determineRecharge" @click="determineRecharge">
        确认充值
      </div>
    </div>
    <div class="paymentDetails" v-show="isMarkShow">
      <div class="paymentSetion">
        <div class="paymentTitle">
          <span class="cancle" @click="cancelHandle">X</span> 付款详情
        </div>
        <div class="transactionDetails">
          <div>
            会员账号
            <span>{{userDetails.card_number}}</span>
          </div>
          <div class="paymentAmount">
            支付金额
            <span>¥&nbsp;{{price}}</span>
          </div>
        </div>
        <div class="paymentMethod">
          <div class="weChat" @click="weChatPay">
            <span class="iconfont icon-weixin"></span> 微信支付
            <span class="iconfont icon-duihao3" :class="isSelectPayType[isPayMap[0]]"></span>
          </div>
          <div class="alipay" v-show="isShowPay" @click="alipayPay">
            <span class="iconfont icon-zhifubao"></span> 支付宝支付
            <span class="iconfont icon-duihao3" :class="isSelectPayType[isPayMap[1]]"></span>
          </div>
          <div class="msPay" v-show="isShowPay" @click="msPayPay">
            <span class="iconfont icon-minsheng"></span> 民生付
            <span class="iconfont icon-duihao3" :class="isSelectPayType[isPayMap[2]]"></span>
          </div>
        </div>
        <div class="goPayment" @click="goPayment">
          确定支付
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import baseURL from '../../common/js/app.js';
import { Toast } from 'mint-ui';
import backBar from '../common/backBar/backBar';
export default {
  data() {
    return {
      price: 0,
      rechargeList: [],
      balance: 0,
      isMarkShow: false,
      channel: '',
      userDetails: {},
      card: 0,
      weChatType: '',
      isSelectMap: [0, 0, 0, 0, 0, 0, 0, 0],
      isPayMap: [0, 0, 0],
      isShowPay: false,
      isSelectReItem: [
        'selectOff',
        'selectOn'
      ],
      isSelectPayType: [
        'payTypeOff',
        'payTypeOn'
      ]
    };
  },
  components: {
    'backBar': backBar
  },
  activated() {
    // 获取当前设备,选择付款详情
    switch (window.buildType) {
      case 'wx':
        this.weChatType = 'wx_pub';
        break;
      case 'web':
        this.weChatType = 'wx';
        break;
      case 'app':
        this.weChatType = 'wx';
        this.isShowPay = true;
        break;
      default:
        return;
    }
    this.balance = this.$route.query.balance;
    this.getUserDetails();
    this.rechargeListUrl();
    console.log(window.buildType);
  },
  methods: {
    getUserDetails() {
      this.$http.get(baseURL.url.profile)
        .then(result => {
          this.userDetails = result;
        });
    },
    determineRecharge() {
      if (!(this.isSelectMap).includes(1)) {
        Toast({
          message: '请选择金额!',
          position: 'center',
          duration: 1500
        });
        return;
      }
      this.isMarkShow = true;
    },
    cancelHandle() {
      this.isMarkShow = false;
    },
    weChatPay() {
      this.isPayMap = [1, 0, 0];
      // this.channel = this.weChatType;
      // Toast({
      //   message: '请使用支付宝支付!',
      //   position: 'center',
      //   duration: 1500
      // });
    },
    alipayPay() {
      this.isPayMap = [0, 1, 0];
      this.channel = 'alipay_wap';
    },
    msPayPay() {
      this.isPayMap = [0, 0, 1];
      // Toast({
      //   message: '请使用支付宝支付!',
      //   position: 'center',
      //   duration: 1500
      // });
    },
    otherAmountHandle() {
      Toast({
        message: '暂不支持!',
        position: 'center',
        duration: 1500
      });
    },
    selectReItem(item, index) {
      this.isSelectMap = [0, 0, 0, 0, 0, 0, 0, 0];
      this.isSelectMap[index] = 1;
      this.price = item.real;
      this.card = item.id;
    },
    goPayment() {
      if (!(this.isPayMap).includes(1)) {
        Toast({
          message: '请选择付款方式!',
          position: 'center',
          duration: 1500
        });
        return;
      }
      Toast({
        message: '暂不支持!',
        position: 'center',
        duration: 1500
      });
      // const url = `${baseURL.url.topUp}?channel=${this.weChatType}&card=${this.card}`;
      // this.$http.get(url)
      //   .then(result => {
      //     window.pingpp.createPayment(result, (data, err) => {
      //       console.log(data);
      //       console.log(err.msg);
      //       console.log(err.extra);
      //     });
      //   });

      // let params = {
      //   params: {
      //     channel: this.weChatType
      //   }
      // };
      // this.$http.get(this.payInfo['payment_url'], params).then((res) => {
      //   if (window.buildType === 'wx') {
      //     window.wx.chooseWXPay({
      //       timestamp: res.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
      //       nonceStr: res.nonceStr, // 支付签名随机串，不长于 32 位
      //       package: res.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
      //       signType: res.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
      //       paySign: res.paySign, // 支付签名
      //       success: (res) => {  // 支付成功后的回调函数
      //         this.$router.push({ name: 'memberCard' });
      //       }
      //     });
      //   } else if (window.buildType === 'app') {
      //     let params = {
      //       partnerid: '10000100', // merchant id
      //       prepayid: 'wx201411101639507cbf6ffd8b0779950874', // prepay id
      //       noncestr: '1add1a30ac87aa2db72f57a2375d8fec', // nonce
      //       timestamp: '1439531364', // timestamp
      //       sign: '0CB01533B8C1EF103065174F50BCA001' // signed string
      //     };
      //     window.Wechat.sendPaymentRequest(params, () => {
      //       this.$router.push({ name: 'memberCard' });
      //     }, (reason) => {
      //       alert(reason);
      //     });
      //   }
      // });
    },
    rechargeListUrl() {
      this.$http.get(baseURL.url.topUpCards)
        .then(result => {
          this.rechargeList = result.reverse();
        });
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.recharge
  position: relative
  z-index: 300
  background-color: #fff
  padding-bottom: 0
  height: 100%
  display: flex
  flex-direction: column
  .header
    box-sizing: border-box
    height: .75rem
    border-bottom .05rem solid #f5f5f5
    padding: .1rem .2rem
    text-align: left
    display: flex
    .info
      flex: 1
      color: #666565
      .name
        font-size: .16rem
        padding: .05rem 0
        font-weight: bold
      .price
        font-size: .13rem
        padding-top: .1rem
        font-weight: bold
    .photo
      width: .3rem
      height: .3rem
      margin-top: .1rem
      box-sizing: border-box
      border-radius: 50%
      border: 1px solid #f5f5f5
      overflow: hidden
  .rechargeableCard
    box-sizing: border-box
    padding: .1rem
    flex: 1
    &>.title
      float: left
      font-size: .12rem
      color: #666565
      font-weight: bold
      padding: .05rem .1rem
      clear: both
    .rechargeList
      clear: both
      box-sizing: border-box
      padding-top: .15rem
      height: 2.95rem
      .rechargeItem
        float: left
        box-sizing: border-box
        width: 1rem
        height: .6rem
        border: .01rem solid #9c4294
        border-radius: .02rem
        font-size: .15rem
        color: #9c4294
        line-height: .6rem
        margin-bottom: .1rem
        margin-left: .2rem
      .selectOff
        border: .02rem solid #9c4294
      .selectOn
        border: .02rem solid #ff4242
        color: #ff4242
      &>:nth-child(3n+1)
        margin-left: 0.08rem
    .determineRecharge
      margin: .2rem auto
      width: 3.35rem
      height: .4rem
      background-color: #9c4294
      line-height: .4rem
      font-size: .15rem
      color: #fff
      font-weight: bold
  .paymentDetails
    position: fixed
    z-index: 400
    top: 0
    left: 0
    width: 100%
    height: 100%
    background: rgba(0, 0, 0, .5)
    .paymentSetion
      position: fixed
      bottom: 0
      left: 0
      box-sizing: border-box
      padding: .1rem 0
      background-color: #fff
      width: 100%
      height: 3.85rem
      .paymentTitle
        font-size: .16rem
        color: #222
        font-weight: bold
        padding: .05rem .1rem
        .cancle
          float: left
          font-size: .14rem
          font-weight: bold
          line-height: .18rem
      .transactionDetails
        height: .8rem
        &>div
          height: .4rem
          line-height .4rem
          box-sizing: border-box
          padding: 0 .1rem
          border-bottom: .01rem solid #d0d0d0
          font-size: .14rem
          text-align: left
          color: #969696
          &>span
            float: right
            font-size: .1rem
            color: #969696
        .paymentAmount
          color: #ff4242
          font-size: .14rem
          font-weight: bold
          &>span
            color: #ff4242
            font-size: .14rem
            font-weight: bold
      .paymentMethod
        box-sizing: border-box
        border-top: .1rem solid #edebeb
        &>div
          height: .4rem
          text-align: left
          line-height: .4rem
          box-sizing: border-box
          padding: 0 .2rem 0 .5rem
          font-size: .14rem
          color: #222
          border-bottom: .01rem solid #d0d0d0
          position: relative
          &>:nth-child(1)
            position: absolute
            top: 0
            left: .2rem
          &>:nth-child(2)
            position: absolute
            top: .12rem
            right: .2rem
            font-size: 0
            box-sizing: border-box
            width: .15rem
            height: .15rem
            border-radius: 50%
            border: .01rem solid #d0d0d0
          .payTypeOn
            font-size: .15rem
            line-height: .15rem
            color: #ff4242
            border-color: #fff
          .payTypeOff
            font-size: 0
        .weChat
          &>:nth-child(1)
            color: #1cc91d
        .alipay
          &>:nth-child(1)
            color: #29a2e2
        .msPay
          &>:nth-child(1)
            color: #005cc4
      .goPayment
        margin: .4rem auto
        width: 2.4rem
        height: .4rem
        background-color: #ff4242
        line-height: .4rem
        font-size: .15rem
        font-weight: bold
        color: #fff
        border-radius: .06rem
</style>



