<template>
  <div class="goods-item" @click="goGoodsDetail(goods.product.id)">
    <div class="left">
      <img :src=" goods.product.images[0] " alt="商品图片">
    </div>
    <div class="right">
      <h1 class="goods-name">{{goods.product.title}}</h1>
      <h2 class="goods-price">¥ {{goods.product.purchase_info.price}}</h2>
      <div class="goods-info">
        <span>{{goods.categary}}</span>
        <span class="monthSale">月销：{{goods.product.sales_per_month}}</span>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: {
      goods: {
        type: Object,
        default: {}
      }
    },
    methods: {
      goGoodsDetail(goodsId) {
        this.$router.push({name: 'goodsDetail', query: {goodsId: goodsId}});
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  .goods-item
    display: flex
    height: 1.30rem
    background-color : #ffffff
    .left
      flex: 0 0 1.25rem
      height: 1.20rem
      overflow: hidden
      padding-top : .05rem
      img
        width: 100%
        height: 100%
    .right
      flex: 1
      text-align: left
      padding-left : .15rem
      padding-top : .15rem
      border-bottom : .01rem solid #d0d0d0
      position : relative
      .goods-name
        font-size: 0.14rem
        color: #313131
        line-height : .2rem
      .goods-price
        padding-top: 0.15rem
        color: #b14ea8
        font-size: 0.12rem
      .goods-info
        position : absolute
        bottom : .1rem
        color: #666565
        font-size: 0.12rem
        width : 2.26rem
        .monthSale
          float : right
          right : .1rem
</style>



