<template>
  <div class="search-wrapper">
    <div class="location" @click="locationClick">
      <span class="iconfont icon-weizhi"></span>
      <span class="city-name">{{cityName}}</span>
    </div>
    <div class="search" @click="searchClick()">
      <span class="iconfont icon-iconfontsousuo1"></span>
      <input type="text" placeholder="快速查找商品或服务">
    </div>
  </div>
</template>
<script>
  import {getLocation, JSONP} from 'common/js/util';
  import app from 'common/js/app';
  export default {
    data() {
      return {
        cityName: ''
      };
    },
    props: {
      searchClick: {
        type: Function,
        default: () => {
        }
      },
      locationClick: {
        type: Function,
        default: () => {
        }
      }
    },
    activated() {
      this.getCurrentCityName();
    },
    methods: {
      /**
       * 获取城市名
       */
      getCurrentCityName() {
        getLocation((res) => {
          let url = `${app.url.getLoactionDetail}&location=${res.latitude},${res.longitude}&callback=getCity`;
          JSONP(url, 'getCity', (res) => {
            // example:北京市 只取北京
            this.cityName = res.result.addressComponent.city.toString().substr(0, res.result.addressComponent.city.toString().length - 1);
          });
        });
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .search-wrapper
    position: fixed
    display: flex
    display: -webkit-flex
    width: 100%
    height: 0.24rem
    top: 0
    left: 0
    padding: 0.1rem 0
    z-index: 200
    .location
      flex: 0 0 0.4rem
      width: 0.4rem
      height: 0.24rem
      line-height: 0.24rem
      text-align: center
      font-size: 0
      color: #000
      .icon-weizhi
        display: block
        height: 0.14rem
        font-size: 0.14rem
      .city-name
        display: block
        height: 0.1rem
        font-size: 0.1rem
    .search
      float: left
      display: flex
      width: 100%
      flex: 1
      border: 1px solid gray
      border-radius: 0.3rem
      margin: 0 0.4rem 0 0
      box-sizing: border-box
      background: rgba(255, 255, 255, 0.9)
      .icon-iconfontsousuo1
        display: block
        flex: 0 0 0.4rem
        width: 0.4rem
        height: 0.24rem
        padding: 0.04rem 0
        font-size: 0.16rem
        color: #4d4d4d
        box-sizing: border-box
      input
        display: block
        flex: 1
        font-size: 0.12rem
        background: rgba(255, 255, 255, 0.9)
        border-radius: 0.3rem
        color: #4d4d4d
        &::-webkit-input-placeholder
          color: #424242
</style>



