<template>
  <div class="submitOrder">
    <v-header>提交订单</v-header>
    <div class="order-title">
      <div class="order-message">
        <span>预约人：</span><span class="order-name" @click="showDatePicker">{{username}}</span><span>{{phoneNum}}</span>
      </div>
    </div>
    <div class="order-title">
      <div class="order-message" @click="showDatePicker">
        <span>时间：</span><span class="order-name">2017-03-21 17:00</span>
      </div>
      <div class="service-Info">
        <div class="in-home" :class="serviceType===0?'active':''" @click="selectServiceType(0)">到家服务</div>
        <div class="in-store" :class="serviceType===1?'active':''" @click="selectServiceType(1)">到店服务</div>
        <div class="service-address">
          <label for="">服务地址:</label>
          <input type="text">
        </div>
      </div>
    </div>
    <div class="goods border-1px">
      <v-goods-item :goods="goodsDetail"></v-goods-item>
    </div>
    <div class="goods-detail-footer">
      <div class="pay-money">待支付¥{{goodsDetail.purchase_info.price ? goodsDetail.purchase_info.price : 0}}</div>
      <div class="submit-now" @click="submitOrder">提交订单</div>
    </div>
    <v-fade-in>
      <v-dialog-pay :close="hidePayDialog" :payInfo="payInfo" v-show="showPayDialog"></v-dialog-pay>
    </v-fade-in>
    <v-fade-in>
      <v-date-picker v-show="showPicker"></v-date-picker>
    </v-fade-in>
  </div>
</template>

<script>
  import {getStorage, getDate, getTime} from 'common/js/util';
  import app from 'common/js/app';
  import dialogPay from 'components/common/dialogPay/dialogPay';
  import datePicker from 'components/common/datePicker/datePicker';
  import fadeIn from 'components/common/fadeIn/fadeIn';
  import goodsItem from 'components/common/goodsItem/goodsItem';
  import header from 'components/common/header/header';
  export default {
    data() {
      return {
        username: '',
        phoneNum: '',
        goodsId: '',
        goodsDetail: {},
        showPayDialog: false,
        showPicker: false,
        payInfo: {},
        serviceType: 0 // 0: 到家 1：到店
      };
    },
    components: {
      'v-header': header,
      'v-dialog-pay': dialogPay,
      'v-date-picker': datePicker,
      'v-fade-in': fadeIn,
      'v-goods-item': goodsItem
    },
    activated() {
      this.goodsId = this.$route.query.goodsId;
      this.getUserInfo();
      this.getGoodsDetail();
    },
    methods: {
      showDatePicker() {
        this.showPicker = true;
      },
      hidePayDialog() {
        this.showPayDialog = false;
      },
      /**
       * 提交订单
       */
      submitOrder() {
        console.log(getDate(), getTime());
        let formData = new FormData();
        formData.append('product', `http://oscar-zhangzs.c9users.io:8081/api/products/${this.goodsId}/`);
        formData.append('date', getDate());
        formData.append('time', getTime());
        this.$http.post(app.url.servicePlaceOrder, formData).then((res) => {
          this.payInfo = res;
          this.showPayDialog = true;
        });
      },
      /**
       * 选择服务类型
       * @param type
       */
      selectServiceType(type) {
        console.log(type);
        this.serviceType = type;
      },
      /**
       * 获取用户信息
       */
      getUserInfo() {
        let username = getStorage('user').username;
        this.$http.get(`${app.url.users}${username}/`).then((res) => {
          this.username = res.name;
          this.phoneNum = username;
        });
      },
      /**
       * 获取商品详情
       */
      getGoodsDetail() {
        this.$http.get(`${app.url.products}${this.goodsId}/`).then((res) => {
          this.goodsDetail = res;
        });
      }
    }
  };
</script>

