  @import "../../common/stylus/mixin.styl"
  .submitOrder
    position: absolute
    top: 0
    left: 0
    z-index: 400
    width: 100%
    height: 100%
    padding: 0
    background: #fff
    .goods
      border-1px(#f2f2f2)
      border-top: 1px solid #f2f2f2
    .order-title
      width: 100%
      font-size: .16rem
      color: #19191A
      line-height: .4rem
      font-weight: 500
      text-align: left
