          & > label
            flex: 0 0 0.8rem
            width: 0.8rem
            text-align: center
          & > input
            flex: 1
            line-height: 0.3rem
            height: 0.3rem
            border-bottom: 1px solid #f2f2f2
        .in-home
          width: 0.7rem
          height: 0.3rem
          line-height: 0.3rem
          border-radius: 0.05rem
          border: 1px solid #f2f2f2
          text-align: center
          &.active
            color: #fff
            background: red
        .in-store
          width: 0.7rem
          height: 0.3rem
          line-height: 0.3rem
          border-radius: 0.05rem
          border: 1px solid #f2f2f2
          text-align: center
          &.active
            color: #fff
            background: red
    .chooseTime
      width: 100%
      height: auto
      .chooseTimeBox
        margin: .1rem .1rem
        border: 1px solid #E1E1E1
        .timeTitle
          font-size: .16rem
          margin: .1rem 0
