<template>
  <transition name="fade" appear>
    <slot></slot>
  </transition>
</template>
<script>
    export default {};
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .fade-enter-active, .fade-leave-active
    position: fixed
    left: 0
    top: 50%
    margin-top: 0
    opacity: 1
    transition: all 0.5s
  .fade-enter, .fade-leave-active
    position: fixed
    left: 0
    top: 100%
    opacity: 0
</style>



