<template>
  <div class="collection">
    <div class="collection-nav">
      <div @click='goodsHandle'
           :class="{active: isActive[0]}">商品</div>
      <div @click="serviceHandle"
           :class="{active: isActive[1]}">服务</div>
      <div @click='shopsHandle'
           :class="{active: isActive[2]}">店铺</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isActive: [true, false, false]
    };
  },
  methods: {
    goodsHandle() {
      this.isActive = [true, false, false];
    },
    serviceHandle() {
      this.isActive = [false, true, false];
    },
    shopsHandle() {
      this.isActive = [false, false, true];
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.collection
  position: relative
  z-index: 300
  height: 100%
  background-color: #fff
  padding: 0
  .collection-nav
    display: flex
    height: .4rem
    box-sizing: border-box
    padding: .05rem 0
    font-size: .16rem
    &>div
      flex: 1
      line-height: .3rem
      box-sizing: border-box
      border-bottom .02rem solid #d2d2d2
    &>:nth-child(n+2)
      border-left .01rem solid #d2d2d2
    &>.active
      color: #ff4242
      border-bottom-color: #ff4242
</style>



