/**
* 带边框BUTTON
*/
<template>
  <div class="border-button">
    <button @click="click($event)"><slot></slot></button>
  </div>
</template>
<script>
  export default {
    props: {
      /**
       * 点击回调
       */
      click: {
        type: Function
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .border-button
    button
      width: 41.6%
      height: 0.3rem
      line-height: 0.3rem
      border: 1px solid #9a9a9a
      border-radius: 0.02rem
      color: #000
      text-align: center
      background: #fff
</style>



