<template>
  <div class="lz-message">
    <img :src="img" alt="联众快讯">
    <ul ref="msg">
      <li v-for="item in msgList">{{item.msg}}</li>
    </ul>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        img: require('./icon_msg.png'),
        msgList: [{msg: '联众商城上线巨优惠!'}, {msg: '联众商城上线巨优惠22!'}]
      };
    },
    created() {
      let top = 0;
      setInterval(() => {
        top++;
        this.$refs.msg.scrollTop = top;
        if (top === 40) {
          top = 0;
        }
      }, 100);
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .lz-message
    display: flex
    height: 0.4rem
    padding: 0.1rem
    background: #fff
    img
      flex: 0 0 0.4rem
      height: 0.2rem
      padding: 0.1rem 0
    ul
      flex: 1
      height: 0.4rem
      line-height: 0.4rem
      text-align: left
      padding-left: 0.1rem
      font-size: 0.13rem
      overflow-y: scroll
      &::-webkit-scrollbar
        display: none
</style>



