<template>
  <div class="recommend-list">
    <h1 class="recommend-title">{{title}}</h1>
    <div class="recommend-item" v-for="goods in goodsList" @click="goGoodsDetail(goods.id)">
      <img :src="goods.images[0]" alt="商品图片">
      <h2 class="goods-name">{{goods.title}}</h2>
      <h2 class="goods-price">¥ {{goods.purchase_info.price}}</h2>
    </div>
  </div>
</template>
<script>
  import app from 'common/js/app';
  export default {
    data() {
      return {
        title: '',
        goodsList: []
      };
    },
    created() {
      this.getRecommendList();
    },
    methods: {
      /**
       * 获取推荐列表
       */
      getRecommendList() {
        let params = {
          params: {
            filter: 'home-top'
          }
        };
        this.$http.get(app.url.recommend, params).then((res) => {
          this.goodsList = res.products;
          this.title = res.link_text;
        });
      },
      /**
       * 跳转到商品详情
       * @param goodsId 商品ID
       */
      goGoodsDetail(goodsId) {
        this.$router.push({name: 'goodsDetail', query: {goodsId: goodsId}});
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  .recommend-list
    width: 100%
    .recommend-title
      height: 0.3rem
      width: 100%
      line-height: 0.3rem
      text-align; center
      font-size: 0.12rem
    .recommend-item
      float: left
      width: 50%
      min-height: 1.2rem
      box-sizing: border-box
      font-size: 0.12rem
      padding: 0.1rem 0
      color: #000
      & > img
        width: 100%
        height: 0.8rem
        overflow: hidden
      &:nth-child(even)
        padding-left: 0.1rem
        padding-right: 0.05rem
      &:nth-child(odd)
        padding-left: 0.05rem
        padding-right: 0.1rem
      .goods-name
        height: 0.15rem
        width: 1.5rem
        line-height: 0.15rem
        padding-bottom: 0.1rem
        text-align: left
        white-space: nowrap
        overflow: hidden
        text-overflow: ellipsis
      .goods-price
        height: 0.15rem
        line-height: 0.15rem
        text-align: left
</style>



