<template>
  <div class="merchantInformation">
    <div class="merchantInfor-header">
      <span class="iconfont icon-fanhui merchantInfor-head-left"
            @click="goBack"></span>
      <span class="merchantInfor-head-center">商户信息</span>
    </div>
    <div class="merchantInfor-store">
      <span class="iconfont icon-mi"></span>
      <label class="merchantInfor-store-lab">商户名称</label>
      <input type="text"
             placeholder="商户名称"
             class="merchantInfor-store-name">
    </div>
    <div class="merchantInfor-area">
      <span class="iconfont icon-mi"></span>
      <label class="merchantInfor-area-lab">省、市区</label>
      <input type="text"
             placeholder="省、市区"
             class="merchantInfor-area-name">
    </div>
    <div class="merchantInfor-address">
      <span class="iconfont icon-mi"></span>
      <label class="merchantInfor-address-lab">详细地址</label>
      <input type="text"
             placeholder="详细地址"
             class="merchantInfor-address-name">
    </div>
    <div class="merchantInfor-map">
      <span class="iconfont icon-mi"></span>
      <label class="merchantInfor-map-lab">地图选址</label>
      <input type="text"
             placeholder="地图选址"
             class="merchantInfor-map-name">
      <span class="iconfont icon-jiantouyou merchantInfor-map-you"></span>
    </div>
    <div class="merchantInfor-telephone">
      <span class="iconfont icon-mi"></span>
      <label class="merchantInfor-telephone-lab">门店电话</label>
      <input type="text"
             placeholder="门店内座机电话"
             class="merchantInfor-telephone-name">
      <span class="iconfont icon-jiantouyou merchantInfor-telephone-you"></span>
    </div>
    <div class="merchantInfor-industry">
      <span class="iconfont icon-mi"></span>
      <label class="merchantInfor-industry-lab">行业类别</label>
      <input type="text"
             placeholder="请选择行业"
             class="merchantInfor-industry-name">
      <span class="iconfont icon-jiantouyou merchantInfor-industry-you"
            @click="industryType"></span>
    </div>
    <div class="merchantInfor-shop">
      <span class="iconfont icon-mi"></span>
      <label class="merchantInfor-shop-lab">店铺介绍</label>
      <input type="text"
             placeholder="店铺介绍"
             class="merchantInfor-shop-name">
    </div>
    <div class="merchantInfor-footer">
      <h3>门店照片</h3>
      <p class="merchantInfor-sure">请确保店铺名称清晰可见</p>
      <p class="merchantInfor-attention">需实际拍摄，勿上传设计图，勿上传资质照片，请避</p>
      <p class="merchantInfor-attentions">免泄露商户信息</p>
      <div class="iconfont icon-zhaoxiangji merchantInfor-photo">
        <img class="merchantInforImg"
             :src="imgUrl"
             :alt="图片上传失败"
             ref="merchantInforImg">
        <input type="file"
               class="merchantInfor-file"
               @change="fileHandle">
      </div>
      <p class="merchantInfor-licence">商户名称需与门头照或营业执照保持一致</p>
      <p class="merchantInfor-correct">请务必保证门店电话准确、畅通，我们将拨打电话核实信息</p>
      <button @click="goCallBack"
              class="merchantInfor-btn">保存并下一步</button>
    </div>
  </div>
</template>

<script>
export default {
  data() { return { imgUrl: '' }; },
  activated() { },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    goCallBack() {
      this.$router.push({ name: 'mine' });
    },
    fileHandle(e) {
      const reader = new FileReader();
      const self = this;
      reader.readAsDataURL(e.target.files[0]);
      reader.onload = function (e) {
        self.$refs.merchantInforImg.src = this.result;
      };
    }
  }
};
</script>

