<template>
  <div class="setUp">
    <div class="logo"></div>
    <div class="setUp-list">
      <div class="setUp-function">
        <div>
          <span class="iconfont icon-baoxianzhushouicon27 invitation"></span> 邀请好友
          <span class="iconfont icon-youjiantou"></span>
          <span>让身边的朋友也能享受到健康的服务</span>
        </div>
        <div>
          <span class="iconfont icon-duihao4 encourage"></span> 给我们鼓励
          <span class="iconfont icon-youjiantou"></span>
          <span>更努力的为大众建康服务</span>
        </div>
      </div>
      <div class="setUp-info">
        <div>
          <span class="iconfont icon-lajitong clearCache"></span>清除缓存
          <span class="iconfont icon-youjiantou"></span>
        </div>
        <div>
          <span class="iconfont icon-banbenguanli versionNumber"></span>版本号
          <span>1.0.0</span>
        </div>
      </div>
    </div>
    <div class="signOut"
         @click='signOut'>退出当前账号</div>
    <backBar></backBar>
  </div>
</template>

<script>
import { removeStorage, delCookie } from '../../common/js/util.js';
import backBar from '../common/backBar/backBar';
export default {
  data() { return {}; },
  activated() { },
  components: {
    'backBar': backBar
  },
  methods: {
    signOut() {
      removeStorage('user');
      delCookie('csrftoken');
      this.$router.push({ name: 'login' });
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import '../../common/stylus/mixin.styl'
.setUp
  height: 100%
  background-color: #fff
  position: relative
  z-index: 300
  .logo
    height: 1.11rem
    background-image url('./tongxing_logo@2x.png')
    background-size: 1.75rem .55rem
    background-repeat: no-repeat
    background-position: 1rem .28rem
  .setUp-list
    border-bottom .1rem solid #f6f6f6
    .setUp-function
      box-sizing: border-box
      padding: 0 .1rem
      border-bottom .1rem solid #f6f6f6
      &>div
        height: .49rem
        box-sizing: border-box
        padding: .175rem 0
        padding-left: .55rem
        text-align: left
        font-size: .14rem
        position: relative
        .invitation
          color: #377dac
        .encourage
          color: #d2d730
        &>:nth-child(1)
          position: absolute
          top: .175rem
          left: .2rem
          font-size: .14rem
        &>:nth-child(n+2)
          float: right
        &>:nth-child(2)
          font-size: .14rem
          color: #c4c4c4
        &>:nth-child(3)
          font-size: .12rem
          line-height: .14rem
          color: #d7d7d7
          padding-right: .1rem
      &>:nth-child(n+2)
        box-sizing: border-box
        border-top: 1px solid #cecece
    .setUp-info
      box-sizing: border-box
      padding: 0 .1rem
      &>div
        height: .49rem
        box-sizing: border-box
        padding: .175rem 0
        padding-left: .55rem
        text-align: left
        font-size: .14rem
        position: relative
        .clearCache
          color: #a2a2a2
        .versionNumber
          color: #a2d0a7
        &>:nth-child(1)
          position: absolute
          top: .175rem
          left: .2rem
          font-size: .14rem
        &>:nth-child(2)
          float: right
          font-size: .14rem
          color: #c4c4c4
      &>:nth-child(n+2)
        box-sizing: border-box
        border-top: 1px solid #cecece
  .signOut
    position: absolute
    bottom: .3rem
    left: 1.09rem
    width: 1.66rem
    height: .43rem
    box-sizing: border-box
    border: 1px solid #8f8f8f
    line-height: .43rem
    color: #8f8f8f
    border-radius: .1rem
</style>



