  <div class="goods-detail">
    <div class="goods-pic-wrapper">
      <mt-swipe :auto="4000"
                class="swipe">
        <mt-swipe-item v-for="swipe in goodsDetail.images"
                       :key="swipe.id"
                       class="swipe-item">
          <img :src="swipe"
               width="100%">
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="goods-desc">
      <div class="goods-desc-header">
        <h1><span class="store-type" v-show="goodsDetail.goodsType===0">自营</span>{{goodsDetail.title}}</h1>
      </div>
      <div class="goods-price">
        <span class="goods-now-price"
              v-if="goodsDetail.purchase_info.price">￥{{goodsDetail.purchase_info.price}}.00</span>
        <span class="goods-old-price"
              v-if="goodsDetail.purchase_info.price_retail">￥{{goodsDetail.purchase_info.price_retail}}.00</span>
      </div>
      <div class="goods-desc-footer">
        <span class="express-free">快递:{{goodsDetail.expressPrice}}.00元</span>
        <span class="sell-count">销量:{{goodsDetail.sales_per_month}}</span>
        <span class="goods-address">{{goodsDetail.category}}</span>
      </div>
    </div>

    <!--优惠券-->
    <div class="goods-discount">
      <span class="text">优惠券:</span>
      <span class="discount-info">满1000减99</span>
      <i class="iconfont icon-jiantouyou"></i>
    </div>
    <!--项目分类-->
    <div class="goods-server">
      <span class="server-shrm server-item"> <img class="img-mark" src="./mark@2x.png"><span>送货入户</span></span>
      <span class="server-fqg server-item">  <img class="img-mark" src="./mark@2x.png"><span>分期购</span></span>
      <span class="server-yyaz server-item"> <img class="img-mark" src="./mark@2x.png"><span>预约安装</span></span>
      <span class="server-zpbz server-item"> <img class="img-mark" src="./mark@2x.png"><span>正品保证</span></span>
      <i class="iconfont icon-jiantouyou"></i>
    </div>

    <div class="goods-classify" @click="showMaskShopcart" v-show="false">
