      <i class="iconfont icon-jiantouyou"></i>
    </div>

    <div class="goods-ratings-header">
      <h1>评价（{{goodsReviews.length}}）</h1>
    </div>
    <div class="goods-ratings-wrapper">
      <ul class="goods-ratings" v-if="goodsReviews.length != 0">
        <li class="goods-rating-item">
          <div class="rating-title">
            <span>{{goodsReviews[0].reviewer_name}}</span>
            <span class="score-star">
              <i class="iconfont icon-xingxing" v-for="item in scoreStar"></i>
            </span>
          </div>
          <div class="rating-content">{{goodsReviews[0].title}}</div>
        </li>
      </ul>
      <div class="show-more-ratings">
        <span class="btn-more-ratings" @click="goReviews" v-if="goodsReviews.length >=1 ">查看全部评价</span>
        <span class="btn-more-ratings noRatings" v-else>暂无更多评价</span>
      </div>
    </div>
    <div class="merchant">
      <div class="goods-merchant">
        <div class="merchant-mess">
          <div class="merchant-icon"></div>
          <div class="merchant-title">
            <span class="merchant-name">{{goodsDetail.partner_info.name}}<span class="merchant-from">自营</span></span>
          </div>
          <p class="merchant-desc">经典香水出自乐客</p>
        </div>
        <div class="merchant-btn">
          <div class="contact-btn merch-button">
            <span class="button-item"><img class="icon-mark" src="./contact-icon@2x.png"><span
              class="title-btn"><a href="tel:18519335090">联系卖家</a></span></span>
          </div>
          <div class="enter-btn merch-button">
            <span class="button-item"><img class="icon-mark" src="./enter-icon@2x.png"><span
              class="title-btn" @click="goStoreDetail(goodsDetail.partner_info.id)">进入店铺</span></span>
          </div>
        </div>
      </div>
    </div>
