      <ul class="goods-desc-pics">
        <li v-for="goodsImg in goodsDetail.images">
          <img :src="goodsImg"
               alt="商品描述图片">
        </li>
      </ul>
    </div>
    <div class="goods-detail-footer">
      <div class="call-seller-wrapper" v-show="goodsDetail['product_class'] === '商品'">
        <span class="iconfont icon-xiaoxi"></span>
        <span class="text">联系卖家</span>
      </div>
      <div class="shopcart-wrapper" @click="goShopcart" v-show="goodsDetail['product_class'] === '商品'">
        <span class="iconfont icon-tubiao4"></span>
        <span class="text">购物车</span>
      </div>
      <div class="add-shopcart" @click="addGoodsToShopcart" v-show="goodsDetail['product_class'] === '商品'">加入购物车


      </div>
      <div class="buy-now" @click="buyNow">立即购买


      </div>
    </div>
    <v-back></v-back>
    <!--购物车遮罩-->
    <v-mask v-show="showMask" :confirm="confirm" :hideMask="hideMaskShopcart" :goodsDetail="goodsDetail"></v-mask>
  </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  import app from 'common/js/app';
  import backBar from 'components/common/backBar/backBar';
  import maskShopcart from 'components/common/maskShopcart/maskShopcart';

  export default {
    data() {
      return {
        goods: {},
        goodsId: '',
        goodsDetail: {purchase_info: {}, partner_info: {}}, // 商品详情
        goodsReviews: [],
        specifications: {}, // 商品规格
        showSpecifications: false,
        specOpationList: [], // 已选商品规格列表
        goodsCount: 0, // 购买商品数量
        showMask: false,
        scoreStar: []
      };
    },
    activated() {
      this.goodsId = this.$route.query.goodsId;
      this.getGoodsDetails();// 商品详情
      this.getReviews();// 商品评价
    },
    components: {
      'v-back': backBar,
      'v-mask': maskShopcart
    },
    methods: {
      /**
       * 获取商品详情
       */
      getGoodsDetails() {
        this.$http.get(`${app.url.products}${this.goodsId}/`).then((res) => {
          this.goodsDetail = res;
        });
      },
      /**
       * 获取评价信息
       */
      getReviews() {
        this.$http.get(`${app.url.products}${this.goodsId}/reviews/`).then((res) => {
          this.goodsReviews = res.results;
          if (this.goodsReviews.length !== 0) {
            this.scoreStar.length = this.goodsReviews[0].score;
          }
        });
      },
      /**
       * 查看全部评论
       */
      goReviews() {
        this.$router.push({name: 'commentList', query: {goodsId: this.goodsId}});
      },
      /**
       * 加入购物车
       */
      addGoodsToShopcart() {
        let params = {
          url: `http://lz.lzhealth.com.cn/api/products/${this.goodsId}/`,
          quantity: 1
        };
        this.$http.post(app.url.addToShopcart, params).then((res) => {
          Toast({
            message: ' 商品已加入购物车',
            position: 'bottom',
            duration: 1000
          });
        });
      },
      /**
       * 立即购买
       */
      buyNow() {
        if (this.goodsDetail['product_class'] === '商品') {
          this.$router.push({
            name: 'goodsOrderSubmit',
            query: {goodsList: [this.goodsDetail]}
          });
        } else if (this.goodsDetail['product_class'] === '服务') {
          this.$router.push({name: 'serviceOrderSubmit', query: {goodsId: this.$route.query.goodsId}});
        }
      },
      /**
       * 跳转到购物车
       */
      goShopcart() {
        this.$router.push({name: 'shopcart'});
      },
      showMaskShopcart() {
        this.showMask = true;
      },
      hideMaskShopcart() {
        this.showMask = false;
      },
      /**
       * 选择规格后确认
       * @param objectId
       * @param count 购买数量
       */
      confirm(objectId, count) {
        this.$router.push({
          name: 'orderSubmit',
          query: {goodsId: this.$route.query.goodsId, objectId: objectId, count: count}
        });
      },
      /**
       * 跳转到店铺详情
       * @param storeId
       */
      goStoreDetail(storeId) {
        this.$router.push({name: 'storeDetail', query: {storeId: this.goodsDetail['partner_info'].id}});
      }
    }
  };
</script>
