<template>
  <div class="register">
    <div class="logo-wrapper">
      <img src="./logo.png" alt="logo">
    </div>
    <h1 class="tips">请输入手机号</h1>
    <div class="phone-wrapper">
      <input class="phone-num" type="number" placeholder="请输入手机号" v-model="phoneNum" @change.lazy="checkPhoneNum" @keyup.enter="nextStep" maxlength="11">
    </div>
    <div class="next-step-wrapper">
      <button class="btn-next-step" @click="nextStep">下一步</button>
    </div>
  </div>
</template>
<script>
  import {checkPhoneNum} from 'common/js/util';
  import {Toast} from 'mint-ui';

  export default {
    data() {
      return {
          phoneNum: '' // 手机号
      };
    },
    created() {
//      console.log(this.$refs);
    },
    methods: {
      checkPhoneNum() {
          if (!this.phoneNum) return;
          let isPhoneNum = checkPhoneNum(this.phoneNum);
          if (!isPhoneNum) {
            Toast({
              message: '手机号不合法',
              position: 'bottom',
              duration: 1500
            });
            this.phoneNum = '';
            return;
          }

          this.sendVerifyCodeMsg();
      },
      /**
       * 发送注册验证码
       */
      sendVerifyCodeMsg() {
//        window.AV.Cloud.requestSmsCode({
//          mobilePhoneNumber: this.phoneNum,
//          name: '联众商城',
//          op: '注册',
//          ttl: 10
//        }).then((result) => {
//          Toast({
//            message: '验证码已发送到您手机',
//            position: 'bottom',
//            duration: 1500
//          });
//          this.$router.push({name: 'register', query: {phoneNum: this.phoneNum}});
//          // 发送成功
//        }, (errMsg) => {
//          Toast({
//            message: '验证码发送失败，请稍后再试',
//            position: 'bottom',
//            duration: 1500
//          });
//          return;
//          // 发送失败
//        });
      },
      /**
       * 下一步
       */
      nextStep() {
        if (!this.phoneNum) {
          Toast({
            message: '请输入手机号',
            position: 'bottom',
            duration: 1500
          });
          return;
        }

        this.checkPhoneNum();
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .register
    position: relative
    width: 100%
    height: 100%
    text-align: center
    background: #fff
    z-index: 300
    .logo-wrapper
      text-align: center
      padding: 0.4rem 0
      img
        width: 1.5rem
    .tips
      text-align: left
      font-size: 0.15rem
      color: #000
      padding: 0.1rem 0
      margin-left: 15%
    .phone-wrapper
      display: inline-block
      width: 70%
      height: 0.4rem
      border: 1px solid #a32397
      box-sizing: border-box
      padding: 0.12rem 0
      font-size: 0
      border-radius: 0.04rem
      text-align: left
      .phone-num
        display: inline-block
        width: 1rem
        font-size: 0.12rem
        margin: 0
        background: #fff
        color: #000
        padding-left: 0.15rem
    .next-step-wrapper
      height: 0.3rem
      text-align: center
      padding-top: 0.3rem
      .btn-next-step
        display: inline-block
        width: 70%
        height: 0.3rem
        line-height: 0.3rem
        margin: 0
        padding: 0
        background: #fff
        color: #a32397
        border: 1px solid #a32397
        border-radius: 0.02rem
</style>



