<template>
  <div class="classify">
    <header class="topHeader">
      <div class="search-wrapper">
        <div class="search" @click="goSearch">
          <span class="iconfont icon-iconfontsousuo1"></span>
          <input type="text" placeholder="快速查找店铺、商品和服务">
        </div>
        <div class="location">
          <span class="iconfont icon-xiaoxi"></span>
        </div>
      </div>
      <div class="classifyType">
        <div v-for="(item, index) in classifyTypes"
             :key="index"
             @click="sellectType(index)"
             :class="{checked:item.isSellect}">
          {{item.name}}

        </div>
      </div>
    </header>
    <div class="classify-body">
      <div class="classify-left">
        <ul class="classify-list">
          <li class="classify-item border-1px" :class="{active: active === index}" v-for="(item,index) in classifyList"
              @click="select(index)">{{item}}

          </li>
        </ul>
      </div>
      <div class="classify-right">
        <mt-loadmore class="pull-refresh"
                     :top-method="loadTop"
                     :bottom-method="loadBottom"
                     :bottom-all-loaded="allLoaded"
                     bottomPullText="上拉加载"
                     bottomDropText="释放加载"
                     ref="loadmore">
          <ul class="goods-all-ul">
            <li class="goods-classify-li" v-for="(items,index) in goodsList" @click="goGoodsDetail(790)">
              <p class="classify-title">{{items.classify}}</p>
              <div class="goods-classify-list">
                <div class="goods-classify-item" v-for="(item,index) in items.goods">
                  <img src="./goods.png" alt="商品图片"/>
                  <p class="good-name">{{item.name}}</p>
                </div>
              </div>
            </li>
          </ul>
        </mt-loadmore>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        classifyTypes: [
          {name: '商城', isSellect: true}, {name: '到家', isSellect: false}
        ],
        classifyList: [],
        classifyList1: ['女性用品', '男性用品', '老年用品', '母婴用品', '儿童用品', '联众药房', '养生器械', '健康食品'],
        classifyList2: ['选择家政', '上门家教', '上门医护', '上门技师', '中医馆', '丽人馆', '母婴馆', '养生馆'],
        goodsList: [
          {
            classify: '种类1',
            goods: [{name: '商品1', imgUrl: 'ddddd'}, {name: '商品2', imgUrl: 'ddddd'}, {
              name: '商品3',
              imgUrl: 'ddddd'
            }, {name: '商品4', imgUrl: 'ddddd'}, {name: '商品5', imgUrl: 'ddddd'}]
          },
          {
            classify: '种类2',
            goods: [{name: '商品1', imgUrl: 'ddddd'}, {name: '商品2', imgUrl: 'ddddd'}, {
              name: '商品3',
              imgUrl: 'ddddd'
            }, {name: '商品4', imgUrl: 'ddddd'}, {name: '商品5', imgUrl: 'ddddd'}]
          },
          {
            classify: '种类3',
            goods: [{name: '商品1', imgUrl: 'ddddd'}, {name: '商品2', imgUrl: 'ddddd'}, {
              name: '商品3',
              imgUrl: 'ddddd'
            }, {name: '商品4', imgUrl: 'ddddd'}, {name: '商品5', imgUrl: 'ddddd'}]
          },
          {
            classify: '种类4',
            goods: [{name: '商品1', imgUrl: 'ddddd'}, {name: '商品2', imgUrl: 'ddddd'}, {
              name: '商品3',
              imgUrl: 'ddddd'
            }, {name: '商品4', imgUrl: 'ddddd'}, {name: '商品5', imgUrl: 'ddddd'}]
          }
        ],
        active: 0,
        allLoaded: false,
        pageSize: 10,
        pageNum: 0,
        loading: true // 正在加载
      };
    },
    created() {
    },
    activated() {
      // 默认查询女性用品
//      this.getGoods(0);
      this.classifyList = this.classifyList1;
    },
    methods: {
      /**
       * 跳转搜索页面
       */
      goSearch() {
        let params = {
          name: 'goodsSearch',
          query: {}
        };
        this.$router.push(params);
      },
      /**
       * 选择 商城和到家
       */
      sellectType(indexs) {
        this.classifyTypes.forEach(function (collectItem, index) {
          if (indexs === index) {
            collectItem.isSellect = true;
          } else {
            collectItem.isSellect = false;
          }
        });
        this.classifyList = indexs === 0 ? this.classifyList1 : this.classifyList2;
      },
      /**
       * 商品查询
       * @param type
       */
      getGoods(type) {
        let query = new window.AV.Query('Goods');
        query.equalTo('goodsClassify', type);
        query.limit(this.pageSize);// 最多返回 10 条结果
        query.skip(this.pageSize * this.pageNum);// 跳过 20 条结果

        query.find().then((res) => {
          let goodsList = JSON.parse(JSON.stringify(res));
          goodsList.forEach((item) => {
            this.goodsList.push(item);
          });
          this.pageNum++;
          this.loading = false;
          this.$refs.loadmore.onTopLoaded();
          this.$refs.loadmore.onBottomLoaded();
        }, (e) => {
          this.loading = false;
          this.$refs.loadmore.onTopLoaded();
          this.$refs.loadmore.onBottomLoaded();
          console.log(JSON.stringify(e));
        });
      },
      /**
       * 跳转到商品详情
       * @param goodsId
       */
      goGoodsDetail(goodsId) {
        this.$router.push({name: 'goodsDetail', query: {goodsId: goodsId}});
      },
      /**
       * 分类选项卡点击
       */
      select(type) {
        this.active = type;
        this.pageNum = 0;
//        this.goodsList = [];
//        this.getGoods(type);
      },
      /**
       * 下拉刷新
       */
      loadTop() {
        this.pageNum = 0;
//        this.goodsList = [];
//        if (!this.loading) {
//          this.getGoods(this.active);
//        }
      },
      /**
       * 上拉加载
       */
      loadBottom() {
        if (!this.loading) {
//          this.getGoods(this.active);
        }
      }
    },
    filters: {
      /**
       * 字符串截取
       * @param str
       * @returns {*}
       */
      ellipsisStr(str) {
        if (str.length > 26) {
          return str.substring(0, 20) + '...';
        } else {
          return str;
        }
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .classify
    display: flex
    flex-direction: column
    width: 100%
    height: 100%
    .topHeader
      width: 100%
      height: .9rem
      border-bottom: .01rem solid #d0d0d0
      padding-bottom: .1rem
      background-color: #ffffff
      .search-wrapper
        display: flex
        display: -webkit-flex
        width: 100%
        height: 0.3rem
        top: 0
        left: .2rem
        padding: 0.1rem 0
        .search
          display: flex
          border: 1px solid #d0d0d0
          border-radius: 0.3rem
          box-sizing: border-box
          background: rgba(255, 255, 255, 0.9)
          margin-right: .15rem
          .icon-iconfontsousuo1
            display: block
            flex: 0 0 0.4rem
            width: 0.4rem
            height: 0.3rem
            font-size: 0.16rem
            color: #d0d0d0
            box-sizing: border-box
          input
            display: block
            flex: 1
            font-size: 0.12rem
            background: rgba(255, 255, 255, 0.9)
            border-radius: 0.3rem
            color: #4d4d4d
            &::-webkit-input-placeholder
              color: #d0d0d0
        .location
          flex: 0.1
          text-align: left
          padding-right: .2rem
          color: #d0d0d0
          .icon-weizhi
            display: block
            font-size: .3rem
      .classifyType
        display: flex
        display: -webkit-flex
        flex-wrap: nowrap
        box-sizing: border-box
        justify-content: center
        width: 100%
        background-color: #ffffff
        position : fixed
        top : .55rem
        padding: .0rem .1rem
        z-index : 1000
        & > :nth-child(1)
          border-top-left-radius: .04rem
          border-bottom-left-radius: .04rem
        & > :nth-child(2)
          border-top-right-radius: .04rem
          border-bottom-right-radius: .04rem
        & > div
          color: #b14ea8
          font-size: .15rem
          height: .30rem
          line-height: .3rem
          padding: .0rem .2rem
          width: .6rem
          text-align: center
          border: .01rem solid #b14ea8
        .checked
          background-color: #b14ea8
          color: #ffffff
    .classify-body
      display: flex
      flex : 1
      .classify-left
        flex: 0 0 0.81rem
        width: 1rem
        height: 100%
        background: #fff
        .classify-list
          .classify-item
            height: 0.5rem
            line-height: 0.5rem
            text-align: center
            font-size: 0.14rem
            border-1px(#d0d0d0)
            border-right: 0.01rem solid #e4e4e4
          .active
            background-color #f2f2f2
            box-sizing: border-box
            color: #b14ea8
            border-1px(#f2f2f2)
            border-left: 0.03rem solid #b14ea8
            border-right: 0.01rem solid #f2f2f2
      .classify-right
        flex: 1
        background: #f2f2f2
        padding: 0 0.1rem
        overflow: scroll
        &::-webkit-scrollbar
          display: none
        .pull-refresh
          width: 100%
          .goods-all-ul
            .goods-classify-li
              background-color: #f2f2f2
              .classify-title
                color: #313131
                text-align: left
                height: .32rem
                line-height: .32rem
                font-size: .13rem
              .goods-classify-list
                display: flex
                box-sizing: border-box
                flex-wrap: wrap
                background-color: #ffffff
                padding-left: .1rem
                padding-right: .1rem
                padding-top: .08rem
                .goods-classify-item
                  flex: 0 1 auto
                  padding: .09rem
                  padding-bottom: .02rem
                  img
                    display: block
                    width: 0.66rem
                    height: 0.66rem
                    background-repeat: no-repeat
                  .good-name
                    height: 0.30rem
                    line-height: 0.30rem
                    width: .66rem
                    font-size: 0.12rem
                    color: #313131
                    text-align: center
                    overflow: hidden

</style>



