<template>
  <div class="commentlist">
    <div class="header">
      <v-header class="det-tit">评价</v-header>
      <!--<div class="commentType">-->
        <!--<div v-for="(item, index) in collectTypes"-->
             <!--:key="index"-->
             <!--@click="sellectType(index)"-->
             <!--:class="{checked:item.isSellect}">-->
          <!--{{item.name}}-->
        <!--</div>-->
      <!--</div>-->
    </div>
    <div class="commentlist-body">
      <mt-loadmore class="pull-refresh"
                   :top-method="loadTop"
                   :bottom-method="loadBottom"
                   :bottom-all-loaded="allLoaded"
                   topPullText="↓"
                   topDropText="↑"
                   bottomPullText="上拉加载"
                   bottomDropText="释放加载"
                   ref="loadmore">
        <ul class="commentlist-ul">
          <li class="commentlist-li" v-for="(item,index) in commentList">
            <div class="commentlist-name">
              <img :src="item.reviewer_avatar_url"/>
              <span>{{item.reviewer_name}}</span>
            </div>
            <div class="commentlist-text">
              <p class="mess">
                <span>{{item.date_created}}</span>
                <span>型号：sxxsl</span>
                <span>颜色：红</span>
              </p>
              <p class="comment">{{item.title}}</p>
              <div class="commentImg" v-if="item.images.length > 0">
                <img class="img" v-for="(itemImg,index) in item.images" :src="itemImg"/>
              </div>
              <p class="commFooter">
                <span class="span1">浏览4次</span>
                <span class="span2 iconfont icon-pinglun">&nbsp6</span>
                <span class="span3 iconfont icon-dianzan">&nbsp7</span>
              </p>
            </div>
          </li>
        </ul>
      </mt-loadmore>
    </div>
  </div>
</template>
<script>
  import app from 'common/js/app';
  import header from 'components/common/header/header';
  export default {
    name: 'page-tab-container',
    data() {
      return {
        goodsId: '',
        collectTypes: [
          {name: '全部', isSellect: true}, {name: '追加', isSellect: false}, {name: '好评', isSellect: true}, {
            name: '中评',
            isSellect: false
          }, {name: '差评', isSellect: false}
        ],
        commentList: [],
        pageNum: 1,
        loading: true, // 是否正在加载数据 http://localhost:8080/#/commentList?goodsId=543
        allLoaded: false
      };
    },
    activated() {
      this.goodsId = this.$route.query.goodsId;
      this.getReviews();// 商品评价
    },
    created() {
    },
    components: {
      'v-header': header
    },
    methods: {
      /**
       * 获取评价信息
       */
      getReviews() {
        this.requestUrl(this.pageNum);
      },
      loadTop() {
        console.log('下拉');
        if (!this.loading) {
          this.pageNum = 1;
          this.commentList = [];
          this.requestUrl(this.pageNum);
        }
        this.loading = true;
      },
      loadBottom() {
        console.log('上拉');
        if (!this.loading) {
          console.log('上拉*****');
          this.pageNum++;
          this.requestUrl(this.pageNum);
        }
        this.loading = true;
      },
      requestUrl(pageNum) {
        let params = {
          pk: this.goodsId,
          page: pageNum
        };
        this.$http.get(`${app.url.products}${this.goodsId}/reviews/`, params).then((res) => {
          this.commentList = res.results;
          this.loading = false;
          this.$refs.loadmore.onTopLoaded();
          this.$refs.loadmore.onBottomLoaded();
        });
      },
      sellectType(index) {
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .commentlist
    display: flex
    flex-direction: column
    width: 100%
    height: 100%
    position: relative
    background-color: #edebeb
    z-index: 900
    .header
      .det-tit
        background-color: #ffffff
      .commentType
        display: flex
        flex-wrap: wrap
        width: 100%
        border-bottom: .01rem solid #d0d0d0
        border-top: .01rem solid #edebeb
        background-color: #ffffff
        padding-top  : .55rem
        & > div
          color: #313131
          background-color: #FEDEE0
          font-size: .15rem
          height: .3rem
          line-height: .3rem
          margin : .0rem .1rem .15rem .12rem
          border-radius: .2rem
          padding: .0rem .2rem
          text-align: center
        .checked
          background-color: #FB2833
          color: #ffffff
    .commentlist-body
      overflow: scroll
      &::-webkit-scrollbar
        display: none
      .pull-refresh
        width : 100%
        background-color : #edebeb
        .commentlist-ul
          width: 100%
          background-color: #ffffff
          .commentlist-li
            position: relative
            padding: .1rem
            border-bottom: .1rem solid #edebeb
            .commentlist-name
              height: .4rem
              img
                float: left
                width: .4rem
                height: .4rem
                background-color: green
                border-radius: .2rem
              span
                float: left
                height: .4rem
                line-height: .4rem
                width: .8rem
                color #313131
                font-size: .15rem
                text-align: left
                padding-left: .2rem
            .commentlist-text
              .mess
                height: .3rem
                line-height: .3rem
                text-align: left
                color: #BDBDBD
                font-size: .12rem
                & > span
                  padding-right: .2rem
              .comment
                color: #313131
                text-align: left
                font-size: .14rem
                line-height: .22rem
                padding : .1rem .0rem
              .commentImg
                display : flex
                flex-wrap: nowrap
                box-sizing: border-box
                .img
                  width : 1.1rem
                  height : 1.1rem
                  padding : .05rem
              .commFooter
                padding-top : .1rem
                color: #BDBDBD
                font-size: .12rem
                text-align : left
                display : flex
                flex-wrap: nowrap
                .span1
                  flex : 2 2 auto
                .span2
                  flex : 1 1 auto
                  text-align : right
                  padding-right : .2rem
</style>



