<template>
  <div class="shopcart">
    <v-header :showBack="false">购物车</v-header>
    <v-shopcart-item v-if="shopcartList.length>0"
                     v-for="item in shopcartList"
                     :item="item"
                     key="item.id"></v-shopcart-item>
    <footer>
      <div class="footer-left"
           @click="selectAll">
        <span :class="$store.getters.getSelectStoreState?'iconfont icon-duihao1':'off'"></span>
        <span class="select-all">全选</span>
      </div>
      <div class="footer-center">
        <div class="total-text">合计<span class="total-price">¥{{$store.getters.getShopcartTotalPrice}}</span></div>
        <div class="express-fee">(含运费¥0)</div>
      </div>
      <div class="footer-right"
           @click="settlement">结算({{$store.getters.getShopcatGoodsCount}})



      </div>
    </footer>
  </div>
</template>
<script>
  import {mapState} from 'vuex';
  import {Toast} from 'mint-ui';
  import app from 'common/js/app';
  import header from 'components/common/header/header';
  import shopcartItem from 'components/common/shopcartItem/shopcartItem';
  export default {
    data() {
      return {};
    },
    components: {
      'v-shopcart-item': shopcartItem,
      'v-header': header
    },
    activated() {
      this.$root.eventHub.$emit('navTab', 2);
      // 获取购物车列表
      this.getShopcartList();
    },
    computed: {
      ...mapState([
        'shopcartList'
      ])
    },
    methods: {
      /**
       * 获取购物车列表
       */
      getShopcartList() {
        let shopcartList = [];
        this.$http.get(app.url.shopcart).then((res) => {
          res.forEach((item) => {
            item.partner_info.select = false;
            item.product_list.forEach((product) => {
              product.select = false;
            });
            shopcartList.push(item);
          });
        });
        this.$store.commit('updateShopcartList', shopcartList);
      },
      /**
       * 结算
       * @constructor
       */
      settlement() {
        if (this.$store.getters.getShopcatGoodsCount === 0) {
          Toast({
            message: '请先选择商品',
            position: 'bottom',
            duration: '500'
          });
          return;
        }
        this.$router.push({name: 'goodsOrderSubmit', query: {goodsList: this.$store.getters.getSelectGoods}});
        console.log('结算');
      },
      /**
       * 全选||反选
       */
      selectAll() {
        this.$store.commit('setAllGoodsSelect', !this.$store.getters.getSelectStoreState);
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  .shopcart
    footer
      position: fixed
      left: 0
      bottom: 0.5rem
      display: flex
      height: 0.4rem
      width: 100%
      line-height: 0.4rem
      background-color: #fff
      .footer-left
        .icon-duihao1
          display: block
          float: left
          height: 0.4rem
          width: 0.4rem
          color: #ff4242
          border-radius: 50%
          border: 1px solid transparent
          box-sizing: border-box
        .select-all
          display: block
          float: right
          height: 0.4rem
          line-height: 0.4rem
          color: #000
          font-size: 0.15rem
          box-sizing: border-box
        .off
          display: block
          float: left
          height: 0.17rem
          width: 0.17rem
          align-self: center
          color: transparent
          border: 1px solid #f2f2f2
          box-sizing: border-box
          margin: 0.115rem
          border-radius: 50%
      .footer-center
        flex: 1
        color: #9a9a9a
        font-size: 0.1rem
        text-align: right
        padding-right: 0.1rem
        box-sizing: border-box
        .total-text
          height: 0.2rem
          line-height: 0.2rem
        .express-fee
          height: 0.2rem
          line-height: 0.2rem
      .footer-right
        color: #fff
        background: #ff4242
        width: 0.9rem

</style>



