<template>
  <div class="addManage">
    <v-header class="det-tit">地址管理</v-header>
    <ul class="addressList">
      <li class="address" v-for="(item, index) in addData" @click="selectAddress(item.id)">
        <p class="userInf aboutUser">{{ item.receiver }}<span class="userTel">{{ item.phone_number }}</span></p>
        <p class="userAddress aboutUser">{{ item.district }}</p>
        <span class="iconfont icon-bianji" @click="editAddress(item.id)"></span>
      </li>
    </ul>
    <div class="addAdd" @click="goAddAddress">新增地址</div>
  </div>
</template>
<script>
  import app from 'common/js/app';
  import header from 'components/common/header/header';
  export default {
    data() {
      return {
        addData: []
      };
    },
    components: {
      'v-header': header
    },
    created() {
      this.init();
    },
    methods: {
      init() {
        this.getAddData();
      },
      /**
       * 请求用户地址列表
       */
      getAddData() {
        this.$http.get(app.url.address).then((res) => {
          console.log(res);
          this.addData = res;
          console.log(this.addData);
        });
      },
      goAddAddress() {
        this.$router.push({name: 'addAddress'});
      },
      /**
       * 修改地址
       */
      editAddress(addId) {
        this.$router.push({name: 'addAddress', query: {editAdd: addId}});
      },
      /**
       * 选择地址
       * @param addressId
       */
      selectAddress(addressId) {
        this.$store.commit('setCurrentAddressId', addressId);
        this.$router.go(-1);
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .addManage
    height: 100%
    position: relative
    font-size: .14rem
    .det-tit
      background: #FAFAFA
    .addressList
      text-align: left
      border-bottom: .05rem solid #F6F6F6
      .address
        padding: 0 .1rem
        position: relative
        .aboutUser
          width: 80%
          line-height: .35rem
        .userInf
          border-bottom: 1px solid #f2f2f2
          .userTel
            margin-left: .35rem
        .icon-bianji
          position: absolute
          right: 0.25rem
          top: 0.25rem
          color: #ccc
          font-size: .25rem
    .addAdd
      width: 60%
      line-height: .4rem
      position: absolute
      bottom: 10%
      border: 1px solid #A6DCFF
      border-radius: .05rem
      left: 20%
      color: #A6DCFF
</style>



